import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';
import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";
import {paths} from "../../environments/environment";

@Injectable()
export class UserManagementService {

  public defaultHeaders: Headers = new Headers();
  public deleteUser = new EventEmitter();
  public getAllUsersList = new EventEmitter();
  public changeUserType = new EventEmitter();
  public getUserIfExist = new EventEmitter();
  public addUserToGroup = new EventEmitter();
  public createOrg = new EventEmitter();
  public switchOrganisations=new EventEmitter();
  public token:any;

  ngOnDestroy() {

  }


  constructor(protected http: Http, private _store: Store<any>, private localStorageService: LocalStorageService) {



    //@todo handle error cases
    this.getAllUsersList.subscribe((token)=> {
      this.token=token;
      let path=paths.MANAGE_USERS_PATH;
      console.log(path);
      this.getAllUsers(path,token).subscribe(response =>{
        console.log(response);
        this._store.dispatch({type: "GET_ALL_USERS_LIST" ,payload:response});
      },err=>{
        console.log(err);
      });
    });

    this.createOrg.subscribe((addOrgObj)=> {
      this.token=this.localStorageService.get("AUTH_TOKEN");
      let path =paths.CREATE_ORGANISATION_PATH ;
      console.log(path);
      this.createOrganisation(path,addOrgObj).subscribe(response =>{
        console.log(response);
        if(response.status >=200 && response.status <300){
          this._store.dispatch({type: "CREATE_ORGANISATION_RESPONSE", payload:{error:'',res:response}});
        }else{
          this._store.dispatch({type: "CREATE_ORGANISATION_RESPONSE", payload:{error:'X',errorMsg:response.error}});
        }
      },err=>{
        console.log(err);
        console.log(err.json());
        this._store.dispatch({type: "CREATE_ORGANISATION_RESPONSE", payload:{error:'X',errorMsg:'http error'}});
      });
    });

    this.switchOrganisations.subscribe((orgId)=> {
      this.token=this.localStorageService.get("AUTH_TOKEN");
      let path =paths.SWITCH_ORG_PATH+orgId;
      console.log(path);
      this.switchOrganisation(path,orgId).subscribe(response =>{
        console.log(response);
        if(response.status >=200 && response.status <300){
          this._store.dispatch({type: "SWITCH_ORG_RESPONSE", payload:{error:'',res:response}});
        }else{
          this._store.dispatch({type: "SWITCH_ORG_RESPONSE", payload:{error:'X',errorMsg:response.error}});
        }
      },err=>{
        console.log(err);
        console.log(err.json());
        this._store.dispatch({type: "SWITCH_ORG_RESPONSE", payload:{error:'X',errorMsg:'http error'}});
      });
    });

    //@todo handle error cases

    this.deleteUser.subscribe((userObj) => {
      let path = paths.DELETE_USERS_PATH + "/remove";
      console.log(path);
      console.log(userObj);
      this.deleteSelectedUser(userObj, path).subscribe(response => {
        console.log(response);
        this._store.dispatch({type: "DELETE_USER_SERVICE_RESPONSE", payload: response});
      }, err => {
        console.log(err);
      });
    });

    this.changeUserType.subscribe((userObj) => {
      let path = paths.DELETE_USERS_PATH + "/add";
      console.log(path);
      console.log(userObj);
      this.updateSelectedUser(userObj, path).subscribe(response => {
        console.log(response);
        this._store.dispatch({type: "UPDATE_USER_STATUS_RESPONSE", payload: response});
      }, err => {
        console.log(err);
      });
    });


    this.addUserToGroup.subscribe((userObj) => {
      let path = paths.DELETE_USERS_PATH + "/add";
      console.log(path);
      console.log(userObj);
      this.updateSelectedUser(userObj, path).subscribe(response => {
        console.log(response);
        this._store.dispatch({type: "UPDATE_USER_STATUS_RESPONSE", payload: response});
      }, err => {
        console.log(err);
      });
    });



    this.getUserIfExist.subscribe((newUserObj) => {
      let path = "";
      if(newUserObj['email']!="" )
      {
        path = paths.ADD_EXISTING_USERS_PATH + "?email="+newUserObj['email'];
      }
      if(newUserObj['mobile']!="")
      {
        path = paths.ADD_EXISTING_USERS_PATH + "?mobile="+newUserObj['mobile'];
      }

      console.log(path);
      console.log(newUserObj);
      this.checkExistingUser(path).subscribe(response => {
        console.log(response);
        this._store.dispatch({type: "EXISTING_USER_RESPONSE", payload: response});
      }, err => {
        console.log(err);
      });
    });


  }

  public deleteSelectedUser(userObj,path){
    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken)

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: userObj,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if(response.status ==200) {
        return response.json();
      }

    });
  }

  public createOrganisation(path,addOrgObj){
    let headers = new Headers();
    console.log(this.token);
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: addOrgObj,
    });

    return this.http.request(path, requestOptions).map((response: Response) => {
      console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }
    });
  }

  public switchOrganisation(path,switchOrgId){
    let headers = new Headers();
    console.log(this.token);
    console.log(this.token);
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: switchOrgId,
    });

    return this.http.request(path, requestOptions).map((response: Response) => {
      console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }
    });
  }

  public updateSelectedUser(userObj,path){
    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: userObj,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if(response.status ==200) {
        return response.json();
      }

    });
  }

  public getAllUsers(path,token){
    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if(response.status ==200) {
        return response.json();
      }

    });
  }

  public checkExistingUser(path){
    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken)

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if(response.status ==200) {
        return response.json();
      }
    });
  }


}





