import {Component} from '@angular/core';
import { Router} from "@angular/router";
import {Store} from "@ngrx/store";
// import {AuthenticationServices} from "../../services/authentication.service";
import {LocalStorageService} from "angular-2-local-storage";
// import {go, back} from "../../app-states/actions";
import {replace, back, go, RouterMethodCall} from "@ngrx/router-store";
import {LoginServices} from "../../services/login-services";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import {TranslateService} from "../../locale/translate.service";
import {ObservableMedia} from "@angular/flex-layout";

@Component({
  selector: 'app-send-otp',
  templateUrl: './send-otp.component.html',
  styleUrls: ['./send-otp.component.css']
})

export class SendOTP {
  public isSpinner=false;
  public OTP:any='';
  public mobNo:any=null;
  public countryCode:any='';
  public unsub_otpGenration:any=null;
  public unsub_token:any=null;

  ngOnDestroy(){

    if (typeof this.unsub_otpGenration != 'undefined' && this.unsub_otpGenration != null && this.unsub_otpGenration != undefined)
      this.unsub_otpGenration.unsubscribe();

    if (typeof this.unsub_token != 'undefined' && this.unsub_token != null && this.unsub_token != undefined)
      this.unsub_token.unsubscribe();
  }

  ngOnInit(){
    //*************** for localization *************************
     let langua= ""+this.localStorageService.get("LANGUAGE");
    this._translate.use(langua);
  //  ***************************************************
  }

  constructor(public media: ObservableMedia,private _translate: TranslateService,public snackBar: MdSnackBar,private _store:Store<any>, private localStorageService: LocalStorageService,private _loginServices:LoginServices,private _router:Router) {
    // if token alrady exists then go to dash board view
    let token = this.localStorageService.get("TOKEN");
    console.log(token);
    // *********  for checking mobile or desktop view **************
    if(token!=null && token !=undefined){
      console.log("back navigation");
      this.navigate(); // navigate to dashboard view
    }
    //***************************************
    this.mobNo=this.localStorageService.get("USER-MOB-NO");
    this.countryCode=this.localStorageService.get("COUNTRY-CODE");

    if(this.mobNo==null && (this.localStorageService.get("TOKEN")==null)){
      console.log("please select Mobile No");
      this.openSnackBar("please select Mobile No");
      this._store.dispatch(go(['/userLogin']));
    }

    this.unsub_otpGenration=this._store.select('otpGenaration').subscribe(value => {
      console.log(value);

      if(value==null){
        return;
      }

      if(value['error']=='X'){
        console.log(value['msg']);
        this.openSnackBar(value['msg']);
        this.localStorageService.remove("USER-MOB-NO");
        this.localStorageService.remove("COUNTRY-CODE");
        this._store.dispatch(go(['/userLogin']));
        return;
      }

      if(value['error']==''){
        this.openSnackBar("OTP sent to your Mobile Number");
        this.mobNo=value['mobNo'];
      }
    });
    // console.log(window.location.pathname);
  }

  login(){

    if(this.mobNo==null && (this.localStorageService.get("TOKEN")==null)){
      console.log("please select Mobile No");
      this.openSnackBar("please select Mobile No");
      this._store.dispatch(go(['/userLogin']));
    }


    if(this.OTP==''  || this.OTP==undefined || this.OTP == null){
      // console.log('Required parameter email was null or undefined.');
      return;
    }else{
      // this.isSpinner=true;
      let user=this.OTP;
      console.log(this.OTP);
      this._store.dispatch({type: "TOKEN",payload:null});
      this._loginServices.login.emit({mobileNo:this.mobNo,otp:this.OTP});

      // this._store.dispatch(replace(['/vtsdashboard'], { query: 'dashboard' }));   pass data with query params
      // this._store.dispatch(go(['/vtsdashboard']));
      // verify otp valid or not
      this.unsub_token=this._store.select('token').subscribe(value => {
        console.log(value);

        if(value==null){
          return;
        }

        if(value['error']=='X'){
          console.log(value['msg']);// set error msg to user
          this.openSnackBar(value['msg']);
          return;
        }

        if(value['error']==''){
          let token=value['token'];
          console.log(token);
          // this.localStorageService.set("USER-MOB-NO",this.mobNo); // set mobile no in local storage
          // this._store.dispatch(go(['/vtsdashboard']));
          this.navigate();
        }
      });
    }
  }// login methods close

 resendOTP(){
   console.log("resend");
   this._store.dispatch({type: "OTP_GENERATION",payload:null});
   this._loginServices.sendOTP.emit({mobileNo:this.mobNo,countryCode:this.countryCode});

    // this._store.dispatch(back());
  }

  navigate(){
    // if(this.media.isActive('gt-xs')){ // gt-xs means width  screen width > 600px
    //   console.log('desktop view');
    //   this._store.dispatch(go(['/dashboardDesktop']));
    //   return;
    // }else{
    //   this._store.dispatch(go(['/vtsdashboard']));
    // }

    this._store.dispatch(go(['/vtsdashboard']));
  }

  openSnackBar(message: string) {
    this.snackBar.open(message , '', <MdSnackBarConfig>{duration :  3000});
  }

}




