import {ActionReducer, Action, combineReducers} from '@ngrx/store';
import {ActionRecorderServices } from '../services/actionRecorder.service';

var obj=new ActionRecorderServices();

export function userInfo(state: any = null, action:Action) {
  switch (action.type) {
    case "USER_INFO" : {
      obj.addAction(action);
      return action.payload;
    }
    default:
      return state;
  }
}




