import {Component, ElementRef,ViewChild} from '@angular/core';
import { Router} from "@angular/router";
import {Store} from "@ngrx/store";
import {IMyOptions} from "mydatepicker";
import {FormControl} from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/operator/map';
import {Services} from "../../services/services";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import {VehicleReportServices} from "../../services/vehicle-report-services";


@Component({
  selector: 'app-overspeed-report',
  templateUrl: './overspeed-report.component.html',
  styleUrls: ['./overspeed-report.component.css']
})
export class OverspeedReport  {

  public isSpinner=false;
  public isAddVehicleDialog:boolean=false;
  public isDisabled=true;
  public search='';
  public selectedVehicles:any=[];
  public errorMsg='';
  public allVehOverspeedDuration='';
  public allVehicleTravelDistance=0.00;
  public vehicleList:any=[];
  public vehicleDetails:any=[];
  public unSub_vehicleList=null;
  public unSub_customerDetails:any=null;
  public unSub_vehOverspeedReport:any=null;
  public showOSFilterDialogue:boolean=false;
  public timeDurationOSUnit="Min";
  public timeDurationOS=5;
  public fileteredVehicleList=[];
  public filterOSApplied:boolean=false;
  // isScrollLeft:boolean=true;
  // isScrollRight:boolean=false;


  public fromDate=null;
  public toDate=null;

  ngAfterViewChecked(){
    for(let i=0;i<this.vehicleDetails.length;i++) {
      if(document.getElementById('timeline_'+i)!=null ) {
        let maxScrollLeft = document.getElementById('timeline_'+i).scrollWidth - document.getElementById('timeline_'+i).clientWidth;
        if (maxScrollLeft == document.getElementById('timeline_'+i).scrollLeft) {

          // console.log(document.getElementById('timeline_'+i).scrollWidth);
          // console.log(document.getElementById('timeline_'+i).clientWidth);
          // console.log(document.getElementById('timeline_'+i).scrollLeft);

          this.vehicleDetails[i].isScrollRightDisable = true;
          // console.log(this.vehicleDetails[i].isScrollRightDisable);
        } else {
          this.vehicleDetails[i].isScrollRightDisable = false;
          // console.log(this.vehicleDetails[i].isScrollRightDisable);
        }

        if (document.getElementById('timeline_'+i).scrollLeft == 0) {
          this.vehicleDetails[i].isScrollLeftDisable = true;
          // console.log(this.vehicleDetails[i].isScrollLeftDisable);
        } else {
          this.vehicleDetails[i].isScrollLeftDisable = false;
          // console.log(this.vehicleDetails[i].isScrollLeftDisable);
        }
      }
    }
  }// close ngAfterViewChecked

  ngOnDestroy() {
    if (typeof this.unSub_vehicleList != 'undefined' && this.unSub_vehicleList != null && this.unSub_vehicleList != undefined)
      this.unSub_vehicleList.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();

    if (typeof this.unSub_vehOverspeedReport != 'undefined' && this.unSub_vehOverspeedReport != null && this.unSub_vehOverspeedReport != undefined)
      this.unSub_vehOverspeedReport.unsubscribe();
  }

  constructor(private _store:Store<any>,_services:Services,public snackBar: MdSnackBar,private vehReportServices:VehicleReportServices) {
    let customer={};
    this._store.dispatch({type: "TITLE",payload:'Overspeed Report'});// set the header name

    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {

      if(value==null)
        return;

      if(value['error']==''){
        customer= value['customer'];
        console.log(customer);
        // _services.getVehicleList.emit(customer.id);
      }else{
        return;
      }

      // ***************  vehicle list ************************************
      this.unSub_vehicleList = _store.select('vehicleListRes').subscribe(value => {
        console.log(value);
        this.errorMsg='';
        // check not  exist then request for list
        if(value==null){
          _services.getVehicleList.emit(customer['id']);
          return;
        }

        if(value['error']==''){
          this.vehicleList = value['data'];
          console.log(this.vehicleList);

          for(let i=0;i<this.vehicleList.length;i++){
            this.vehicleList[i]['select']=false;
          }

        }else {
          console.log(value['errorMsg']);
          this.errorMsg=value['errorMsg'];
        }
      });

    });
    // this.stateCtrl = new FormControl();
    // this.VehicleNoList = this.stateCtrl.valueChanges
    //   .startWith(null)
    //   .map(name => this.filterStates(name));
    // filterStates(val: string) {
    //   return val ? this.vehicleList.filter(s => new RegExp(`^${val}`, 'gi').test(s))
    //     : this.vehicleList;

  }// constructor close

  openAddVehicleDialog(){
    if(this.fromDate==null || this.toDate== null){
      this.openSnackBar("Please select Date first");
      return;
    }

    if(this.toDate - this.fromDate < 0){
      this.openSnackBar("Start date must be less than End date");
      return;
    }

    if(this.fromDate.getTime() > new Date().getTime() || this.toDate.getTime() > new Date().getTime() ){
      this.openSnackBar("Date should not be greater than current Date");
      return;
    }

    this.isAddVehicleDialog=true;
  }

    AddVehicle(){
      if(this.fromDate==null || this.toDate== null){
        this.openSnackBar("Please select Date first");
        return;
      }

      let durationTime: any;
      if(this.timeDurationOSUnit=="Hrs") {

        durationTime = this.timeDurationOS * 60 * 60 * 1000;
      }

      else if(this.timeDurationOSUnit=="Min") {
        durationTime = this.timeDurationOS * 60 * 1000;
      }


      this.selectedVehicles=[];
      let vehDetails=[]; //
      this.vehicleDetails=[];
      let vehicles=[]; //  sending data for query

      for(let i=0;i<this.vehicleList.length;i++)
      {
        if(this.vehicleList[i]['select']==true){
          this.selectedVehicles.push(JSON.parse(JSON.stringify(this.vehicleList[i])));
          vehDetails.push(JSON.parse(JSON.stringify(this.vehicleList[i])));
          vehDetails[vehDetails.length - 1]['details']=[];
          vehicles.push(this.vehicleList[i]['uuid']);
          this.fileteredVehicleList.push(this.vehicleList[i]['uuid'])


          // vehicles.push('1234'); // temporary data
        }
      }

      console.log(this.selectedVehicles);

      // this.selectedVehicles[0].vtsDeviceId='1234';// temporary  data
      // vehDetails[0].vtsDeviceId='1234';// temporary  data

      let obj= {
        "startTime": this.fromDate.getTime(),
        "endTime": this.toDate.getTime(),
        "vehicleList": vehicles,
        "minimumTime":durationTime
      }
      // get running report
      this.isSpinner=true;
      // this.totalDuration=this.msToTime(this.toDate - this.fromDate);

      // console.log(this.totalDuration);

      if (typeof this.unSub_vehOverspeedReport != 'undefined' && this.unSub_vehOverspeedReport != null && this.unSub_vehOverspeedReport != undefined)
        this.unSub_vehOverspeedReport.unsubscribe();

      this.vehReportServices.getOverspeedReport.emit(obj);
      this._store.dispatch({type: "VEH_OVERSPEED_REPORT",payload:null});

      // ***************  get Running Report  ************************************
      this.unSub_vehOverspeedReport = this._store.select('vehOverspeedReport').subscribe(value => {
        console.log(value);
        this.vehicleDetails=[];
        this.errorMsg='';
        if(value==null)
        {
          return;
        }

        this.isSpinner=false;
        if(value['error']==''){
          let details = value['data'];
          let vehOverspeedDuration=0;
          let distance=0;
          console.log(details);
          for(let i=0;i<details.length;i++){
            for(let j=0;j< details[i]['value'].length;j++) {
              details[i]['value'][j]['time'] = new Date(Number(details[i]['value'][j]['startTime']));
              details[i]['value'][j]['overspeedDuration'] = this.msToTime(Number(details[i]['value'][j]['duration']));
              vehOverspeedDuration=vehOverspeedDuration+Number(details[i]['value'][j]['duration']);
              details[i]['value'][j]['distance']=((((Number(details[i]['value'][j]['duration'])/1000)/60)/60)*Number(details[i]['value'][j]['averageSpeed']));
              // console.log(details[i]['value'][j]['distance']);
              distance=distance+details[i]['value'][j]['distance'];
            }
            // console.log(distance);
            details[i]['vehOverspeedDuration']=vehOverspeedDuration;
            details[i]['_vehOverspeedDuration']=this.msToTime(vehOverspeedDuration);
            details[i]['vehTravelDistance']=Number(distance);
            // console.log(details[i]['vehTravelDistance']);
          }

          let allDuration=0;
          let allDistance=0;
          for(let i=0;i<vehDetails.length;i++){
            for(let j=0;j< details.length;j++){
              if(vehDetails[i]['uuid']==details[j]['vehicleId']){
                if(this.filterOSApplied==true)
                {
                  vehDetails[i]['details']=[];
                }
                vehDetails[i]['details'].push(details[j]);
                allDuration=allDuration+details[j]['vehOverspeedDuration'];
                allDistance=allDistance+details[j]['vehTravelDistance'];
              }
            }
            this.allVehOverspeedDuration=this.msToTime(allDuration);
            this.allVehicleTravelDistance=allDistance;
          }
          // console.log(vehDetails);

          // ***************** remove additional value *****************************
          // for(let i=0;i<vehDetails.length;i++){
          //   if(vehDetails[i]['details'].length > 1){
          //     vehDetails[i]['details'].splice(0,vehDetails[i]['details'].length - 1);
          //   }
          // }

          this.vehicleDetails=vehDetails;
          console.log(this.vehicleDetails);
          console.log(this.vehicleDetails[0]['details'].length);
          console.log(this.vehicleDetails[0]['details']);
          console.log(this.selectedVehicles);

        }else {
          console.log(value['errorMsg']);
          this.vehicleDetails=vehDetails;
          // this.errorMsg=value['errorMsg'];
          this.openSnackBar(value['errorMsg']);
          // this._store.dispatch({type: "VEH_OVERSPEED_REPORT",payload:null}); // when error occur then clear store
        }
      });
      //
      // this.unSub_vehStoppageReport = this._store.select('vehStoppageReport').subscribe(value => {
    }

  unselectVehicle(index){
    this.selectedVehicles.splice(index,1);
    this.vehicleDetails.splice(index,1);

    let allDuration=0;
    let allDistance=0;
    if(this.vehicleDetails.length==0) {
      this.allVehOverspeedDuration='';
      this.allVehicleTravelDistance=0;
      return;
    }

    console.log(this.vehicleDetails);
    for(let i=0;i<this.vehicleDetails.length;i++){
      if(this.vehicleDetails[i].details.length>0) {
        allDuration = allDuration + this.vehicleDetails[i].details[0].vehOverspeedDuration;
        allDistance=allDistance+this.vehicleDetails[i].details[0].vehTravelDistance;
      }
    }

    if(allDuration==0){
      this.allVehOverspeedDuration='';
      this.allVehicleTravelDistance=0;
      return;
    }
    this.allVehOverspeedDuration=this.msToTime(allDuration);
    this.allVehicleTravelDistance=allDistance;
  }

  scrollLeft(ind){
    document.getElementById('timeline_'+ind).scrollLeft=document.getElementById('timeline_'+ind).scrollLeft-40;
  }

  scrollRight(ind){
    console.log(ind);
    document.getElementById('timeline_'+ind).scrollLeft=document.getElementById('timeline_'+ind).scrollLeft+40;
  }

  chackValid(){
    for(let i=0;i<this.vehicleList.length;i++)
    {
      if(this.vehicleList[i]['select']==true){
        this.isDisabled=false;
        break;
      }else{
        this.isDisabled=true;
      }
    }
  }


  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration='';

    if(days>0){
      totalDuration=days+"d ";
    }
    if(hours>0){
      totalDuration=totalDuration+hours+"hr ";
    }

    if(days >= 1){
      return totalDuration;
    }

    if(minutes>0){
      return totalDuration=totalDuration+minutes+"min";
    }
    if(seconds>0 && totalDuration == ''){
      totalDuration=totalDuration+seconds+" sec";
    }

    if(totalDuration==''){
      return '0 sec';
    }else {
      return totalDuration;
    }

  }//close methods msToTime



  openSnackBar(message: string) {
    this.snackBar.open(message, '', <MdSnackBarConfig>{duration :  4000});
  }

  filterOverspeedReport()
  {
    let durationTime: any;
    if(this.timeDurationOSUnit=="Hrs") {

      durationTime = this.timeDurationOS * 60 * 60 * 1000;
    }

    else if(this.timeDurationOSUnit=="Min") {
      durationTime = this.timeDurationOS * 60 * 1000;
    }


    let obj1= {
      "startTime": this.fromDate.getTime(),
      "endTime": this.toDate.getTime(),
      "vehicleList": this.fileteredVehicleList,
      "minimumTime":durationTime
    }

    this.vehReportServices.getStoppageReport.emit(obj1);
    this.filterOSApplied=true;
    this.showOSFilterDialogue=false;
  }

}




