import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';
import * as io from 'socket.io-client';
import {Injectable} from "@angular/core";
import {LocalStorageService} from "angular-2-local-storage";
import {Store} from "@ngrx/store";
// import {VEHICLE_LIST_SOCKET_PATH} from "./path-services";
import {Services} from "./services";
import {paths} from "../../environments/environment";
// import {Http} from "@angular/http";

@Injectable()
export class SocketService {
  private url = paths.VEHICLE_LIST_SOCKET_PATH;
  private socket ;
  private token:any ;

  public vehicleList :any=[];
  public customer:any=null;
  public unSub_customerDetails:any=null;

  ngOnDestroy(){
    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
    {
      this.unSub_customerDetails.unsubscribe();
    }
  }

  constructor(private _services:Services,private _store:Store<any>,private localStorageService:LocalStorageService){
    // this.getVehicleList();
    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      console.log(value);
      // this.customer=null;
      // **********   uncomment please **********************
      if(value==null){
        return;
      }

      if(value['error']==''){
        this.customer = value['customer'];
      }else{
        return;
      }

        // this.token = this.localStorageService.get('TOKEN');
        this.token = this.localStorageService.get('AUTH_TOKEN');
        // console.log(this.token);
    // this.getVehicleList(token);
  });


    _store.select('vehicleListRes').subscribe(value => {
      // console.log(value);
      if(value!=null)
      {
        this.vehicleList =value['data'];
      }
    });
}

  getVehicleList() {
    this.socket = io(this.url);
    //
    if( this.customer == null || this.token == null){
      // console.log("no details found please login again!");
      return;
    }

    // this.socket.on("connection" ,() => {
    //   console.log("Hurreey , Connected");
    // }) ;

    // let isCallHttp = true;
    // this.socket.on("init", (data) =>{
    //   console.log("connected");
    //   this.vehicleList = data;
    //   isCallHttp = false;
    //   this._store.dispatch({type: "VEHICLE_LIST",payload:{error:'',data:data}});
    // });

    // setTimeout(()=>{
    //   if(isCallHttp) {
    //     console.log('call from http');
    console.log(this.customer['orgId']);
    this._services.getVehicleList.emit(this.customer['orgId']);
    // }
    // },1000);
    this.socket.emit('joinVehicleDashboard', this.token);

    this.socket.on("update", (data) => {
    //
      if(this.vehicleList ==null){
        return;
      }

      // console.log(typeof data);
      // console.log(data);

      if(typeof data =='string'){
        data =JSON.parse(data);
      }

      for(let i=0;i<this.vehicleList.length;i++){
        if(this.vehicleList[i]._id == data.vehicleId.string ){
          this.vehicleList[i].speed = data.speed;
          this.vehicleList[i].latitude = data.latitude;
          this.vehicleList[i].longitude = data.longitude;
         if(data.state){
          this.vehicleList[i].state = data.state;
         }
        }
      //
      //   // let  diffLat =0.00000000;
      //   // let  diffLng =0.00000000;
      //   // let prevoiusLat = this.vehicleList[i]['latitute'];
      //   // let prevoiusLng = this.vehicleList[i]['longitude'];
      //   // diffLat = (this.vehicleList[i]['longitude'] - prevoiusLat/20);
      //   // diffLng = (this.vehicleList[i]['longitude'] - prevoiusLng/20);
      //   //
      //   // for(let i=0 ;i < 10;i++) {
      //   //   setTimeout(()=>{
      //   //   this.vehicleList[i]['longitude'] = this.vehicleList[i]['longitude'] + diffLat;
      //   //   this.vehicleList[i]['longitude'] = this.vehicleList[i]['longitude'] + diffLng;
      //   //   // if(i >= 19) {
      //   //   //   let prevoiusLat = this.vehicleList[i]['latitute'];
      //   //   //   let prevoiusLng = this.vehicleList[i]['longitude'];
      //   //   // }
      //   //   console.log(prevoiusLat);
      //   //   console.log(prevoiusLng);
      //   //  },500*i);
      //   // }
      }
      // // console.log(data);
      // this._store.dispatch({type: "VEHICLE_LIST",payload:{error:'',data:this.vehicleList}});
    });

    // let observable = new Observable(observer => {
  }


  disconnectSocket(){
    console.log("disconnected");
    if(this.socket != null || this.socket != undefined)
    this.socket.disconnect();
  }

}



