import {Component} from '@angular/core';
import {Router, ActivatedRoute} from "@angular/router";
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";
import {go, show, RouterState} from "@ngrx/router-store";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import {TranslateService} from "../../locale/translate.service";
import {UserSignupServices} from "../../services/user-signup-services";
// import formatErrorMsg = jasmine.formatErrorMsg;


@Component({
  selector: 'app-user-signup',
  templateUrl: './user-signup.component.html',
  styleUrls: ['./user-signup.component.css'],
})

export class UserSignup {
  public isSpinner=false;
  public mobileNo:any='';
  public userName:any='';
  public email:any='';
  public signupOTP:any='';
  public validOTP:boolean=true;
  // public countryCode:any='+91';
  public userToken=null;
  public unSub_signupTokenValidation=null;
  public unSub_signupSuccessNew=null;
  // public countryCodeList:any=[{countryID:'IND',countryCode:'+91'}];

  public selectedLanguage='en';
  public supportedLangs: any=[ { name: 'English', code: 'en' },{ name: 'हिंदी', code: 'hi' }
  ];


  ngOnDestroy() {
    if (typeof this.unSub_signupTokenValidation != 'undefined' && this.unSub_signupTokenValidation != null && this.unSub_signupTokenValidation != undefined)
    {
      this.unSub_signupTokenValidation.unsubscribe();
    }
  }

  ngOnInit() {
    this.selectedLanguage= (this.localStorageService.get("LANGUAGE"))?(""+this.localStorageService.get("LANGUAGE")):'en';
    this.selectLang();
  }

  selectLang() {
    this._translate.use(this.selectedLanguage);
  }

  constructor(private userSignupServices:UserSignupServices, private route: ActivatedRoute,private _translate: TranslateService,public snackBar: MdSnackBar,private _store:Store<any>,private localStorageService: LocalStorageService) {

    route.queryParams.subscribe(params => {
       this.userToken = params['token'];
      console.log( this.userToken );
    });

    if(this.userToken == undefined || this.userToken == null || this.userToken =='') {
      console.log(this.userToken)
      this.openSnackBar("Invalid Request");
    }else{
      this.userSignupServices.signupTokenValidation.emit(this.userToken);
      // this.email=this._store.select('signupTokenValidation')
    }


    //subscribe here
    // ***************  vehicle list ************************************
    this.unSub_signupTokenValidation = _store.select('signupTokenValidation').subscribe(value => {
      // console.log(value);
      if(value==null || value['data']==undefined)
      {
        return;
      }

      console.log(value);
      this.email=value['data']['email'];
      this.isSpinner=false;
    });

    let token = this.localStorageService.get("TOKEN");
    console.log(token);
  }


  fieldsValidation(){
    if(this.email==''  ||this.email==undefined || this.email == null){
      console.log("Email should not be blank");
      return false;
    }

    if(this.mobileNo==''  ||this.mobileNo==undefined || this.mobileNo==null){
      console.log("please fill mobile no");
      return false;
    }

    if(this.userName==''  ||this.userName==undefined || this.userName==null){
      console.log("please fill User Name");
      return false;
    }

    if(this.signupOTP==''  ||this.signupOTP==undefined ||this.signupOTP == null){
      console.log("please fill OTP");
      return false;
    }
    return true;
  }


  validateOtp(mobNo){
    console.log(this.mobileNo);
    console.log(mobNo.length);
    this.userSignupServices.sendOTPSignUp.emit(this.mobileNo);
    this.unSub_signupTokenValidation = this._store.select('signUpOtpGenaration').subscribe(value => {
      if(value==null)
      {
        return;
      }
      console.log(value);

      if(value['msg']==="OTP generated")
      {
        this.validOTP=false;
      }
    });
  }


  signup(){
    if(this.fieldsValidation()){

      var obj={
        "data":{
        "contactPersonName":this.userName,
          "emailAddress":this.email,
          "mobileNumber":this.mobileNo,
          "organizationName":null,
          "organizationBillingAddress":null,
          "organizationShippingAddress":null,
          "typeOfCustomer":null
      },
        "token":this.userToken,
        "otp":this.signupOTP
      }


      this.userSignupServices.signUpUser.emit(obj);


      this.unSub_signupSuccessNew = this._store.select('signUpNewUserData').subscribe(value => {
        if(value==null)
        {
          return;
        }
        console.log(value);

if(value['data']['error']=="" || value['data']['message']=="Sign up successfull")
        {
           this._store.dispatch(go(['/userLogin']));
        }
       else {
          // if (value['data']['status'] != 200) {
            console.log(value['data']['errorMsg']);
            this.openSnackBar(value['data']['errorMsg']);
          // }
        }


      });

    }else{
      // this.openSnackBar(errorMsg);
      console.log("please insert all required fields");
    }
  }

  openSnackBar(message: string) {
    this.snackBar.open(message , '', <MdSnackBarConfig>{duration :  60*60*1000});
  }

}




