import {Injectable, Inject} from '@angular/core';
import { TRANSLATIONS } from './translations';
import {LocalStorageService} from "angular-2-local-storage"; // import our opaque token

@Injectable()
export class TranslateService {
	private _currentLang: string=null;

  // inject our translations
	constructor(@Inject(TRANSLATIONS) private _translations: any,private localStorageService: LocalStorageService) {
	}

  public get currentLang() {
    return this._currentLang;
  }

  public use(lang: string): void {
		// set current language
		this._currentLang = lang;
    this.localStorageService.set("LANGUAGE",this._currentLang);
	}

	private translate(key: string): string {
		// private perform translation
		let translation = key;
    let currentLang=this._currentLang;
    if (this._translations[this._currentLang] && this._translations[this._currentLang][key]) {
			return this._translations[this._currentLang][key];
		}
		return translation;
	}

	public instant(key: string) {
		// public perform translation
		return this.translate(key);
	}
}



