import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {ENTER, MdSnackBarConfig, MdSnackBar} from "@angular/material";
import {ScheduleReportServices} from "../../../services/schedule-report-services";
import {go} from "@ngrx/router-store";
import { Angulartics2Mixpanel } from 'angulartics2/dist';
// import {MdChipInputEvent} from '@angular/material';

@Component({
  selector: 'app-create-schedule',
  templateUrl: './create-schedule.component.html',
  styleUrls: ['./create-schedule.component.css']
})
export class CreateSchedule implements OnInit {

  public steperNo:any=1;
  public validationMsg ='';
  public isReportTypeDialog:boolean=false;
  public allFrequency:any=['Daily','Weekly','Monthly','Periodic'];
  public reportType:any={running:true,stoppage:false,timeline:false};
  public reportTypes:any=['Running'];
  public rType ="Running";
  public selectedFrequency ="Daily";
  public scheduleDate =null;
  public scheduleTime =null;
  public dateNos = [];
  public periodicNoList = [];
  public vehicleList:any= [];
  public vehicles:any= [];
  public emailList = [];
  public dateNo = "1";
  public periodicNo = "1";
  public actionType = "email";
  public email :any= '';
  public repeat ="";
  public day ={sunday:false,monday:true,tuesday:false,wednesday:false,thursday:false,friday:false,saturday:false};
  chipValue: string;

  private unSub_PickedVehicles: any =null;
  private unSub_scheduleCreatedAck: any =null;



  ngOnDestroy() {
    console.log("destroy");
    this._store.dispatch({type: "IS_BACK",payload:{isBack:false,backToComponent:''}});
    if (typeof this.unSub_PickedVehicles != 'undefined' && this.unSub_PickedVehicles != null && this.unSub_PickedVehicles != undefined)
      this.unSub_PickedVehicles.unsubscribe();

    if (typeof this.unSub_scheduleCreatedAck != 'undefined' && this.unSub_scheduleCreatedAck != null && this.unSub_scheduleCreatedAck != undefined)
      this.unSub_scheduleCreatedAck.unsubscribe();
  }

  constructor(private _store:Store<any>,private scheduleReportService:ScheduleReportServices,public snackBar: MdSnackBar,public _angularitics:Angulartics2Mixpanel) {
    _store.dispatch({type: "TITLE",payload:'Create Schedule'}); // set the header name
    _store.dispatch({type: "IS_BACK",payload:{isBack:true,backToComponent:"scheduleList"}});// set the component url which you want to back
    for(let i=1;i<=31;i++){
      this.dateNos.push(""+i);

      if(i<=30){
        this.periodicNoList.push(""+i);
      }

    }

    // dispatch data from vehicle picker component
    this.unSub_PickedVehicles = _store.select('PickedVehicles').subscribe(value => {
      console.log(value);
      this.vehicleList = value;
      this.vehicles = [];

      for(let i=0;i<this.vehicleList.length;i++){
        this.vehicles.push(this.vehicleList[i]['uuid']);
      }

      console.log(this.vehicles);
    });

  }// constructor close

  ngOnInit() {

  }

  remove(item){
    this.emailList.splice(item, 1);
    //*************** for remove placeholder ****************
    if(this.emailList.length <=0){
      this.chipValue = "";
    }
    //***************close for remove placeholder ****************
  }

  add(value){
    console.log(value.trim())
    if(this.validate(value.trim())){
      this.emailList.push(value.trim());

      //*************** for remove placeholder ****************
      if(this.emailList.length>0){
        this.chipValue = " ";
      }else{
        this.chipValue = "";
      }
      //***************close for remove placeholder ****************
    }
    console.log(this.emailList)
  }

  validate(email){
    let pattern=/^([a-zA-Z0-9_.-])+@([a-zA-Z0-9_.-])+\.([a-zA-Z])+([a-zA-Z])+/;
    if(pattern.test(email)){
      return true
    }else{
      return false;
    }
  }

  goToStep2(){
    console.log(this.day);
    console.log(this.scheduleTime);
    this.steperNo =2;
  }

  goToStep3(){
    this.steperNo =3;
  }

  create(){
   let reqObj = {
      "customerId": "rohitpalod-fretron", // @todo remove when back-end remove
      "emailList": this.emailList,
      "vehicleList":  this.vehicles,
      "reportSchduleType": this.selectedFrequency,
      "reportType": this.reportTypes,
      "time": this.scheduleTime.getTime()
    }

    if(this.selectedFrequency == 'Daily'){
      reqObj['schedule']= {}
    }else  if(this.selectedFrequency == 'Weekly'){
      reqObj['schedule']= {
        "daysOfWeek": this.getWeekNos(this.day)
      }
    }else  if(this.selectedFrequency == 'Monthly'){
      reqObj['schedule']= {
        "datesOfMonth": [Number(this.dateNo)]
      }
    } else if(this.selectedFrequency == 'Periodic'){
      reqObj['schedule']= {
        "noOfDays": Number(this.periodicNo)
      }
    }

    console.log(reqObj);

    this._angularitics.eventTrack('Create_Schedule', reqObj);
    this.scheduleReportService.createNewSchedule.emit(reqObj);

    // dispatch data from vehicle picker component
    this.unSub_scheduleCreatedAck = this._store.select('scheduleCreatedAck').subscribe(value => {
      console.log(value);
      if(value !=null){
        if(value['error'] == ''){
            // console.log(value['errorMsg']);
          this.openSnackBar(value['errorMsg']);
          this._store.dispatch(go(['/vtsdashboard/scheduleList']));
        }else{
          // console.log(value['errorMsg']);
          this.openSnackBar(value['errorMsg']);
        }
      }
    });
  }// close methods create schedule


  getWeekNos(days){
    let daysNos = [];
    if(days.sunday == true)
      daysNos.push(0);

    if(days.monday == true)
      daysNos.push(1);

    if(days.tuesday == true)
      daysNos.push(2);

    if(days.wednesday == true)
      daysNos.push(3);

    if(days.thursday == true)
      daysNos.push(4);

    if(days.friday == true)
      daysNos.push(5);

    if(days.saturday == true)
      daysNos.push(6);

    return daysNos;
  }

  selReportType(){
    console.log(this.reportType);
    this.validationMsg ='';
    this.rType=''; // string value for show in input box
    let tempReportTypes =[];
    if(this.reportType.running ==true){
      this.rType = 'Running';
      tempReportTypes.push('Running');
    }

    if(this.reportType.stoppage ==true){
      this.rType = this.rType+' Stoppage';
      tempReportTypes.push('Stoppage');
    }

    if(this.reportType.timeline ==true) {
      this.rType = this.rType + ' Timeline';
      tempReportTypes.push('Timeline');
    }

    if(tempReportTypes.length >0){
      this.isReportTypeDialog=false;
      this.reportTypes = JSON.parse(JSON.stringify(tempReportTypes));
     }else{
       this.validationMsg ="Please select at least one"
       for(let i=0;i<this.reportTypes.length;i++){
         this.rType =this.rType+this.reportTypes[i]+" ";
       }
    }

    this.rType =this.rType.trim();
    this.rType =this.rType.replace(/ /g,", ");

    // console.log(this.rType);
    // console.log(this.reportTypes);
    // console.log(tempReportTypes);
  }

  openSnackBar(message: string) {
    this.snackBar.open(message, '', <MdSnackBarConfig>{duration :  3000});
  }
}



