import {ElementRef, Component, OnInit, NgZone, ViewChild, AfterViewInit, Output, EventEmitter} from '@angular/core';
import { Store } from "@ngrx/store";
import { go, back } from "@ngrx/router-store";
import { FormControl } from "@angular/forms";
import { isNullOrUndefined } from "util";
import { MdSnackBar, MdSnackBarConfig } from "@angular/material";
import {TripTemplateServices} from "../../../services/tripTemplate-services";
import { TripServices } from "../../../services/tripServices";

import {LocalStorageService} from "angular-2-local-storage";
import {ETA_DistanceService} from "../../../services/eta-distance.service";



@Component({
  selector: 'app-add-trip-template',
  templateUrl: './add-trip-template.component.html',
  styleUrls: ['./add-trip-template.component.css']
})



export class AddTripTemplateComponent implements OnInit {

  // @Output()


  public searchOriginControl: FormControl;
  public searchDestControl: FormControl;
  @ViewChild("searchOrigin")
  public searchOrigineElementRef: ElementRef;

  @ViewChild("searchDestination")
  public searchDestElementRef: ElementRef;

  public orgId;
  public isClicked:boolean=false;
  public originObj = null;
  public originName: string = "";
  public originAddress: string = "";
  public destinObj = null;
  public destinationName: string = "";
  public destinationAddress: string = "";

  public unSub_freightLocation=null;
  public waitForLastResp: boolean = false;
  public isAddVehicleDialog: boolean = false;
  public isShowLocationDialog:boolean=false;
  public unSub_etaDistance = null;
  public etaDistanceData: any[] = [];

  public transitDays: number=0;
  public distance =null;
  public material = null;
  public quantity = null;
  public locationNameList:any[]=[];
  public showAddressList=false;
  public showDestAddressList=false;
  public locationType;
  public oLat: any;
  public dLat: any;
  public oLng: any;
  public dLng: any;

  public unSub_searchName = null;
  public unSub_detailsAddTripTemplate = null;
  public unSub_sourceAddress=null;
  public unSub_destinationAddress=null;
  public typeOfLocationCreator:any={ type : "Polygon"};

  public searchLocationByName = new EventEmitter<any>();

  ngOnDestroy() {
    this._store.dispatch({ type: "IS_BACK", payload: { isBack: false, backToComponent: '' } });
    this._store.dispatch({type :"ETA_DISTANCE" ,payload : null})
    if (typeof this.unSub_detailsAddTripTemplate!= 'undefined' && this.unSub_detailsAddTripTemplate != null && this.unSub_detailsAddTripTemplate != undefined)
    this.unSub_detailsAddTripTemplate.unsubscribe();

    if (typeof this.unSub_sourceAddress != 'undefined' && this.unSub_sourceAddress != null && this.unSub_sourceAddress != undefined) {
      // console.log(" unSub_sourceAddress");
      this.unSub_sourceAddress.unsubscribe();
    }
    if (typeof this.unSub_destinationAddress != 'undefined' && this.unSub_destinationAddress != null && this.unSub_destinationAddress != undefined) {
      // console.log(" unSub_destinationAddress");
      this.unSub_destinationAddress.unsubscribe();
    }

    if (typeof this.unSub_etaDistance != 'undefined' && this.unSub_etaDistance != null && this.unSub_etaDistance != undefined) {
      this.unSub_etaDistance.unsubscribe();
    }

    if (typeof this.unSub_searchName != 'undefined' && this.unSub_searchName != null && this.unSub_searchName != undefined) {

      this.unSub_searchName.unsubscribe();
    }
  }

  constructor(private _store: Store<any>,public localStorageService:LocalStorageService, public templateService: TripTemplateServices, private tripServices: TripServices,public snackBar: MdSnackBar,private eta_distance: ETA_DistanceService)
   {
     this.orgId=this.localStorageService.get("ORGID");
     console.log(this.orgId);
    this._store.dispatch({ type: "TITLE", payload: 'Add Trip Template' });

     this.unSub_etaDistance = this._store.select("etaDistanceStore").subscribe(res => {
       if (res == null)
         return;

       console.log(res['value'])

       this.etaDistanceData = res['value'];
       if (this.etaDistanceData != null) {
         console.log(this.etaDistanceData[0]['status'])
         if (this.etaDistanceData[0]['status'] == 'OK') {
           this.setETA_Distance()
         }
         else {
           this.transitDays = 0;
           this.distance = "0";
         }
         console.log(this.etaDistanceData);
       }

     });
}
setLocationType(type){
  this.locationType=type;
  this.searchString();
  this.isShowLocationDialog=true;
}
setOriginLocation(value){
  console.log(value);
  let type=this.locationType;

    let obj1 ={};
    if(value['freightLocation']!= null || value['freightLocation'] != undefined)
    {
      console.log('contains location Obj source');
      obj1= value['freightLocation'];
    }
    else {
      console.log('contains No freight location Obj source');
      obj1= value;
    }

    if (isNullOrUndefined(obj1['address']) || obj1['address']== "") {
      obj1['address']=value['name']
    }
    this.originName = obj1['name'];
    this.oLat = obj1["geofence"]["type"]== 'Proximity'?obj1["geofence"]["circle"]["center"]["latitude"] : obj1["geofence"]["center"]["latitude"];
  this.oLng = obj1["geofence"]["type"]== 'Proximity'?obj1["geofence"]["circle"]["center"]["longitude"] : obj1["geofence"]["center"]["longitude"];
    this.etaDistanceCalaculater(this.oLat,this.oLng,this.dLat,this.dLng);
    this.showAddressList = false;
    this.originObj = obj1;
  this.isShowLocationDialog=false;
}

setDestinationLocation(value)
{

  let obj ={};
  if(value['freightLocation']!= null || value['freightLocation'] != undefined)
  {
    console.log('contains location Obj Destination');
    obj= value['freightLocation'];
  }
  else {
    console.log('contains No freight location Obj Destination');
    obj= value;
  }
  if (isNullOrUndefined(obj['address']) || obj['address']== "") {
    obj['address']=value['name']
  }
  this.destinationName = obj['name'];

  // this.dLat = value["freightLocation"]["geofence"]["center"]["latitude"];
  this.dLat = obj["geofence"]["type"]== 'Proximity'?obj["geofence"]["circle"]["center"]["latitude"] : obj["geofence"]["center"]["latitude"];
  // this.dLng =value["locObj"]["freightLocation"]["geofence"]["center"]["longitude"];
  this.dLng =obj["geofence"]["type"]== 'Proximity'?obj["geofence"]["circle"]["center"]["longitude"] : obj["geofence"]["center"]["longitude"];
  this.etaDistanceCalaculater(this.oLat,this.oLng,this.dLat,this.dLng);
  this.showDestAddressList = false;
  this.destinObj = obj;
  this.isShowLocationDialog=false;
}
  ngOnInit() {
  }

 // *****************  create new trip  ********************8
 addTemplateData() {
  if (isNullOrUndefined(this.originName) || this.originName == "") {
    this.openSnackBar("Please fill Trip Origin");
    return;
  }
  if (isNullOrUndefined(this.destinationName) || this.destinationName== "") {
    this.openSnackBar("Please fill Trip Destination");
    return;
  }
  if (isNullOrUndefined(this.transitDays) || "" + this.transitDays == "") {
    this.openSnackBar("Please fill Transit Days");
    return;
  }
   if (isNullOrUndefined(this.material) || "" + this.material == "") {
    this.openSnackBar("Please fill Material Type");
    return;
  }

  let tripData = {
              "orgId": this.orgId,
              "origin": this.originObj,
              "destination": this.destinObj,
              "transitDays": this.transitDays,
              "distance": this.distance,
              "material": this.material,
              "quantity":this.quantity
          };
  this.isClicked=true;
  console.log(tripData);
  this.templateService.createTripTemplate.emit(tripData);
  this.templateService.tripTemplateStatus.emit();
   this._store.dispatch({type: "REFERSH_TRIP_TEMPALTE",payload:'Reload Template List'});
  this._store.dispatch(go(['/vtsdashboard/tripTemplateStatus']));
}
openSnackBar(message: string) {
  this.snackBar.open(message, 'OK', <MdSnackBarConfig>{ duration: 4000 });
}

etaDistanceCalaculater(oLat,oLng,dLat,dLng) {
  if (dLng != null && dLat != null && oLat!=null && oLng!=null) {
    // this.eta_distance.path = "https://maps.googleapis.com/maps/api/distancematrix/json?units=imperial&origins=" + this.oLat + "," + this.oLng + "&destinations=" + this.dLat + "," + this.dLng + "&key=AIzaSyCLN-Y6Ugg4D-kzS1l_keqhyJ4tam-TKiI";
    let obj = {"oLat" : oLat, "oLng":oLng, "dLat":dLat, "dLng" :dLng}
    this.eta_distance.getEtaDistance.emit(obj);
  }
}
  cancelAddTrip()
  {
    this._store.dispatch(go(['/vtsdashboard/tripTemplateStatus']));
  }

  searchString()
  {
    let searchStringVal:string="";
    console.log(this.locationType);
    console.log(this.originName);
    if(this.locationType== 'origin')
    {
      searchStringVal=this.originName;
    }
    if(this.locationType== 'destination')
    {
      searchStringVal=this.destinationName;
    }
    console.log(searchStringVal);
    this.searchLocationByName.emit(searchStringVal);
  }

  setETA_Distance(){
    let dur = Number(this.etaDistanceData[0]["duration"]["value"]) / (60 * 60 * 24);
    console.log(Math.ceil(dur));

    console.log(dur);
    this.transitDays = Math.ceil(dur);

    // Number(this.etaDistanceData[0]["distance"]["value"])

    this.distance = String(Number((this.etaDistanceData[0]["distance"]["value"]) / 1000).toFixed(2));
  }

}



