  constructor(private _router:Router,private localStorageService:LocalStorageService,public mapApiWrapper: GoogleMapsAPIWrapper,private _store:Store<any>,private tripServices:TripServices) {
    // this.latitude = 21.15083373;
    // this.longitude = 79.081145;
    // this.zoom = 14;
    // this.currentVts = 'waitForLoad';
    this.view =document['view'];

    if(this._router.url =='/vtsdashboard/trip/status/details/editTripLocation' && this.view =='web'){
      this._store.dispatch(go(['vtsdashboard/trip/status']));
      return;
    }

    let tempObject:any=this.localStorageService.get("VEHICLE-TRIP-DETAIL-OBJ");
    if(tempObject == undefined || tempObject == null ){
      // this._store.dispatch(go(['']));
      return;
    }
    console.log(tempObject);
     this.vehicleTripDetailsObject = JSON.parse(JSON.stringify(tempObject));

    if(this.vehicleTripDetailsObject['forEditPlace'] == 'destination'){
      this.headerName = 'Edit Destination'
    }else  if(this.vehicleTripDetailsObject['forEditPlace'] == 'pickup'){
      this.headerName = 'Edit Pickup'
    }

    this._store.dispatch({type: "TITLE",payload:this.headerName}); // set the header name
    _store.dispatch({type: "IS_BACK",payload:{isBack:true,backToComponent:"trip/status/details"}});

    if(this.vehicleTripDetailsObject['vehicleTripDetails'].movementLegs != null){
    for(let i=0 ;i<this.vehicleTripDetailsObject['vehicleTripDetails'].movementLegs.length;i++){
           this.vehicleTripDetailsObject['vehicleTripDetails'].movementLegs[i]._totalTime = this.msToTime(this.vehicleTripDetailsObject['vehicleTripDetails'].movementLegs[i].totalTime)
      }
    }
    console.log(this.vehicleTripDetailsObject);
    this.setupUI(this.vehicleTripDetailsObject); // call for setup various ui according to vehicle state
  } //close constructor

