import { ActionReducer, Action } from '@ngrx/store';
import {ActionRecorderServices } from '../services/actionRecorder.service';

var obj=new ActionRecorderServices();

export function selectedFilterVehicleStatus(state: any = null, action:Action) {

  switch (action.type) {
    case "SELECTED_VEHICLE_FILTER_STATUS" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}

export function vehicleListSearchFliter(state: any = null, action:Action) {

  switch (action.type) {
    case "VEHICLELIST_SEARCH_FILTER" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}

export function selectedVehicleLocationHistory(state: any = null, action:Action) {

  switch (action.type) {
    case "SELECTED_VEHICLE_LOCATION_HISTORY" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}

export function showLocationHistory(state: any = null, action:Action) {
  switch (action.type) {
    case "SHOW_LOCATION_HISTORY" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}


export function getTimeline(state: any = null, action:Action) {
  switch (action.type) {
    case "GET_TIMELINE" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}




