import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {Store} from "@ngrx/store";
import {Services} from "../../services/services";
import {go} from "@ngrx/router-store";
import {LocalStorageService} from "angular-2-local-storage";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";


@Component({
  selector: 'vehicle-sharing',
  templateUrl: './vehicle-sharing.component.html',
  styleUrls: ['./vehicle-sharing.component.css']
})
export class VehicleSharing implements OnInit {
  public isSpinner=true;
  public  vehicles=[];
  public  search='';
  public  isDisabled=true;
  public  fromDate=null;
  public  accesssLevel='';
  public  email=null;
  public  toDate=null;
  public vehicleList:any=[];
  public emailList:any=[];
  public errorMsg='';
  public isShareVehicleDialog=false;
  public unSub_vehicleList=null;
  public unSub_customerDetails:any=null;
  public unSub_shareDetails:any=null;
  public str="";
  public customerId=""


  ngOnDestroy() {
    if (typeof this.unSub_vehicleList != 'undefined' && this.unSub_vehicleList != null && this.unSub_vehicleList != undefined)
      this.unSub_vehicleList.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();

    if (typeof this.unSub_shareDetails != 'undefined' && this.unSub_shareDetails != null && this.unSub_shareDetails != undefined)
      this.unSub_shareDetails.unsubscribe();
  }

  ngOnInit(){
  }

  constructor(private _store:Store<any>,public snackBar: MdSnackBar,public _services:Services,private localStorageService: LocalStorageService) {
    let customer={};
    let token = this.localStorageService.get("TOKEN");
    console.log("token");
    console.log(token);
    this._store.dispatch({type: "TITLE",payload:'Vehicle Sharing'});// set the header name
    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      if(value==null)
        return;

      if(value['error']==''){
        customer= value['customer'];
        console.log(customer);
        this.customerId=customer['id'];
        // _services.getVehicleList.emit(customer.customerId);
      }else{
        return;
      }

      // ***************  vehicle list ************************************
      this.unSub_vehicleList = _store.select('vehicleListRes').subscribe(value => {
        // console.log(value);
        this.errorMsg='';
        // check not  exist then request for list
        if(value==null){
          _services.getVehicleList.emit(customer['id']);
          return;
        }

        if(value['error']==''){
          let vehicleList = JSON.parse(JSON.stringify(value['data']));
          this.vehicleList= vehicleList.sort(this.compare);

          for(let i=0;i<this.vehicleList.length;i++){
            this.vehicleList[i]['select']=false;
          }

        }else {
          console.log(value['errorMsg']);
          this.errorMsg=value['errorMsg'];
        }
        this.isSpinner=false;
      });
    });
  } //constructor

  // ************* for shoting purpose  **********************************
  compare(a,b) {
    if (a.vehicleType.toUpperCase() < b.vehicleType.toUpperCase())
      return -1;
    if (a.vehicleType.toUpperCase() > b.vehicleType.toUpperCase())
      return 1;
    return 0;
  }

  checkSelect(){
      for(let i=0;i<this.vehicleList.length;i++)
      {
        if(this.vehicleList[i]['select']==true){
          this.isDisabled=false;

          break;
        }else{
          this.isDisabled=true;
        }
      }
  }


  shareVehicle(){
    this.isShareVehicleDialog=false;
    let selectedVehicles=[];
    for(let i=0;i<this.vehicleList.length;i++)
    {
      if(this.vehicleList[i]['select']==true){
        selectedVehicles.push(this.vehicleList[i]);
      }
    }
    console.log(selectedVehicles);
  }

  // makeChip(event)
  // {
  //   if((event.keyCode>=33 && event.keyCode<=122)||(event.keyCode==190)) {
  //
  //     this.str = this.str + event.key;
  //   }
  //
  //   if((event.keyCode==8)) {
  //
  //     this.str = this.str.substring(0,this.str.length-1);
  //   }
  //
  //   if(event.keyCode==188 || event.keyCode==32 || event.keyCode==186)
  //   {
  //     if(this.str!="") {
  //       this.emailList.push(this.str);
  //     }
  //     console.log(this.emailList);
  //     this.str="";
  //   }
  // }


  makeChip(emails)
  {
    this.emailList=[];
    console.log('email string:');
    console.log(emails);
    let emailid=emails.trim();
    let emailsArr=[];
    emailsArr=emailid.split(",");
    this.emailList=emailsArr;
  }



  shareSelectedVehicles()
  {
    let selectedVehicle=[];

    for(let i=0; i<this.vehicleList.length;i++)
    {
      if (this.vehicleList[i].select)
      {
        selectedVehicle.push(this.vehicleList[i].uuid);
      }
    }
    var obj={
      "sharedByCustomer": this.customerId,
      "sharedWith": this.emailList,
      "vehicleList": selectedVehicle,
      "validTill": this.toDate.getTime(),
      "validFrom": this.fromDate.getTime(),
      "accessLevel":this.accesssLevel
    }

    console.log(obj);

    if(this.checkValid(obj)) {
      this._services.shareVehicleList(obj);
    }

    this.unSub_shareDetails=this._store.select('shareVehicle').subscribe((data)=>{
      console.log('data');
      console.log(data);

      if(data!= null || data!= undefined) {
        if (data['error'] == "") {
          // this.openSnackBar(data['data']['message'])
          this._store.dispatch(go(['/vtsdashboard']));
        }
        else {
          console.log(data);
          this.openSnackBar(data['error'])

        }
      }

    })
  }

  checkValid(obj)
  {
    if(obj['sharedByCustomer']==''||obj['sharedByCustomer']==null ||obj['sharedByCustomer']==undefined)
    {
      return false;
    }

    if(obj['sharedWith'].length==0)
    {
      return false;
    }

    if(obj['vehicleList'].length==0)
    {
      return false;
    }

    if(obj['validTill']==''||obj['validTill']==null ||obj['validTill']==undefined)
    {
      return false;
    }

    if(obj['validFrom']==''||obj['validFrom']==null ||obj['validFrom']==undefined)
    {
      return false;
    }

    if(obj['accessLevel']==''||obj['accessLevel']==null ||obj['accessLevel']==undefined)
    {
      return false;
    }
    return true;
  }

  openSnackBar(message: string) {
    this.snackBar.open(message , '', <MdSnackBarConfig>{duration :  3000});
  }

}



