// ***********************  get vehicle list **********************
  public getList(path){
    console.log(path);   // let queryParameters = new URLSearchParams();
    let headers = new Headers(this.defaultHeaders.toJSON());
    // headers.set('Content-Type', 'application/x-www-form-urlencoded; charset=UTF-8');
    // let token=this.localStorageService.get('TOKEN');
    let token=this.localStorageService.get('AUTH_TOKEN');
    console.log(token);
    let bearerToken="Bearer "+token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    console.log(headers);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: Id,
    });

    return this.http.request(path, requestOptions).map((response: Response) => {
      console.log(response.status);
      console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }
    });
  }


  public getVehicleAddress(path){
    let headers = new Headers();
    // headers.append('Access-Control-Allow-Origin', '*');
    // headers.append('Access-Control-Allow-Methods','GET,PUT,POST,DELETE,OPTIONS');


    // headers.set('Content-Type', 'application/json');
    // headers.set("accept", "text/xml");

    // console.log(headers);
    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: Id,
    });

    let token=this.localStorageService.get('TOKEN');
    let bearerToken="Bearer "+token;
    headers.set('authorization', bearerToken);

    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      // console.log(response);
        return response.json();
    });
  }


  // ***********************  get location **********************
  public getLocation(path,data){
    // console.log(data);
    // let queryParameters = new URLSearchParams();
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let token=this.localStorageService.get('TOKEN');
    console.log('token');
    console.log(token);
    let bearerToken="Bearer "+token;
    headers.set('authorization', bearerToken);

    // console.log(headers);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body:data ,
    });




    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      // console.log(response);
      if(response.status==200){
        console.log("response.json():");
        console.log(response.json());
        return response.json();
      }else if(response.status==204){
        return {error:'N'};
      }else {
        return {error:'X'};
      }
    });
  }

