  public shareVehicle(path,data){
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let token=this.localStorageService.get('TOKEN');
    console.log('token');
    console.log(token);
    let bearerToken="Bearer "+token;
    headers.set('authorization', bearerToken);

    // console.log(headers);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body:data ,
    });

    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      // console.log(response);
      if(response.status==200){
        console.log("response.json():");
        console.log(response.json());
        // let res={"status":response.status,"message":response.json()};
        let res=response.json();

        // this._store.dispatch({type: "SHARE_VEHICLE",payload:{error:'',data:res}});
        return res;
      }else if(response.status==204){
        return {error:'N'};
      }else {
        return {error:'X'};
      }
    });
  }



