import { Component, ViewChild, OnInit } from '@angular/core';
import { MapsAPILoader, AgmMap, GoogleMapsAPIWrapper } from "@agm/core";
import * as mapTypes from '@agm/core/services/google-maps-types';


@Component({
  selector: 'app-add-shipper-location',
  templateUrl: './add-shipper-location.component.html',
  styleUrls: ['./add-shipper-location.component.css']
})
export class AddShipperLocationComponent implements OnInit {

  @ViewChild(AgmMap) agmMap;

  public latitudeOrigin: number;
  public longitudeOrigin: number;

  public latitudeDestination: number;
  public longitudeDestination: number;
  public oZoom: number;
  public dZoom: number;

  public map: mapTypes.GoogleMap;

  constructor(public mapApiWrapper: GoogleMapsAPIWrapper) { }

  ngAfterViewInit() {
    //   this.mapApiWrapper.getNativeMap().then((map) => {
    //     this.map = map;
    //     console.log(map);
    // }, err=>{
    //   console.log('error',err);
    // });
    this.agmMap.mapReady.subscribe(map => {
      this.map = map ;
    });
  }
  ngOnInit() {
    this.latitudeOrigin = 21.149633;
    this.longitudeOrigin = 79.081145;

    this.latitudeDestination = 21.149633;
    this.longitudeDestination = 79.081145;

    this.oZoom = 4;
    this.dZoom = 4;
  }

  markerDragPosition(event) {

    console.log(event);
    console.log("drag end");
    let latPolygonOrigin = event.coords.lat;
    let lngPolygonOrigin = event.coords.lng;
    console.log(this.map);

    let points = this.getNPointsCircularPolygone({lat : latPolygonOrigin , lng : lngPolygonOrigin} , 0.5 , 5);
       // Styling & Controls
    let polygone = this.addPolygone(this.map , points);

  }


  addPolygone(map, points) {
    // Map Center

    // Styling & Controls
    var myPolygon = new google.maps.Polygon({
      paths: points,
      draggable: true, // turn off if it gets annoying
      editable: true,
      strokeColor: '#FF0000',
      strokeOpacity: 0.8,
      strokeWeight: 2,
      fillColor: '#FF0000',
      fillOpacity: 0.35
    });
    myPolygon.setMap(map);
    return myPolygon;
  }


  getNPointsCircularPolygone(center, dist, n) {

    var i = 0;
    var polygonePoint = [];
    for (i = 1; i <= n; i++) {
      polygonePoint.push(getPointAt(center, (360.0 / n) * (i), dist))
    }


    function toRAD(NUM) {
      return NUM * Math.PI / 180;
    }
    function toDeg(NUM) {
      return NUM * 180 / Math.PI;
    }

    function getPointAt(latlng, brng, dist) {
      // console.log(latlng);
      // console.log(latlng);
      dist = dist / 6371;
      brng = toRAD(brng);

      var lat1 = toRAD(latlng.lat), lon1 = toRAD(latlng.lng);
      var lat2 = Math.asin(Math.sin(lat1) * Math.cos(dist) + Math.cos(lat1) * Math.sin(dist) * Math.cos(brng));
      var lon2 = lon1 + Math.atan2(Math.sin(brng) * Math.sin(dist) * Math.cos(lat1), Math.cos(dist) - Math.sin(lat1) * Math.sin(lat2));

      if (isNaN(lat2) || isNaN(lon2)) return null;
      return new google.maps.LatLng(toDeg(lat2), toDeg(lon2))
    };
    return polygonePoint;
  }
}




