import { ActionReducer, Action } from '@ngrx/store';
import {ActionRecorderServices } from '../services/actionRecorder.service';

var obj=new ActionRecorderServices();

export function tripTemplateList(state: any =null, action:Action) {
  switch (action.type) {
    case 'TRIP_TEMPLATE_STATUS_LIST':
    {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }
}
export function tripTemplateDetail(state: any = null, action:Action) {
    switch (action.type) {
      case 'TEMPLATE_DETAILS':
      {
        obj.addAction(action);
        return action.payload;
      }
  
      default:
        return state;
    }
  }
  export function createTripTemplate(state: any = null, action:Action) {
    switch (action.type) {
      case 'CREATE_TRIP_TEMPLATE':
      {
        obj.addAction(action);
        return action.payload;
      }
  
      default:
        return state;
    }
  }
  export function updateTripTemplate(state: any = null, action:Action) {
    switch (action.type) {
      case 'UPDATE_TRIP_TEMPLATE':
      {
        obj.addAction(action);
        return action.payload;
      }
  
      default:
        return state;
    }
  }
  export function deleteTripTemplate(state: any = null, action:Action) {
    switch (action.type) {
      case 'DELETE_TRIP_TEMPLATE':
      {
        obj.addAction(action);
        return action.payload;
      }
  
      default:
        return state;
    }
  }
  
  export function addTripTemplateSaveDetails(state: any = null, action:Action) {
    switch (action.type) {
      case 'ADD_TRIP_TEMPLATE_SAVE_DETAILS':
      {
        obj.addAction(action);
        return action.payload;
      }
  
      default:
        return state;
    }
  }
  export function saveEditTripTemplateDetails(state: any = null, action:Action) {
    switch (action.type) {
      case 'EDIT_TRIP_TEMPLATE_DETAILS':
      {
        obj.addAction(action);
        return action.payload;
      }
  
      default:
        return state;
    }
  }
  


