import {ActionReducer, Action, combineReducers} from '@ngrx/store';
// import {routerActions} from "../app-states/actions";
// import { routerActions } from './actions';

import {ActionRecorderServices} from "../services/actionRecorder.service";
var obj=new ActionRecorderServices();


export function isBack(state: any = null, action:Action) {
  switch (action.type) {
    case "IS_BACK" : {
      obj.addAction(action);
      return action.payload;
    }
    default:
      return state;
  }
}


export function closeDialog(state: any = false, action:Action) {
  switch (action.type) {
    case "CLOSE_DIALOG" : {
      obj.addAction(action);
      return action.payload;
    }
    default:
      return state;
  }
}


