import { Component, Input, ViewChild } from '@angular/core';
import { Router } from "@angular/router";
import { Store } from "@ngrx/store";
// import {Services} from "../../services/services";
// import {AuthenticationServices} from "../../services/authentication.service";
import { Angulartics2Mixpanel } from "angulartics2";
import { go } from "@ngrx/router-store";
// import {SocketService} from "../../services/socket-service";
import * as mapTypes from '@agm/core/services/google-maps-types';
import { AgmMap , GoogleMapsAPIWrapper } from "@agm/core";
import { } from '@types/googlemaps';
import { Services } from "../../../services/services";
import { SocketService } from "../../../services/socket-service";
import { isNullOrUndefined } from "util";

import {DashboardCoordinator} from "../../../services/dashboard.cordinator.service"

declare var MarkerClusterer:any;



@Component({
  selector: 'app-maps',
  templateUrl: './maps.component.html',
  styleUrls: ['./maps.component.css']
})
export class MapsComponent {
  @ViewChild(AgmMap) agmMap;

  // public view:String="map";
  public unSub_vehicleList = null;
  public search = '';
  public status = '';
  public unSub_searchFilter = null;
  public unSub_customerDetails: any = null;
  public unSub_vehicleStatusFilter = null;
  public unsub_interval: any = null;
  public vehicleList = [];
  public filters : any = {search : null , state : null };
  title: string = 'Fretron Traccar';
  public view = '';
  // lat: number = 28.45926888966;
  // lng: number = 77.06804178966;
  public map: mapTypes.GoogleMap;
  public markerCluster : any ;
  public context : any;

  ngOnDestroy() {
    this.socketService.disconnectSocket();
    if (typeof this.unSub_searchFilter != 'undefined' && this.unSub_searchFilter != null && this.unSub_searchFilter != undefined) {
      console.log("unSub_searchFilter");
      this.unSub_searchFilter.unsubscribe();
    }

    if (typeof this.unSub_vehicleList != 'undefined' && this.unSub_vehicleList != null && this.unSub_vehicleList != undefined) {
      this.unSub_vehicleList.unsubscribe();
    }

    if (typeof this.unSub_vehicleStatusFilter != 'undefined' && this.unSub_vehicleStatusFilter != null && this.unSub_vehicleStatusFilter != undefined) {
      console.log("unSub_vehicleStatusFilter");
      this.unSub_vehicleStatusFilter.unsubscribe();
    }

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined) {
      this.unSub_customerDetails.unsubscribe();
    }
  }


  ngOnInit() {

 this.agmMap.mapReady.subscribe(map => {
      this.map = map ;
      this.loadClusterData();
    });

    this._angularitics.eventTrack('Maps_View_Opened', { category: 'DashboardMaps', label:'Vehicles_View_Opened'});
  }

  constructor( public dashboardCoordinator : DashboardCoordinator, public mapApiWrapper: GoogleMapsAPIWrapper ,private socketService: SocketService, private _store: Store<any>, private _router: Router, _services: Services, public _angularitics: Angulartics2Mixpanel) {
    this._store.dispatch({ type: "TITLE", payload: 'Map Dashboard' }); // set the header name


    this.context = this;
    this.view = document['view'];


    if (this._router.url == '/vtsdashboard/mapsComponents' && this.view == 'web') {
      this._store.dispatch(go(['vtsdashboard/vehicleDashboard']));
      return;
    }

    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      console.log(value);
      if (value == null) {
        return;
      }

      if(this.view != 'web'){
        this.socketService.getVehicleList();
      }
    });

    // ***************  vehicle list ************************************
    this.unSub_vehicleList = _store.select('vehicleListRes').subscribe(value => {
      console.log(value);
      // this.errorMsg='';
      // check not  exist then request for list
      if (value == null) {
        // _services.getVehicleList.emit(customer['id']);
        return;
      }

      // let time = new Date().getTime();
      if (value['error'] == '') {

        this.vehicleList = value['data'];
        for (let i = 0; i < this.vehicleList.length; i++) {
          // console.log(this.vehicleList[i]['latitude']);
          // this.vehicleList[i]['lat']=Number(this.vehicleList[i]['latitude']);
          // this.vehicleList[i]['long']=Number(this.vehicleList[i]['longitude']);

          this.vehicleList[i]['_duration'] = this.msToTime(this.vehicleList[i]['duration']);

          this.vehicleList[i]['_endTime'] = this.epochTotime(this.vehicleList[i]['endTime']);

          // Array.observe(this.vehicleList,() => {
          //  let  diffLat =0.00000000;
          //  let  diffLng =0.00000000;
          //  let prevoiusLat = this.vehicleList[i]['latitute'];
          //  let prevoiusLng = this.vehicleList[i]['longitude'];
          //  diffLat = (this.vehicleList[i]['longitude'] - prevoiusLat/20);
          //  diffLng = (this.vehicleList[i]['longitude'] - prevoiusLng/20);
          //
          //  for(let i=0 ;i < 20;i++) {
          //    setTimeout(()=>{
          //    this.vehicleList[i]['longitude'] = this.vehicleList[i]['longitude'] + diffLat;
          //    this.vehicleList[i]['longitude'] = this.vehicleList[i]['longitude'] + diffLng;
          //    // if(i >= 19) {
          //    //   let prevoiusLat = this.vehicleList[i]['latitute'];
          //    //   let prevoiusLng = this.vehicleList[i]['longitude'];
          //    // }
          //    console.log(prevoiusLat);
          //    console.log(prevoiusLng);
          //   },250*i);
          //  }
          //
          //  });

        }
        // this.vehicleList = this.vehicleList.slice(0, 1);
        this.loadClusterData();
        // console.log(this.vehicleList);
      } else {
        console.log(value['errorMsg']);
        // this.errorMsg=value['errorMsg'];
      }
      // console.log( "time taken in for loop" ,new Date().getTime() - time);
      dashboardCoordinator.fileterCoordinator.subscribe((filter) => {

          this.filters = filter ;
          this.loadClusterData();
      })

    });
    // });


    // ***************************vehicle status filter store**********************************
    this.unSub_vehicleStatusFilter = _store.select('selectedFilterVehicleStatus').subscribe(value => {
      if (isNullOrUndefined(value)) {
        return;
      }
      else {
        console.log(value.toString());
        console.log('In vehicle list status filter:');
        this.status = value.toString();
      }
    });


    // *************************** search bar filter **********************************
    this.unSub_searchFilter = _store.select('vehicleListSearchFliter').subscribe(value => {
      if (isNullOrUndefined(value)) {
        return;
      }
      else {
        console.log(value.toString());
        console.log('In search filter:');
        this.search = value.toString();
      }

    });
  }// constructor close



  epochTotime(epochValue){
    if(epochValue != null && epochValue != undefined || epochValue > 0 ) {
      let dateObj = new Date(epochValue);
      let h = dateObj.getHours(),
        m = dateObj.getMinutes();

      return (dateObj.toString().substring(0, 15) + " " + ((h > 12) ? (h - 12 + ':' + m + ' PM') : (h + ':' + m + ' AM')));
    }else{
      return '';
    }
  }


  gotoListView() {
    this._router.navigate(['/vtsdashboard/vehicleDashboard']);
  }

  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration = '';

    if (days > 0) {
      totalDuration = days + "d ";
    }
    if (hours > 0) {
      totalDuration = totalDuration + hours + "hr ";
    }

    if (days >= 1) {
      return totalDuration;
    }

    if (minutes > 0) {
      return totalDuration = totalDuration + minutes + "min";
    }
    if (seconds > 0 && totalDuration == '') {
      totalDuration = totalDuration + seconds + " sec";
    }

    if (totalDuration == '') {
      return '0 sec';
    } else {
      return totalDuration;
    }

  }//close methods msToTime

  back() {
    this._store.dispatch(go(['/vtsdashboard/vehicleDashboard']));
  }



 loadClusterData(){

  if(this.map == null || this.vehicleList == null ){
    return;
  }
  console.log(this.filters);
   var markers= this.vehicleList.filter((v) => this.filter(v , this.context)).map(function(vehicle, i) {

    let marker  =  new google.maps.Marker({
      position: {lat : vehicle.latitude , lng : vehicle.longitude },
      icon : '../../../../images/marker_truck_32X32.png'
      //label: labels[i % labels.length]
    });
    var contentString = `<div class="marker-info" >
    <table >
      <tr>
        <td ><span style="color: black;font-size: 15px;font-weight: 400;">
         ${vehicle['vehicleRegistrationNumber'] }</span>
         </td>
         <td>
            <span style="font-size:11px">${ vehicle['vehicleType'] }</span>
        </td>
      </tr>
      <tr >
        <td > ${ (vehicle['speed']).toFixed(2)} km/h</td>
        <td style="text-align: right">  ${ vehicle['_duration']} </td>
      </tr>
      <tr><td colspan="2">  ${vehicle.address} </td></tr>
      <tr style="margin-top: 2px">
        <td>
          <span> ${vehicle['_endTime']} </span></td>
          <td style="text-align: right;">
          <span style="font-size: 13px;font-weight: bold;color:brown;" > ${vehicle['state']} </span>
        </td>
      </tr>
    </table>
  </div>`;

  var infowindow = new google.maps.InfoWindow({
    content: contentString
  });
  marker.addListener('click', function() {
    infowindow.open(this.map, marker);
  });
  return marker;
  });
  if(this.markerCluster){
    this.markerCluster.clearMarkers();
    this.markerCluster.addMarkers(markers);
    this.markerCluster.fitMapToMarkers();
  }else{
    this.markerCluster = new MarkerClusterer(this.map, markers,
      {imagePath: 'https://developers.google.com/maps/documentation/javascript/examples/markerclusterer/m'});
      this.markerCluster.fitMapToMarkers();

    }
}

filter(v : any , context){

  if(!v.latitude){
    return false;
  }

  if(!v.longitude){
    return false;
  }


  if(context.filters.state && context.filters.state != "" ){
    if( v.state != context.filters.state){
      return false;
    }
  }

  if(context.filters.search && context.filters.search != "" ){
    if(v.vehicleRegistrationNumber.indexOf(context.filters.search) == -1){
      return false;
    }
  }

  return true;
}


}



