import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {Store} from "@ngrx/store";
import {Services} from "../../services/services";
import {AlertService} from "../../services/alert-services";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import {go} from "@ngrx/router-store";
import { Angulartics2Mixpanel } from 'angulartics2/dist';

@Component({
  selector: 'app-configure-alerts',
  templateUrl: './configure-alerts.component.html',
  styleUrls: ['./configure-alerts.component.css']
})
export class ConfigureAlertsComponent implements OnInit {

  public isRecordAvailable:boolean=false;
  public alertsList:any[]=[];
  public alerts:any;
  public view ='';
  public customerId:any;
  public customerOrgId:any;
  public isShowConfirmDialogue:boolean=false;
  public alertObjForDelete:any={};

  private unSub_AlertsList: any =null;
  private unSub_deleteAlertsResponse: any =null;
  private unSub_customerDetails: any =null;

  constructor(private _store:Store<any>,public _services:Services,public alertsService:AlertService,public snackBar: MdSnackBar,public _angularitics:Angulartics2Mixpanel) {
    this._store.dispatch({type: "TITLE",payload:'Configure Alerts'});// set the header name



    this.view=document['view'];

    this.unSub_customerDetails =  this._store.select('customerDetails').subscribe(value => {
      console.log("customer datails in alerts");
      console.log(value);
      if(value==null)
        return;

      this.customerId=value['customer']['id'];
      this.customerOrgId=value['customer']['orgId'];
      this.alertsService.getAllAlertsList.emit(this.customerOrgId);
      // this.customerMobile=value['customer']['mobileNumber'];
    });


    this.unSub_AlertsList = _store.select('configuredAlertsList').subscribe(value => {

      if(value==null)
      {
        return;
      }
      console.log('all Alerts list in component');
      console.log(value);
      this.alerts=value;

      this.alertsList=[];

      for(let i=0; i<this.alerts.length; i++)
      {
        this.alertsList.push(this.alerts[i]);
      }
      console.log("ALL ALERTS********************************");
      console.log(this.alertsList);

      if(this.alerts.length>0)
      {
        this.isRecordAvailable=true;
      }
    });

    this.unSub_deleteAlertsResponse = _store.select('alertDeleteServiceResponse').subscribe(value => {

      if(value==null)
      {
        return;
      }
      console.log('Delete Alert Response');
      console.log(value);

      if(value['status']== 200)
      {
        this.alertsService.getAllAlertsList.emit(this.customerOrgId);
        this.openSnackBar("Alert Deleted Successfully!");
      }

      this.alertsService.getAllAlertsList.emit(this.customerOrgId);

    });

  }

  ngOnInit() {
    this._angularitics.eventTrack('AlertsList_Opened', { category: 'AlertsConfiguration'});
  }

  ngOnDestroy()
  {
    if (typeof this.unSub_AlertsList != 'undefined' && this.unSub_AlertsList != null && this.unSub_AlertsList != undefined)
      this.unSub_AlertsList.unsubscribe();

    if (typeof this.unSub_deleteAlertsResponse != 'undefined' && this.unSub_deleteAlertsResponse != null && this.unSub_deleteAlertsResponse != undefined)
      this.unSub_deleteAlertsResponse.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();
  }

  goToAddAlerts()
  {
    this._store.dispatch(go(['/vtsdashboard/addAlerts']));
  }

  addNewAlert()
  {
    this._store.dispatch({type: "SELECTED_ALERT_FOR_EDIT",payload: null }); // sets value of edit store to null to prevent from
    this.goToAddAlerts();
  }

  deleteSelecetedAlert()
  {
    console.log(this.alertObjForDelete);
    if(this.alertObjForDelete!== {} )
      this.isShowConfirmDialogue=false;
    this.alertsService.deleteSelectedAlert.emit(this.alertObjForDelete);
    this._angularitics.eventTrack('Alert_Item_Deleted', {category: "AlertsConfiguration"});
  }

  editSelectedAlert(selectedAlertObj)
  {
    this._angularitics.eventTrack('Alert_Item_Clicked', {category: "AlertsConfiguration"});
    console.log(selectedAlertObj);
    this._store.dispatch({type: "SELECTED_ALERT_FOR_EDIT",payload:selectedAlertObj});
    this.goToAddAlerts();
  }

  openSnackBar(message: string) {
    this.snackBar.open(message , '', <MdSnackBarConfig>{duration :  3000});
  }
  showConfirmDialogue(alert)
  {
    console.log(alert);
    this.alertObjForDelete=alert;
    this.isShowConfirmDialogue=true;
  }
}






