import {Component, ViewEncapsulation} from '@angular/core';
import { Router} from "@angular/router";
import {Store} from "@ngrx/store";
// import {DeviceServices} from "../../services/devices.service";

@Component({
  selector: 'statistics-web',
  templateUrl: './statistics.component.html',
  styleUrls: ['./statistics.component.css'],
})
export class StatisticsWeb {

  public isSpinner=false;
  public isAddDeviceDialog:boolean=false;
  public allDeviceStatistics:any=[];
  public deviceStatistics:any=[];
  public startPageNo:number=1;
  public endPageNo:number=10;
  public totalPage:number=0;
  public selectedTableHeight:any='10';


  constructor(private _store:Store<any>) {
    this._store.dispatch({type: "TITLE",payload:'Statitics'}); // set the header name

    for(let i=1;i<=9;i++){
      this.allDeviceStatistics.push({'S.No':i});
    }
    this.totalPage=this.allDeviceStatistics.length;
    this.endPageNo=((Number(this.selectedTableHeight)<this.totalPage)?Number(this.selectedTableHeight):this.totalPage);

    for(let i=0;i<this.selectedTableHeight && i<this.totalPage;i++){
      this.deviceStatistics.push(this.allDeviceStatistics[i]);
    }

    this.isSpinner=false;
    // this.deviceServices.devicesGetWithHttpInfo().subscribe(res =>{
    //   this.isSpinner=false;
    //   console.log(res);
    // },err=>{
    //   this.isSpinner=false;
    //   console.log(err.status);
    // });
  }

  addDevice(){
    this.isAddDeviceDialog=true;
  }

  selectTableHeight(){
    this.deviceStatistics=[];
    this.startPageNo=1;
    // this.endPageNo=Number(this.selectedTableHeight);
    this.endPageNo=((Number(this.selectedTableHeight)<this.totalPage)?Number(this.selectedTableHeight):this.totalPage);

    for(let i=0;i<this.selectedTableHeight && i<this.totalPage;i++){
      this.deviceStatistics.push(this.allDeviceStatistics[i]);
    }
  }

  prevPage(){
    if(this.startPageNo>=1) {
      this.deviceStatistics=[];
      this.endPageNo=this.startPageNo-1;
      let pageLimit=this.endPageNo-this.startPageNo;
      this.startPageNo=this.startPageNo-Number(this.selectedTableHeight);

      for(let i=this.startPageNo-1;i<this.endPageNo;i++){
        this.deviceStatistics.push(this.allDeviceStatistics[i]);
      }
    }
  }

  nextPage(){
    if(this.endPageNo<this.totalPage) {
      this.deviceStatistics=[];
      this.startPageNo = (this.startPageNo + Number(this.selectedTableHeight));
      let pageLimit=this.totalPage-this.endPageNo;
      this.endPageNo=this.endPageNo+(pageLimit<Number(this.selectedTableHeight)?pageLimit:Number(this.selectedTableHeight));

      for(let i=this.startPageNo-1;i<this.endPageNo;i++){
        this.deviceStatistics.push(this.allDeviceStatistics[i]);
      }
    }
  }

}




