import { Component, OnInit } from '@angular/core';
import { Angulartics2Mixpanel } from 'angulartics2/dist';

@Component({
  selector: 'app-trip-status',
  templateUrl: './trip.component.html',
  styleUrls: ['./trip.component.css']
})
export class Trip {

  public view ='';
  constructor(public _angularitics:Angulartics2Mixpanel) {

    this.view=document['view'];
    console.log(this.view);
  }

  ngOnInit()
  {
    this._angularitics.eventTrack('TripView_Opened', { category: 'TripDashboard'});
  }

}



