import { Component, OnInit } from '@angular/core';
import {MdSnackBar} from "@angular/material";
import {Store} from "@ngrx/store";
// import {Services} from "../../services/services";
import {ActivatedRoute, Router} from "@angular/router";
import {go} from "@ngrx/router-store";
import {LocalStorageService} from "angular-2-local-storage";
import {TripDetailSocketService} from "../../../services/trip.socket.service";
import {Services} from "../../../services/services";
import {TripServices} from "../../../services/tripServices";
import { Angulartics2Mixpanel } from 'angulartics2/dist';
import { TripTemplateServices } from "../../../services/tripTemplate-services";


@Component({
  selector: 'app-vehicle-trip-status-details',
  templateUrl: './vehicle-trip-status-details.component.html',
  styleUrls: ['./vehicle-trip-status-details.component.css']
})
export class VehicleTripStatusDetails implements OnInit {
  public view ='';

  public vehicleNo:any;
  public isSpinner:boolean = false;
  public vehicleState:any;
  public isExpand:boolean = false;
  public isDisconnectDialog:boolean = false;
  public isStartTimeDisable:boolean = false;
  public isEditTripLocationDialog:boolean = false;
  public movementLogs:any = {};
  public tripView = "list";
  public forConfirmPlace = "";
  public validationErrorMgs = "";
  public upSideHeight = '0px';
  public unSub_vehicleTripDetails = null;
  public vehicleTripDetails : Object ={};
  public vehicleTripDetailsClone : Object ={};
  public selectedConfirmLocation : Object ={};
  public startTime =null;
  public tillTime =null;
  public forEditPlace ='';
  public unSub_customerDetails =null;
  public unSub_templateDetails =null;
  public unSub_templateList =null;

  public unSub_closeDialog =null;
  public distanceToGo : any;
  public eta : any;
  public timeValue : any;
  public isDelayed = false;
  public delayedBy : any;
  public isCreateTripDialog:boolean=false;
  public templateObj:any={};

  public latLngObj=null;
  public orgId;


  ngAfterViewChecked(){
   let ref=document.getElementById("expandable-box");
    if(ref !=null) {
      let mapOffset = ref.getBoundingClientRect();
      // console.log(mapOffset);
      this.upSideHeight = ((0 - (mapOffset.height + 150)) + 'px');
      // console.log(this.upSideHeight)
    };
  }

  ngOnInit() {

  }

  ngOnDestroy() {

    this.tripDetailSocketService.disconnectTripSocket();
    this._store.dispatch({type: "IS_BACK",payload:{isBack:false,backToComponent:''}});

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined){
      this.unSub_customerDetails.unsubscribe();
    }

    if (typeof this.unSub_vehicleTripDetails != 'undefined' && this.unSub_vehicleTripDetails != null && this.unSub_vehicleTripDetails != undefined)
      this.unSub_vehicleTripDetails.unsubscribe();
    if (typeof this.unSub_closeDialog != 'undefined' && this.unSub_closeDialog != null && this.unSub_closeDialog != undefined)
      this.unSub_closeDialog.unsubscribe();
    if (typeof this.unSub_templateDetails != 'undefined' && this.unSub_templateDetails != null && this.unSub_templateDetails != undefined)
      this.unSub_templateDetails.unsubscribe();
    if (typeof this.unSub_templateList != 'undefined' && this.unSub_templateList != null && this.unSub_templateList != undefined)
      this.unSub_templateList.unsubscribe();
  }

  /******************************constructor start here ************************* */
  constructor(private _router:Router,private tripDetailSocketService:TripDetailSocketService, private localStorageService:LocalStorageService,public snackBar: MdSnackBar,private _store:Store<any>,private _services:Services,private route: ActivatedRoute,private tripServices:TripServices,private templateService:TripTemplateServices,public _angularitics:Angulartics2Mixpanel) {

    this.view =document['view'];
    this.orgId=localStorageService.get("ORGID");
    console.log(this.orgId);

    if(this._router.url =='/vtsdashboard/trip/status/details' && this.view =='web'){
      this._store.dispatch(go(['vtsdashboard/trip/status']));
      return;
    }

    this._store.dispatch({type: "TITLE",payload:''}); // set the header name
    _store.dispatch({type: "IS_BACK",payload:{isBack:true,backToComponent:"trip/status"}});
    // _store.select('currentVehicle').subscribe(value => {
    let currentTripVehicle:any=this.localStorageService.get("CURRENT_TRIP_VEHICLE");
    if(currentTripVehicle == undefined || currentTripVehicle == null ){
      this._store.dispatch(go(['']));
      return;
    }

    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      console.log(value);
      if(value==null){
        return;
      }

      tripDetailSocketService.joinUpdates(currentTripVehicle.uuid);
      console.log(currentTripVehicle);
      this.isSpinner =true;
    });

    this.vehicleNo = ""+currentTripVehicle.vehicleRegistrationNumber;

    if(currentTripVehicle['distanceToGo']!=null) {
    this.distanceToGo = currentTripVehicle['distanceToGo']+" remaining";
   }
    this.eta = currentTripVehicle['eta'];
    this.timeValue = currentTripVehicle['timeValue']*1000;
    // this.tripServices.getTripDetails.emit({vehicleID:""+currentTripVehicle.uuid}); // call trip details services

    this.unSub_templateList = this._store.select('tripTemplateList').subscribe(value => {
      console.log(value);

      if (value == null) {
        return;
      }

      let obj :any=[];
      obj = value['data'];
      // console.log(obj);
      if (value['error'] == "") {

        if(obj['status']== 200)
        {
          this.templateObj=obj['list'][0];
          console.log(this.templateObj);
        }
        // console.log(obj);
      }
    });
  this.unSub_templateDetails = _store.select('tripTemplateDetail').subscribe(value => {
    console.log(value);
    if(value==null){
      return;
    }
    if(value['error'] == ''){
        // console.log(value['data']['data']);
        this.templateObj=JSON.parse(value['data']['data']);

      }
  });
    //********************** get trip details data *****************
    this.unSub_vehicleTripDetails = _store.select('vehicleTripDetails').subscribe(value => {
      if(value == null){
        return;
      }

      // if(value['error'] == 'X'){
      //   console.log(value['errorMsg']);
      //   return;
      // }


      this.isSpinner = false;
      this.vehicleTripDetails = JSON.parse(JSON.stringify(value)); // create new object
      this.vehicleTripDetailsClone = JSON.parse(JSON.stringify(value)); // create new object

      if (this.vehicleTripDetails['latestGpsState'] != null) {
        let currentTime = this.vehicleTripDetails['latestGpsState']['endTime'];

        if (this.vehicleTripDetails['currentTrip'] != null) {
          if (this.vehicleTripDetails['currentTrip']['plannedEndTime'] != null) {
            if(this.timeValue!=null) {
            let transitTime = this.vehicleTripDetails['currentTrip']['plannedEndTime'] - currentTime;

            if (transitTime < 0) {
              this.isDelayed = true;
              console.log(transitTime);
              console.log(this.timeValue)
              this.delayedBy = this.msToTime(this.timeValue + Math.abs(transitTime));
              console.log(this.msToTime(this.timeValue + Math.abs(transitTime)));
            }

            else if (transitTime > 0) {
              let actualTime = this.timeValue - transitTime;
              if (actualTime > 0) {
                this.isDelayed = true;
                this.delayedBy = this.msToTime(this.timeValue-Math.abs(transitTime));
              }

              else {
                this.isDelayed = false;
              }

            }
            else if (transitTime == 0) {
              this.isDelayed = false;
            }
          }
          }
        }
      }
      else {
        this.delayedBy = '(N/A)'
      }

      if(this.vehicleTripDetails['vts']!= null) {
        this.vehicleTripDetails['vts']['_stateDuration'] = this.msToTime(this.vehicleTripDetails['vts']['endTime'] - this.vehicleTripDetails['vts']['startTime']);


        if (this.vehicleTripDetails['lastTrip'] != null) {
          this.vehicleTripDetails['lastTrip'].destination[0]['_unloadingDuration'] = this.msToTime(this.vehicleTripDetails['lastTrip'].destination[0]['tillTime'] - this.vehicleTripDetails['lastTrip'].destination[0]['fromTime']);
          this.vehicleTripDetails['lastTrip'].origin[0]['_loadingDuration'] = this.msToTime(this.vehicleTripDetails['lastTrip'].origin[0]['tillTime'] - this.vehicleTripDetails['lastTrip'].origin[0]['fromTime']);
          this.vehicleTripDetails['lastTrip']['_tripCompletionDuration'] = this.msToTime(this.vehicleTripDetails['lastTrip']['actualEndTime'] - this.vehicleTripDetails['lastTrip']['actualStartTime']);
        }

        if (this.vehicleTripDetails['currentTrip'] != null) {
          this.vehicleTripDetails['currentTrip']['_transiteTime'] = this.msToTime(this.vehicleTripDetails['currentTrip']['plannedEndTime'] - this.vehicleTripDetails['currentTrip']['plannedStartTime']);
        }

        if (this.vehicleTripDetails['vts']['state'] == 'WaitingForLoad') {
          this.vehicleState = 'Waiting For Load';
          /*****new code */
          let latitude;
          let longitude;
          if (this.vehicleTripDetails['lastTrip'] == null) {
              if (this.vehicleTripDetails['latestGpsState'] != null) {
                latitude=this.vehicleTripDetails['latestGpsState']['endLocation']['latitude'];
                longitude=this.vehicleTripDetails['latestGpsState']['endLocation']['longitude'];
                this.latLngObj={"latitude":latitude,"longitude":longitude};
                this.templateObj=null;
                this.templateService.tripTemplateDetails.emit(this.latLngObj);
                console.log("WFL "+this.latLngObj);

              }
              else{

                this.templateService.tripTemplateStatus.emit(this.orgId);
                console.log("WFL: LGS=null");

              }
          }
          else{
            latitude=this.vehicleTripDetails['lastTrip']['destination'][0]['freightLocation']['geofence']['center']['latitude'];
            longitude=this.vehicleTripDetails['lastTrip']['destination'][0]['freightLocation']['geofence']['center']['longitude'];
            this.latLngObj={"latitude":latitude,"longitude":longitude};
            this.templateObj=null;
            this.templateService.tripTemplateDetails.emit(this.latLngObj);
          }
          console.log(this.latLngObj);
          /********** */
        }
        else if (this.vehicleTripDetails['vts']['state'] == 'EnrouteForDestination') {
          this.vehicleState = 'Enroute for Delivery';
          /*****new code */
          let latitude;
          let longitude;
          if (this.vehicleTripDetails['nextTrip'] == null) {
            latitude=this.vehicleTripDetails['currentTrip']['destination'][0]['freightLocation']['geofence']['center']['latitude'];
            longitude=this.vehicleTripDetails['currentTrip']['destination'][0]['freightLocation']['geofence']['center']['longitude'];
            this.latLngObj={"latitude":latitude,"longitude":longitude};
            this.templateObj=null;
            this.templateService.tripTemplateDetails.emit(this.latLngObj);
            console.log("EFD "+this.latLngObj);
          }
          console.log(this.latLngObj);
          /********** */

          if (this.vehicleTripDetails['currentTrip'] != null) {
            this.vehicleTripDetails['currentTrip'].origin[0]['_loadingDuration'] = this.msToTime(this.vehicleTripDetails['currentTrip'].origin[0]['tillTime'] - this.vehicleTripDetails['currentTrip'].origin[0]['fromTime']);
          }

          console.log(this.vehicleTripDetails['movementLegs']);
          if (this.vehicleTripDetails['movementLegs'] != null) {
            let totalTravlingDistance = 0;
            let totalTravlingTime = 0;
            let stopCount = 0;
            let disconnectedCount = 0;
            for (let i = 0; i < this.vehicleTripDetails['movementLegs'].length; i++) {
              totalTravlingDistance = totalTravlingDistance + this.vehicleTripDetails['movementLegs'][i]['totalDistance'];
              totalTravlingTime = totalTravlingTime + this.vehicleTripDetails['movementLegs'][i]['totalTime'];
              this.vehicleTripDetails['movementLegs'][i]['_totalDuration'] = this.msToTime(this.vehicleTripDetails['movementLegs'][i]['totalTime'])

              if (this.vehicleTripDetails['movementLegs'][i]['state'] == 'Stopped') { // count total stopage
                stopCount++;
              }
              if (this.vehicleTripDetails['movementLegs'][i]['state'] == 'Disconnected') { // count total disconnection
                disconnectedCount++;
              }
            }
            this.movementLogs.totalTravlingDistance = totalTravlingDistance;
            this.movementLogs._totalTravlingDistance = totalTravlingDistance / 1000;
            this.movementLogs.totalTravlingTime = totalTravlingTime;
            this.movementLogs._totalTravlingTime = this.msToTime(totalTravlingTime);
            this.movementLogs.disconnected = disconnectedCount;
            this.movementLogs.stopped = stopCount;

            console.log(this.movementLogs);
          }
        }
        else if (this.vehicleTripDetails['vts']['state'] == 'EnrouteForPickUp') {
          this.vehicleState = 'Enroute for Pickup';
        }
        else if (this.vehicleTripDetails['vts']['state'] == 'AtPickUpPoint') {
          this.vehicleState = 'At Pickup Point';
        }
        else if (this.vehicleTripDetails['vts']['state'] == 'AtDestination') {
          this.vehicleState = 'At Delivery Point';
          /*****new code */
          let latitude;
          let longitude;
          if (this.vehicleTripDetails['nextTrip'] == null) {
            latitude=this.vehicleTripDetails['currentTrip']['destination'][0]['freightLocation']['geofence']['center']['latitude'];
            longitude=this.vehicleTripDetails['currentTrip']['destination'][0]['freightLocation']['geofence']['center']['longitude'];
            this.latLngObj={"latitude":latitude,"longitude":longitude};
            this.templateObj=null;
            this.templateService.tripTemplateDetails.emit(this.latLngObj);
            console.log("ADP "+this.latLngObj);

          }
          console.log(this.latLngObj);
          /********** */
          if (this.vehicleTripDetails['currentTrip'] != null) {
            this.vehicleTripDetails['currentTrip'].origin[0]['_loadingDuration'] = this.msToTime(this.vehicleTripDetails['currentTrip'].origin[0]['tillTime'] - this.vehicleTripDetails['currentTrip'].origin[0]['fromTime']);
          }

          if (this.vehicleTripDetails['movementLegs'] != null) {
            let totalTravlingDistance = 0;
            let totalTravlingTime = 0;
            let stopCount = 0;
            let disconnectedCount = 0;
            for (let i = 0; i < this.vehicleTripDetails['movementLegs'].length; i++) {
              totalTravlingDistance = totalTravlingDistance + this.vehicleTripDetails['movementLegs'][i]['totalDistance'];
              totalTravlingTime = totalTravlingTime + this.vehicleTripDetails['movementLegs'][i]['totalTime'];
              this.vehicleTripDetails['movementLegs'][i]['_totalDuration'] = this.msToTime(this.vehicleTripDetails['movementLegs'][i]['totalTime'])

              if (this.vehicleTripDetails['movementLegs'][i]['state'] == 'Stopped') { // count total stopage
                stopCount++;
              }
              if (this.vehicleTripDetails['movementLegs'][i]['state'] == 'Disconnected') { // count total disconnection
                disconnectedCount++;
              }
            }
            this.movementLogs.totalTravlingDistance = totalTravlingDistance;
            this.movementLogs._totalTravlingDistance = totalTravlingDistance / 1000;
            this.movementLogs.totalTravlingTime = totalTravlingTime;
            this.movementLogs._totalTravlingTime = this.msToTime(totalTravlingTime);
            this.movementLogs.disconnected = disconnectedCount;
            this.movementLogs.stopped = stopCount;
            console.log(this.movementLogs);
          }
        }
        console.log(this.vehicleTripDetails);
      }
    });



    this.unSub_closeDialog = _store.select('closeDialog').subscribe(value => {
      console.log(value);
      this.isEditTripLocationDialog = value['isClose'];
    });


      // console.log(currentTripVehicle);
  } // constructor close

  // back(){
  //   this._store.dispatch(go(['dashboard/trip/status']));
  // }

  goToEditTripPickupView(forEditPlace,tripName){
    let obj = {'vehicleTripDetails':this.vehicleTripDetailsClone,'forEditPlace':forEditPlace,'tripName':tripName};
    this.localStorageService.set("VEHICLE-TRIP-DETAIL-OBJ",obj);

    this._angularitics.eventTrack('Edit_Trip_Location', obj);


    if(this.view == 'web'){
      this.forEditPlace  =forEditPlace;
    this.isEditTripLocationDialog =true;
    }else{
      this._store.dispatch(go(['/vtsdashboard/trip/status/details/editTripLocation']));
    }

  }

  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration='';

    if(days>0){
      totalDuration=days+"d ";
    }
    if(hours>0){
      totalDuration=totalDuration+hours+"hr ";
    }

    if(days >= 1){
      return totalDuration;
    }

    if(minutes>0){
      return totalDuration=totalDuration+minutes+"min";
    }

    if(seconds>0 && totalDuration == ''){
      totalDuration=totalDuration+seconds+" sec";
    }

    if(totalDuration==''){
      return '0 sec';
    }else {
      return totalDuration;
    }

  }//close methods msToTime

  openConfirmPointDialog(forConfirmPlace){
    this.forConfirmPlace = forConfirmPlace;
    this.validationErrorMgs = "";
    console.log(forConfirmPlace);
    this.isDisconnectDialog = true;
    console.log( this.vehicleTripDetails);

    if(this.vehicleTripDetails['vts']['state'] == 'EnrouteForDestination' || this.vehicleTripDetails['vts']['state'] == 'AtDestination'){
        // this.selectedConfirmLocation = this.vehicleTripDetails ['currentTrip']['destination'][0];
      this.startTime = this.vehicleTripDetails ['currentTrip']['destination'][0]['fromTime'];
      this.tillTime = this.vehicleTripDetails ['currentTrip']['destination'][0]['tillTime'];
    } else if(this.vehicleTripDetails['vts']['state'] == 'EnrouteForPickUp' || this.vehicleTripDetails['vts']['state'] == 'AtPickUpPoint' ){
      // this.selectedConfirmLocation = this.vehicleTripDetails ['currentTrip']['origin'][0];
      this.startTime = this.vehicleTripDetails ['currentTrip']['origin'][0]['fromTime'];
      this.tillTime = this.vehicleTripDetails ['currentTrip']['origin'][0]['tillTime'];
    }

    if(this.vehicleTripDetails['vts']['state'] == 'EnrouteForDestination' || this.vehicleTripDetails['vts']['state'] == 'EnrouteForPickUp' ){
      this.isStartTimeDisable = false;
      this.startTime = null;
      this.tillTime = null;
    }
    else if(this.vehicleTripDetails['vts']['state'] == 'AtDestination' || this.vehicleTripDetails['vts']['state'] == 'AtPickUpPoint' ){
      this.isStartTimeDisable = true;
    }
  }

  confirmPlace(){
   if(this.checkValid()) {
     let eTime = null;
     if (this.tillTime != null) {
       eTime = new Date(this.tillTime).getTime();

       //@todo more set validation
       if (eTime < new Date(this.startTime).getTime()) {
         // console.log("start Time should not be greater then end Time");
         this.validationErrorMgs = "start Time should not be greater then end Time";
         return;
       }

       let todayDate = new Date().getTime();
       if (eTime > todayDate) {
         // console.log("start Time should not be greater then end Time");
         this.validationErrorMgs = "End time should not be greater then current date";
         return;
       }
     }

     this.isDisconnectDialog = false;
     let obj = {forConfirmPlace:this.forConfirmPlace,
       conObj: {
         vehicleId: this.vehicleTripDetails['vts']['vehicleId'],
         tripId: this.vehicleTripDetails['vts']['tripId'],
         startTime: new Date(this.startTime).getTime(),
         endTime: eTime
       }};

     console.log(obj);
     this.tripServices.confirmLocation.emit(obj);// after call it page data automatically  update by socket services.
   }
  }
  confirmCreateTripFromTemplate()
  {
    let tripPlannedStartTime;
    if(this.vehicleTripDetails['vts']['state'] == 'WaitingForLoad'){
      if (this.vehicleTripDetails['lastTrip'] == null) {
        tripPlannedStartTime=new Date().getTime();
      }
      else{
        tripPlannedStartTime=this.vehicleTripDetails['lastTrip']['actualEndTime'];
      }
    }

    if(this.vehicleTripDetails['vts']['state'] == 'AtDestination')
    {
      tripPlannedStartTime=this.vehicleTripDetails['currentTrip']['plannedEndTime'];
    }
    if(this.vehicleTripDetails['vts']['state'] == 'EnrouteForDestination')
    {
      tripPlannedStartTime=this.vehicleTripDetails['currentTrip']['plannedEndTime'];
    }
    let originList = [];
    let origin = {
      "freightLocation":{"name":this.templateObj['origin']['name'],
                        "address":this.templateObj['origin']['address'],
                        "geofence":{"boundry":this.templateObj['origin']['geofence']['boundry'],
                                    "center":this.templateObj['origin']['geofence']['center']
                                  },
                        "material":[""]
                      },
      "fromTime": null,
      "tillTime": null
    }

    originList.push(origin);

    let destinList = [];
    let destination = {
      "freightLocation":{"name":this.templateObj['destination']['name'],
      "address":this.templateObj['destination']['address'],
      "geofence":{"boundry":this.templateObj['destination']['geofence']['boundry'],
      "center":this.templateObj['destination']['geofence']['center']
    },
      "material":[""]
    },
      "fromTime": null,
      "tillTime": null
    }
    destinList.push(destination);

    let tripData = {
      "vehicleId": this.vehicleTripDetails['vts']['vehicleId'],
      "origin": originList,
      "destination": destinList,
     "material": this.templateObj['material'],
      "tripId": null,
      "actualStartTime": null,
      "actualEndTime": null,
      "plannedStartTime": tripPlannedStartTime,
      "plannedEndTime": (tripPlannedStartTime+ this.templateObj['transitDays'] * 86400000),
      "lane": null,
      "tripState": "Created",
      "driverMobNumber":987654321,
      "driverName": "xyz"
    };

    console.log(tripData);
    this.tripServices.createTrip.emit(tripData);
    this.isCreateTripDialog=false;

  }

  checkValid(){
    if(this.isStartTimeDisable){
      if(this.startTime == null ){
        this.validationErrorMgs = "Start Time should not be blank";
        return false;
      }
      if(this.tillTime == null){
        this.validationErrorMgs = "End Time should not be blank";
        return false;
      }

      let todayDate = new Date().getTime();
      if (new Date(this.startTime).getTime() > todayDate) {
        this.validationErrorMgs = "start time should not be greater then current date";
        return false;
      }

    }else{
      if(this.startTime == null ){
        this.validationErrorMgs = "start time should not be blank";
        return false;
      }

      let todayDate = new Date().getTime();
      if (new Date(this.startTime).getTime() > todayDate) {
        this.validationErrorMgs = "start time should not be greater then current date";
        return false;
      }
    }
    return true;
  }// close ckeck valid

}



