import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {Dashboard} from "../components/dashboard/dashboard.component";
import {PieChartComponent} from "../components/pie-chart/pie-chart.component";

import {OsmMapComponent} from "../components/osm-map/osm-map.component";
import {StatisticsWeb} from "../components/statistics/statistics.component";
import {StatisticsMobileComponent} from "../components/statistics-mobile/statistics-mobile.component";
import {SendOTP} from "../components/send-otp/send-otp.component";

import {StoppageReport} from "../components/stoppage-report/stoppage-report.component";
import {OverspeedReport} from "../components/overspeed-report/overspeed-report.component";
import {RunningReport} from "../components/running-report/running-report.component";

import {UserLogin} from "../components/user-login/user-login.component";
import {LocHisVehicleList} from "../components/location-history-vehicle-list/location-history-vehicle-list.component";
import {SettingComponent} from "../components/setting/setting.component";
import {AddTrip} from "../components/add-trip/add-trip.component";

import {SelectedVehicleStatus} from "../components/selected-vehicle-status/selected-vehicle-status.component";
import {VehicleActivity} from "../components/vehicle-activity/vehicle-activity.component";
import {UserSignup} from "../components/user-signup/user-signup.component";
import {VehicleSharing} from "../components/vehicle-sharing/vehicle-sharing.component";
import {Angulartics2Module, Angulartics2Mixpanel} from "angulartics2";

import {GpsDisconnectedComponent} from "../components/gps-disconnected/gps-disconnected.component";
import {AddShipperLocationComponent} from "../components/add-shipper-location/add-shipper-location.component";

import {AddTripLocationComponent} from "../components/add-trip-location/add-trip-location.component";
import {VehicleTimelineReport} from "../components/vehicle-timeline-report/vehicle-timeline-report.component";
import {SelectVehicleTimelineReport} from "../components/select-vehicle-timeline-report/select-vehicle-timeline-report.component";
import {ScheduleList} from "../components/report-schedule/schedule-list/schedule-list.component";
import {CreateSchedule} from "../components/report-schedule/create-schedule/create-schedule.component";
import {Trip} from "../components/trip/trip.component";
import {VehicleStatus} from "../components/trip/status/vehicle-status/vehicle-status.component";
import {VehicleTripStatusDetails} from "../components/trip/vehicle-trip-status-details/vehicle-trip-status-details.component";
import {EditTripLocation} from "../components/trip/edit-trip-location/edit-trip-location.component";
import {MapsComponent} from "../components/vehicle-dashboard/maps/maps.component";
import {LocationHistory} from "../components/vehicle-dashboard/location-history/location-history.component";
import {VehicleList} from "../components/vehicle-dashboard/vehicle-list/vehicle-list.component";
import {VehicleDashboardComponent} from "../components/vehicle-dashboard/vehicle-dashboard.component";
import {VehicleDashboardWebComponent} from "../components/vehicle-dashboard/vehicle-dashboard-web/vehicle-dashboard-web.component";
import {VehicleDashboardMobileComponent} from "../components/vehicle-dashboard/vehicle-dashboard-mobile/vehicle-dashboard-mobile.component";
import {ConfigureAlertsComponent} from "app/components/configure-alerts/configure-alerts.component";
import {AddAlertsComponent} from "app/components/add-alerts/add-alerts.component";
import {UserManagementConsoleComponent} from "../components/user-management-console/user-management-console.component";
import {UsermanagementWebViewComponent} from "../components/user-management-console/usermanagement-web-view/usermanagement-web-view.component";
import {UsermanagementMobileViewComponent} from "../components/user-management-console/usermanagement-mobile-view/usermanagement-mobile-view.component";
import {ManageOrganisationComponent} from "../components/manage-organisation/manage-organisation.component";
import {VehicleManagementComponent} from "../components/vehicle-management/vehicle-management.component";
import {EditVehiclesComponent} from "../components/vehicle-management/edit-vehicles/edit-vehicles.component";
import {GroupedVehiclesListComponent} from "../components/vehicle-management/grouped-vehicles-list/grouped-vehicles-list.component";

import {AddTripTemplateComponent} from "../components/trip-template/add-trip-template/add-trip-template.component";
import {TripTemplateStatusComponent} from "../components/trip-template/trip-template-status/trip-template-status.component";
import {EditTripTemplateComponent} from "../components/trip-template/edit-trip-template/edit-trip-template.component";
import {AddNewOrganisationComponent} from "../components/add-new-organisation/add-new-organisation.component";

import { FreightLocationCreaterComponent } from "../components/freight-location-components/freight-location-creater/freight-location-creater.component";
import { FreightLocationPickerComponent } from "../components/freight-location-components/freight-location-picker/freight-location-picker.component";
import {AlertsNotificationComponent} from "../components/alerts-notification/alerts-notification.component";
const routes: Routes = [
  // {path: '', redirectTo: 'userLogin', pathMatch: 'full'},
  // {path: 'login', component: Login},
  {path: 'sendOTP', component: SendOTP},
  {path: 'userLogin', component: UserLogin},
  {path: 'userSignup', component: UserSignup}
  // {path: 'dashboard', component: Dashboard},
];

// const childRoutesDesktopView: Routes = [{path: 'dashboardDesktop', component: DashboardDesktop,
//   children: [
//     {path: 'vehicleList', component: VehicleList},
//     {path: 'tripStatus', component: TripStatus},
//     {path: 'settingComponent', component: SettingComponent},
//     {path: 'scheduleList', component: ScheduleList},
//   ]
// }];

const childRoutes: Routes = [
  {path: '', redirectTo: 'vtsdashboard', pathMatch: 'full'},
  {
    path: 'vtsdashboard', component: Dashboard,
    children: [
      // {path: '', redirectTo: 'vehicleList', pathMatch: 'full'},
      {path: 'vehicleList', component: VehicleList},
      {path: 'trip/status', component: Trip},
      {path: 'pieChart', component: PieChartComponent},
      {path: 'mapsComponents', component: MapsComponent},
      {path: 'openStreetsMaps', component: OsmMapComponent},
      {path: 'statisticsWeb', component: StatisticsWeb},
      {path: 'statisticsMobile', component: StatisticsMobileComponent},
      {path: 'stoppageReport', component: StoppageReport},
      {path: 'overspeedReport', component: OverspeedReport},
      {path: 'runningReport', component: RunningReport},
      {path: 'locationHistory', component: LocationHistory},
      {path: 'locHisVehicleList', component: LocHisVehicleList},
      {path: 'settingComponent', component: SettingComponent},
      {path: 'addTrip', component: AddTrip},
      {path: 'addTripTemplate', component: AddTripTemplateComponent},
      {path: 'tripTemplateStatus', component: TripTemplateStatusComponent},
      {path: 'editTripTemplate', component: EditTripTemplateComponent},



      // {path: 'vehicleStatus', component: VehicleStatus},
      {path: 'selectedVehicle', component: SelectedVehicleStatus},
      {path: 'vehicleActivity', component: VehicleActivity},
      {path: 'vehicleSharing', component: VehicleSharing},
      {path: 'trip/status/details', component: VehicleTripStatusDetails},
      {path: 'gpsDisconnected', component: GpsDisconnectedComponent},
      {path: 'addShipperLocation', component: AddShipperLocationComponent},
      {path: 'trip/status/details/editTripLocation', component: EditTripLocation},
      {path: 'addTripLocation', component: AddTripLocationComponent},
      {path: 'selectVehicleTimelineReport', component: SelectVehicleTimelineReport},
      {path: 'vehicleTimelineReport', component: VehicleTimelineReport},
      {path: 'scheduleList', component: ScheduleList},
      {path: 'createSchedule', component: CreateSchedule},
      {path: 'configureAlerts', component: ConfigureAlertsComponent},
      {path: 'addAlerts', component: AddAlertsComponent},

      {path: 'vehicleDashboard', component: VehicleDashboardComponent},
      {path: 'vehicleDashboardMobile', component: VehicleDashboardMobileComponent},
      {path: 'vehicleDashboardWeb', component: VehicleDashboardWebComponent},

      {path: 'userManagementConsole', component: UserManagementConsoleComponent},
      {path: 'userManagementConsoleWeb', component: UsermanagementWebViewComponent},
      {path: 'userManagementConsoleMobile', component: UsermanagementMobileViewComponent},

      {path: 'manageOrganisation', component: ManageOrganisationComponent},
      {path: 'manageVehicles', component: VehicleManagementComponent},
      {path: 'editVehicles', component: EditVehiclesComponent},
      {path: 'groupedVehiclesList', component: GroupedVehiclesListComponent},
      {path: 'addNewOrganisation', component: AddNewOrganisationComponent},
      {path: 'freightLocationPicker', component:FreightLocationPickerComponent},
      {path: 'alertsNotification', component:AlertsNotificationComponent},
      // {path: 'freightLocationCreater', component:FreightLocationCreaterComponent},

    ]
  }];

@NgModule({
  imports: [RouterModule.forRoot(routes), RouterModule.forChild(childRoutes), Angulartics2Module.forRoot([Angulartics2Mixpanel])],
  exports: [RouterModule]
})
export class AppRoutingModule {
  constructor(public angularitics: Angulartics2Module) {
  }
}



