import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {Store} from "@ngrx/store";
import {Services} from "../../services/services";
import {go} from "@ngrx/router-store";
import {LocalStorageService} from "angular-2-local-storage";

@Component({
  selector: 'app-location-history-vehicle-list',
  templateUrl: './location-history-vehicle-list.component.html',
  styleUrls: ['./location-history-vehicle-list.component.css']
})
export class LocHisVehicleList implements OnInit {
  public isSpinner=true;
  public  vehicles=[];
  public  search='';
  public vehicleList:any=[];
  public errorMsg='';
  public unSub_vehicleList=null;
  public unSub_customerDetails:any=null;

  ngOnDestroy() {
    if (typeof this.unSub_vehicleList != 'undefined' && this.unSub_vehicleList != null && this.unSub_vehicleList != undefined)
      this.unSub_vehicleList.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();
  }

  ngOnInit(){
  }

  constructor(private _store:Store<any>,public _services:Services,private localStorageService: LocalStorageService) {
    // this.vehicles=[{vehicleType:'truck 10 trola',vehNo:'DL123456'},
    //   {vehicleType:'mini-van',vehNo:'BL123456'},
    //   {vehicleType:'monster-truck',vehNo:'DL123456'},
    //   {vehicleType:'container',vehNo:'HL123456'},
    //   {vehicleType:'truck-van',vehNo:'ML123455'},
    //   {vehicleType:'pick-up',vehNo:'PL123456'},]


    let customer={};
    this._store.dispatch({type: "TITLE",payload:'Location History'});// set the header name
    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      if(value==null)
        return;

      if(value['error']==''){
        customer= value['customer'];
        console.log(customer);
        // _services.getVehicleList.emit(customer.customerId);
      }else{
        return;
      }

      // ***************  vehicle list ************************************
      this.unSub_vehicleList = _store.select('vehicleListRes').subscribe(value => {
        // console.log(value);
        this.errorMsg='';
        // check not  exist then request for list
        if(value==null){
          _services.getVehicleList.emit(customer['id']);
          return;
        }

        if(value['error']==''){
          let vehicleList = JSON.parse(JSON.stringify(value['data']));
          this.vehicleList= vehicleList.sort(this.compare);

          for(let i=0;i<this.vehicleList.length;i++){
            this.vehicleList[i]['select']=false;
          }

        }else {
          console.log(value['errorMsg']);
          this.errorMsg=value['errorMsg'];
        }
        this.isSpinner=false;
      });
    });


  } //constructor

  compare(a,b) {
    if (a.vehicleType.toUpperCase() < b.vehicleType.toUpperCase())
      return -1;
    if (a.vehicleType.toUpperCase() > b.vehicleType.toUpperCase())
      return 1;
    return 0;
  }

  goToLoactionHisView(vehicle){
    console.log(vehicle);
    this._store.dispatch({type: "LOCATION_HISTORY",payload:null}); // clear  the previous records
    this.localStorageService.set('SELECTED-LOCATION-HIS-VEHICLE',vehicle); // put value in local storage
    this._store.dispatch(go(['/vtsdashboard/locationHistory']));
  }

}



