import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import {Angulartics2Mixpanel} from "angulartics2";

@Component({
  selector: 'app-vehicle-dashboard-mobile',
  templateUrl: './vehicle-dashboard-mobile.component.html',
  styleUrls: ['./vehicle-dashboard-mobile.component.css']
})
export class VehicleDashboardMobileComponent implements OnInit {


  public vehicleCount = {all:0,stopped:0,disconnected:0,moving:0};
  public isSpinner=true;
  public errorMsg='';
  public search='';
  public isSearch = false;
  public vehicleList=[];
  public status='';

  public unSub_vehicleList=null;

  ngOnDestroy()
  {
    if (typeof this.unSub_vehicleList != 'undefined' && this.unSub_vehicleList != null && this.unSub_vehicleList != undefined){
      console.log("unsusVehicle list");
      this.unSub_vehicleList.unsubscribe();
    }
  }

  constructor(private _store:Store<any>,public _angularitics: Angulartics2Mixpanel) {
    // ***************  vehicle list ************************************
    this.unSub_vehicleList = _store.select('vehicleListRes').subscribe(value => {
      // console.log(value);
      this.errorMsg='';
      if(value==null)
      {
        return;
      }

      if(value['error']==''){
        console.log(value);
        this.vehicleCount = {all:0,stopped:0,disconnected:0,moving:0};
        this.vehicleList = value['data'];
        this.vehicleCount['all'] = this.vehicleList.length;
        for(let i=0;i<this.vehicleList.length; i++)
        {
          this.vehicleList[i]['_duration'] = this.msToTime(this.vehicleList[i]['duration'] );
          if(this.vehicleList[i]['state'] == 'Stopped'){
            this.vehicleCount['stopped'] = this.vehicleCount['stopped'] + 1;
          }

          if(this.vehicleList[i]['state'] == 'Disconnected'){
            this.vehicleCount['disconnected'] = this.vehicleCount['disconnected'] + 1;
          }

          if(this.vehicleList[i]['state'] == 'Moving'){
            this.vehicleCount['moving'] = this.vehicleCount['moving'] + 1;
          }
        }
        console.log(this.vehicleList);

        if(this.vehicleList.length==0){
          this.errorMsg="No records found."
        }

        // console.log(this.vehicleList);
      }else {
        // console.log(value['errorMsg']);
        this.errorMsg=value['errorMsg'];
      }

      this.isSpinner=false;
    });
  }

  ngOnInit() {
  }

  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration='';

    if(days>0){
      totalDuration=days+"d ";
    }
    if(hours>0){
      totalDuration=totalDuration+hours+"hr ";
    }

    if(days >= 1){
      return totalDuration;
    }

    if(minutes>0){
      return totalDuration=totalDuration+minutes+"min";
    }
    if(seconds>0 && totalDuration == ''){
      totalDuration=totalDuration+seconds+" sec";
    }

    if(totalDuration==''){
      return '0 sec';
    }else {
      return totalDuration;
    }

  }//close methods msToTime

  filterVehicleStatus()
  {
    console.log('desired vehicle status');
    console.log(this.status);
    // this._angularitics.eventTrack('VehicleList_Fliter_Mobile', this.status);
    this._angularitics.eventTrack('VehicleList_Fliter_Mobile', this.status);
    this._store.dispatch({type: "SELECTED_VEHICLE_FILTER_STATUS",payload:this.status});
  }

  gotoMapView(){
    this._angularitics.eventTrack('MapsView_Clicked', { category: 'DashboardMaps'});
    // this._router.navigate(['/vtsdashboard/openStreetsMaps']);
    // this._router.navigate(['/vtsdashboard/mapsComponents']);
    this._store.dispatch(go(['/vtsdashboard/mapsComponents']));
  }

  goToAlertsNotification()
  {
    this._store.dispatch(go(['/vtsdashboard/alertsNotification']));
  }

  searchVehicle()
  {
    console.log('search Term:');
    console.log(this.search);
    this._store.dispatch({type: "VEHICLELIST_SEARCH_FILTER",payload:this.search});
  }

}



