import { ActionReducer, Action } from '@ngrx/store';
import {ActionRecorderServices } from '../services/actionRecorder.service';

var obj=new ActionRecorderServices();

export function signupTokenValidation(state: any = null, action:Action) {

  switch (action.type) {
    case "SIGNUP-TOKEN-VALIDATION" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}

export function signUpOtpGenaration(state: any = null, action:Action) {

  switch (action.type) {
    case "SIGNUP_OTP_GENERATION" : {
      obj.addAction(action);
      return action.payload;
    }
    default:
      return state;
  }

}

export function signUpNewUserData(state: any = null, action:Action) {

  switch (action.type) {
    case "SIGNUP_NEW_USER" : {
      obj.addAction(action);
      return action.payload;
    }
    default:
      return state;
  }
}




