import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";
import {paths} from "../../environments/environment";
// import {QUICK_TRIP_CREATE_UPDATE_PATH } from "./path-services";
// import {TRIP_PATH,TRIP_CREATE_UPDATE_PATH,TRIP_STATUS_DETAILS_PATH,TRIP_DISCARD_PATH,TRIP_UPDATE_PATH,TRIP_UPDATE_FROM_STOP_PATH,TRIP_CONFIRM_LOCATION_PATH} from "./path-services";

@Injectable()
export class FreightLocationServices {
  // protected basePath = 'http://35.189.162.187:8080/command';
  public defaultHeaders: Headers = new Headers();
  public getTripStatus=new EventEmitter();
  public getTripDetails=new EventEmitter();
  public addTripFreightLocation= new EventEmitter();
  public createTrip= new EventEmitter();
  public createQuickTrip= new EventEmitter();
  public searchLocation= new EventEmitter();
  public discardTrip=new EventEmitter();
  public updateTrip=new EventEmitter();
  public updateTripFromStop=new EventEmitter();
  public confirmLocation=new EventEmitter();

  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>,private localStorageService: LocalStorageService){
    // this.defaultHeaders.append('Access-Control-Allow-Origin', '*');
    // this.defaultHeaders.append('Access-Control-Allow-Methods', 'GET,POST, OPTIONS, PUT, PATCH, DELETE');

    //@todo handle error cases
    this.addTripFreightLocation.subscribe((fretObj)=> {
      let path=paths.TRIP_CREATE_UPDATE_PATH+"/freight/create";
      console.log(path);
      this.addFretLocation(fretObj,path).subscribe(response =>{
        // console.log('addTripFreightLocation Response::==>')
        // console.log(response);
        this._store.dispatch({type: "ADD_FREIGHT_TRIP_DETAILS" ,payload:response});
      },err=>{
        console.log(err);
      });
    });

    //@todo handle error cases
    this.searchLocation.subscribe((name)=> {
      let path=paths.TRIP_CREATE_UPDATE_PATH+"/freight?name="+name;
      // console.log(path);
      this.searchLocationByName(path,name).subscribe(response =>{
        // console.log(response);
        this._store.dispatch({type: "SEARCH_LOCATION_NAME",payload:response});
      },err=>{
        console.log(err);
      });
    });



    //@todo handle error cases





  }// constructor close here


  // *********************** add fret Location service  **********************
  public addFretLocation(fretObj,path){
    // let path = TRIP_CREATE_UPDATE_PATH+"/fret/create";
    // let queryParameters = new URLSearchParams();
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');

    // console.log(headers);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: fretObj,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      // console.log(response);
      if(response.status==200) {


        return response.json();    //todo: Uncomment this code that returns the _id from request and comment the return obj

        // let fretId={
        //   "_id":"0001A"
        // }
        //
        // return fretId;
      }

    });
  }




  public searchLocationByName(path,name){

    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    // console.log(headers);
    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      console.log(response.status);
      console.log(response);
      if(response.status==200){
        let obj={"status":response.status,
                "data":response.json()
        }

      return obj;
      }else{
      //   console.log("not 200");
      //   return {error:'X'};
      }
    });
  }
}





