import {Component} from '@angular/core';
import {Store} from "@ngrx/store";
import 'rxjs/add/operator/startWith';
import 'rxjs/add/operator/map';
import {Services} from "../../services/services";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import {VehicleReportServices} from "../../services/vehicle-report-services";
import {LocalStorageService} from "angular-2-local-storage";
import {go} from "@ngrx/router-store";
import {IMyDrpOptions, IMyDateRangeModel} from "mydaterangepicker";
import {config} from "../../../environments/environment";
import { Angulartics2Mixpanel } from 'angulartics2/dist';
// import {MAX_RANGE_DAYS} from "../../services/config-services";

@Component({
  selector: 'app-select-vehicle-timeline-report',
  templateUrl: './select-vehicle-timeline-report.component.html',
  styleUrls: ['./select-vehicle-timeline-report.component.css']
})
export class SelectVehicleTimelineReport  {

  public view='';
  public isSpinner=false;
  public isAddVehicleDialog:boolean=false;
  // public isDisabled=true;
  public search='';
  public vehicleNo = 'Select vehicle here';
  public selectedVehicle:any={};
  public vehicleList:any=[];
  public errorMsg='';
  public totalDuration='';
  public totalDistance=0;

  public unSub_vehicleList=null;
  public unSub_customerDetails:any=null;

  // date=new Date();
  public fromDate=null;//: Object = { date: { year: this.date.getFullYear(), month: this.date.getMonth(), day: this.date.getDate() } };
  public toDate=null;//: Object = { date: { year: this.date.getFullYear(), month: this.date.getMonth(), day: this.date.getDate() } };


  currenDate = new Date();
  public myDateRangePickerOptions: IMyDrpOptions = {
    dateFormat: 'dd.mmm.yyyy',
    showClearDateRangeBtn: false,
    width:'100%',
    showApplyBtn: false,
    markCurrentDay : true,
    editableDateRangeField : false,
    openSelectorOnInputClick : true,
    indicateInvalidDateRange : true,
    disableSince : {year: this.currenDate.getFullYear(), month: this.currenDate.getMonth()+1, day: this.currenDate.getDate()+1}
  };

  ngOnDestroy() {
    if (typeof this.unSub_vehicleList != 'undefined' && this.unSub_vehicleList != null && this.unSub_vehicleList != undefined)
      this.unSub_vehicleList.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();
  }


  constructor(private localStorageService: LocalStorageService,private _store:Store<any>,private _services:Services,private vehReportServices:VehicleReportServices,public snackBar: MdSnackBar,public _angularitics:Angulartics2Mixpanel) {
    let customer={};
    this.view =document['view'];
    this._store.dispatch({type: "TITLE",payload:'Vehicle Timeline Report'}); // set the header name
    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      if(value==null)
        return;

      if(value['error']==''){
        customer= value['customer'];
        console.log(customer);
        // _services.getVehicleList.emit(id);
      }else{
        return;
      }
      // ***************  vehicle list ************************************
      this.unSub_vehicleList = _store.select('vehicleListRes').subscribe(value => {
        console.log(value);
        this.errorMsg='';
        // check not  exist then request for list
        if(value==null){
          _services.getVehicleList.emit(customer['id']);
          return;
        }

        if(value['error']==''){
          this.vehicleList = value['data'];
          console.log(this.vehicleList);

          for(let i=0;i<this.vehicleList.length;i++){
            this.vehicleList[i]['select']=false;
          }

        }else {
          console.log(value['errorMsg']);
          this.errorMsg=value['errorMsg'];
        }
      });

    });
  }

  onDateRangeChanged(event: IMyDateRangeModel) {
    if(((event.endEpoc+86400) - 1) - event.beginEpoc >(86400*config.MAX_RANGE_DAYS) ){
      this.openSnackBar("you can select max  15 day range");
      this.fromDate = null;
      this.toDate =null;
      return;
    }

    this.fromDate = event.beginEpoc*1000;
    this.toDate = (((event.endEpoc+86400) - 1)*1000);
  }


  AddVehicle(){
    console.log(this.fromDate);
    console.log(this.toDate);

    if(this.fromDate == null || this.toDate == null || this.fromDate ==0 || this.toDate == 86399000){
      this.openSnackBar("Please select valid date");
      return;
    }

    if(this.toDate - this.fromDate >(86400000*config.MAX_RANGE_DAYS) ){
      this.openSnackBar("you can select max  15 day range !!");
      return;
    }

    if(this.selectedVehicle['uuid']  == '' || this.selectedVehicle['uuid'] == null){ // in this we show vehicle no but internally send uuid to server
      this.openSnackBar("vehicle no should not be empty");
      return;
    }

    let obj={
      startTime:this.fromDate,
      endTime: this.toDate,
      selectedVehicle:this.selectedVehicle
    };


    this._angularitics.eventTrack('Timeline_Report_Parameters_Set', obj);
    this.localStorageService.set('VEHICLE_TIMELINE_REQUEST_OBJ',obj);
    this._store.dispatch({type: "LOCATION_HISTORY",payload:null});
    this._store.dispatch(go(['/vtsdashboard/vehicleTimelineReport']));

    console.log(obj)

  }

  selectVehicle(vehicle){
    this.isAddVehicleDialog=false;
    this.vehicleNo = vehicle['vehicleRegistrationNumber'];
    this.selectedVehicle = vehicle;
  }



  openSnackBar(message: string) {
    this.snackBar.open(message, '', <MdSnackBarConfig>{duration : 4000});
  }
}



