import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {paths} from "../../environments/environment";
// import {PATH} from "./path-services";

@Injectable()
export class LoginServices {
  // protected basePath = 'http://35.189.162.187:8080/command';
  public defaultHeaders: Headers = new Headers();
  private token:any='';
  private unSub_token:any=null;
  public login=new EventEmitter();
  public sendOTP=new EventEmitter();

  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>){
    this.defaultHeaders.append('Access-Control-Allow-Origin', '*');
    this.defaultHeaders.append('Access-Control-Allow-Methods', 'GET,POST, OPTIONS, PUT, PATCH, DELETE');
    //************ send otp ***************************************************
    this.sendOTP.subscribe((obj)=> {
      // const path = paths.PATH+':8094/login?mobileNumber='+obj.mobileNo;// @todo uncomment before final merge to stop getting OTP onto console in response
      // const path = paths.PATH+':8094/login/forany?mobileNumber='+obj.mobileNo;//@todo comment before final merge to stop getting OTP onto console in response
      const path = paths.SEND_OTP_PATH+obj.mobileNo;
      this.sendOtp(path).subscribe(response =>{
        console.log(response);
        console.log(response['error']);

        if(response['error']=='X'){
          this._store.dispatch({type: "OTP_GENERATION",payload:{error:'X',mobNo:obj.mobileNo, msg:"OTP not send"}});
          return;
        }

        let res=response;
        this._store.dispatch({type: "OTP_GENERATION",payload:{error:'',mobNo:obj.mobileNo,msg:res.success}});
      },err=>{
        if(err.status == 0){
          this._store.dispatch({type: "OTP_GENERATION",payload:{error:'X',mobNo:obj.mobileNo, msg:'No Internet Connection'}});
        }else {
          this._store.dispatch({type: "OTP_GENERATION",payload:{error:'X',mobNo:obj.mobileNo, msg:err.json().error}});
        }
        console.log(err);
        console.log(err.json());
      });
    });


    //************ login  ***************************************************
    this.login.subscribe((obj)=> {
      const path = paths.PATH+':8094/authentication?mobileNumber='+obj.mobileNo+'&otp='+obj.otp;
      this.dologin(path).subscribe(response =>{
        console.log(response);
        console.log(response['error']);

        if(response['error']=='X'){
          this._store.dispatch({type: "TOKEN",payload:{error:'X', msg:"login failed please try again later...."}});
          return;
        }

        let res=response;
        this._store.dispatch({type: "TOKEN",payload:{error:'',token:res['token']}});
      },err=>{
        if(err.status ==0){
          this._store.dispatch({type: "TOKEN",payload:{error:'X', msg:"No Internet Connection"}});
        }else {
          this._store.dispatch({type: "TOKEN",payload:{error:'X', msg:"error occur , "+err.json().error}});
        }
        console.log(err);
        console.log(err.json());
      });
    });




  }// constructor close here


// ***********************  get vehicle list **********************
  public sendOtp(path){

    // let queryParameters = new URLSearchParams();
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');

    // console.log(headers);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: Id,
    });


    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      // note :- if otp send than it return status code 200 and if mobile no not register then it return 409 with http error
      console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }
    });
  }



  // ***********************  otp uthentication **********************
  public dologin(path){

    // let queryParameters = new URLSearchParams();
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');

    // console.log(headers);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: Id,
    });


    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if(response.status==201){
        return response.json();
      }else{
        console.log("not 201");
        return {error:'X'};
      }
    });
  }




}// class close




