import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
// import {SUSPENDED_TIME} from "../../../services/config-services";
import {DashboardCoordinator} from "../../../services/dashboard.cordinator.service"
import {config} from "../../../../environments/environment";
import {Angulartics2Mixpanel} from "angulartics2/dist";


@Component({
  selector: 'app-vehicle-dashboard-web',
  templateUrl: './vehicle-dashboard-web.component.html',
  styleUrls: ['./vehicle-dashboard-web.component.css']
})
export class VehicleDashboardWebComponent implements OnInit {

  public vehicleCount = {all:0,stopped:0,disconnected:0,moving:0,suspended:0};
  public isSpinner=true;
  public errorMsg='';
  public search='';
  public status='';
  public isSearch = false;
  public vehicleList=[];

  public isShowLocationHistory:boolean=false;

  public unSub_vehicleList=null;
  public unSub_showLocationHistory=null;
  public unSub_vehicleStatusFilter=null;


  ngOnDestroy()
  {
    if (typeof this.unSub_vehicleList != 'undefined' && this.unSub_vehicleList != null && this.unSub_vehicleList != undefined){
      console.log("unsusVehicle list");
      this.unSub_vehicleList.unsubscribe();
    }

    if (typeof this.unSub_showLocationHistory != 'undefined' && this.unSub_showLocationHistory != null && this.unSub_showLocationHistory != undefined){
      console.log("unsusShowLocationHistory");
      this.unSub_showLocationHistory.unsubscribe();
    }

    if (typeof this.unSub_vehicleStatusFilter != 'undefined' && this.unSub_vehicleStatusFilter != null && this.unSub_vehicleStatusFilter != undefined){
      console.log("unSub_vehicleStatusFilter");
      this.unSub_vehicleStatusFilter.unsubscribe();
    }

  }

  constructor(public dashboardCoordinator : DashboardCoordinator, private _store:Store<any>,public _angularitics: Angulartics2Mixpanel ) {
    // ***************  vehicle list ************************************
    this.unSub_vehicleList = _store.select('vehicleListRes').subscribe(value => {
      // console.log(value);
      this.errorMsg='';
      if(value==null)
      {
        return;
      }

      if(value['error']==''){
        console.log(value);
        this.vehicleCount = {all:0,stopped:0,disconnected:0,moving:0,suspended:0};
        this.vehicleList = value['data'];
        this.vehicleCount['all'] = this.vehicleList.length;
        for(let i=0;i<this.vehicleList.length; i++)
        {
          this.vehicleList[i]['_duration'] = this.msToTime(this.vehicleList[i]['duration'] );
          if(this.vehicleList[i]['state'] == null || (this.vehicleList[i]['state'] == 'Disconnected' && this.vehicleList[i]['duration'] > config.SUSPENDED_TIME )){
            this.vehicleList[i]['state'] = "Suspended"
          }

          if(this.vehicleList[i]['state'] == 'Stopped'){
            this.vehicleCount['stopped'] = this.vehicleCount['stopped'] + 1;
          }

          if(this.vehicleList[i]['state'] == 'Disconnected'){
            this.vehicleCount['disconnected'] = this.vehicleCount['disconnected'] + 1;
            this.vehicleList[i]['state'] = 'Disconnected';
          }

          if(this.vehicleList[i]['state'] == 'Moving'){
            this.vehicleCount['moving'] = this.vehicleCount['moving'] + 1;
          }

          if(this.vehicleList[i]['state'] == "Suspended"){
            this.vehicleCount['suspended'] = this.vehicleCount['suspended'] + 1;
          }
        }

        console.log(this.vehicleList);

        if(this.vehicleList.length==0){
          this.errorMsg="No records found."
        }

        // console.log(this.vehicleList);
      }else {
        // console.log(value['errorMsg']);
        this.errorMsg=value['errorMsg'];
      }

      this.isSpinner=false;
    });

    // ***************  dispatch from vehicle list **********************//
    // **************  used for show timeline(location history component) ***************
    this.unSub_showLocationHistory = _store.select('showLocationHistory').subscribe(value => {
      console.log(value);
      if(value==null){
        return;
      }
      this.isShowLocationHistory=true;
    });

    // ***************************vehicle status filter store**********************************
    this.unSub_vehicleStatusFilter = _store.select('selectedFilterVehicleStatus').subscribe(value => {
      if(value== null || value==undefined) {
        return;
      }
      else{
        console.log(value.toString());
        console.log('In vehicle list status filter:');
        this.status = value.toString();
      }

    });


  }

  ngOnInit() {
  }


  filterVehicleStatus(status)
  {
    console.log('desired vehicle status');
    console.log(status);

    this._angularitics.eventTrack('VehicleList_Fliter_Web', {category:"ListFilters"});

    this._store.dispatch({type: "SELECTED_VEHICLE_FILTER_STATUS",payload:status});
    this.dashboardCoordinator.fileterCoordinator.emit({search : this.search , state : this.status}  )
  }

  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration='';

    if(days>0){
      totalDuration=days+"d ";
    }
    if(hours>0){
      totalDuration=totalDuration+hours+"hr ";
    }

    if(days >= 1){
      return totalDuration;
    }

    if(minutes>0){
      return totalDuration=totalDuration+minutes+" min";
    }
    if(seconds>0 && totalDuration == ''){
      totalDuration=totalDuration+seconds+" sec";
    }

    if(totalDuration==''){
      return '0 sec';
    }else {
      return totalDuration;
    }

  }//close methods msToTime


  showMapView()
  {
    this.isShowLocationHistory=false;
  }

  searchVehicle()
  {
    console.log('search Term:');
    console.log(this.search);
    this._store.dispatch({type: "VEHICLELIST_SEARCH_FILTER",payload:this.search});

    this.dashboardCoordinator.fileterCoordinator.emit({search : this.search , state : this.status}  )
  }
}



