  ngOnDestroy() {
    this._store.dispatch({ type: "IS_BACK", payload: { isBack: false, backToComponent: '' } });
    this._store.dispatch({type :"ETA_DISTANCE" ,payload : null})
    if (typeof this.unSub_sourceAddress != 'undefined' && this.unSub_sourceAddress != null && this.unSub_sourceAddress != undefined) {
      // console.log(" unSub_sourceAddress");
      this.unSub_sourceAddress.unsubscribe();
    }

    if (typeof this.unSub_etaDistance != 'undefined' && this.unSub_etaDistance != null && this.unSub_etaDistance != undefined) {
      // console.log(" unSub_destinationAddress");
      this.unSub_etaDistance.unsubscribe();
    }

    if (typeof this.unSub_destinationAddress != 'undefined' && this.unSub_destinationAddress != null && this.unSub_destinationAddress != undefined) {
      // console.log(" unSub_destinationAddress");
      this.unSub_destinationAddress.unsubscribe();
    }

    if (typeof this.unSub_detailsAddTrip != 'undefined' && this.unSub_detailsAddTrip != null && this.unSub_detailsAddTrip != undefined) {
      // console.log("unSub_detailsAddTrip");
      this.unSub_detailsAddTrip.unsubscribe();
    }


    if (typeof this.unSub_searchName != 'undefined' && this.unSub_searchName != null && this.unSub_searchName != undefined) {

      this.unSub_searchName.unsubscribe();
    }

    if (typeof this.unSub_vehicleList != 'undefined' && this.unSub_vehicleList != null && this.unSub_vehicleList != undefined) {
      this.unSub_vehicleList.unsubscribe();
    }


    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined) {
      this.unSub_customerDetails.unsubscribe();
    }
    if (typeof this.unSub_createQuickTrip != 'undefined' && this.unSub_createQuickTrip != null && this.unSub_createQuickTrip != undefined) {
      this.unSub_createQuickTrip.unsubscribe();
    }

    if (typeof this.unSub_PickedVehicles != 'undefined' && this.unSub_PickedVehicles != null && this.unSub_PickedVehicles != undefined) {
      this.unSub_PickedVehicles.unsubscribe();
    }

  }

  constructor(private _store: Store<any>, public _services: Services,public templateServices:TripTemplateServices, private mapsAPILoader: MapsAPILoader, private ngZone: NgZone, private _geoServices: geoCodeServices, public snackBar: MdSnackBar, private tripServices: TripServices, private eta_distance: ETA_DistanceService,public localStorageService: LocalStorageService)
