  // *****************  create new trip  ********************8
  addTripData() {
    if (isNullOrUndefined(this.vehicleNo) || this.vehicleNo == "") {
      this.openSnackBar("Please fill Vehicle Number");
      return;
    }

    if (isNullOrUndefined(this.originTripAddress) || this.originTripAddress == "") {
      this.openSnackBar("Please fill Trip Origin");
      return;
    }

    if (isNullOrUndefined(this.vehicleId) || this.vehicleId == "") {
      this.vehicleNo = '';
      this.openSnackBar("Please fill vehicle no");
      return;
    }

    if (isNullOrUndefined(this.destinationTripAddress) || this.destinationTripAddress == "") {
      this.openSnackBar("Please fill Trip Destination");
      return;
    }

    if (isNullOrUndefined(this.pickupTime) || this.pickupTime == "") {
      this.openSnackBar("Please select PickUp Time");
      return;
    }

    if (isNullOrUndefined(this.transitDays) || "" + this.transitDays == "") {
      this.openSnackBar("Please fill Transit Days");
      return;
    }

    let centerDestinLat = 0;
    let centerDestinLng = 0;
    let boundaryDestin = [];


    let originList = [];

    let origin = {
      "freightLocation": this.originObj,
      "fromTime": null,
      "tillTime": null
    }

    originList.push(origin);

    let destinList = [];
    let destination = {
      "freightLocation": this.destinObj,
      "fromTime": null,
      "tillTime": null
    }

    destinList.push(destination);

    let tripData = {
      "vehicleId": this.vehicleId,
      "origin": originList,
      "destination": destinList,
      "material": this.material,
      "tripId": null,
      "actualStartTime": null,
      "actualEndTime": null,
      "plannedStartTime": this.pickupTime.getTime(),
      "plannedEndTime": (this.pickupTime.getTime() + this.transitDays * 86400000),
      "lane": null,
      "tripState": "Created",
      "driverMobNumber": this.driverMob,
      "driverName": this.driverName
    };

    console.log(tripData);
    this.isClicked = true;
    this.tripServices.createTrip.emit(tripData);
  }



  // *****************  create Quick trip  ********************
  addQuickTripData() {
    if (isNullOrUndefined(this.selectedVehicleList) || this.selectedVehicleList == "") {
      this.openSnackBar("Please fill Vehicle Numbers");
      return;
    }

    if (isNullOrUndefined(this.originTripAddress) || this.originTripAddress == "") {
      this.openSnackBar("Please fill Trip Origin");
      return;
    }
    if (isNullOrUndefined(this.destinationTripAddress) || this.destinationTripAddress == "") {
      this.openSnackBar("Please fill Trip Destination");
      return;
    }

    if (isNullOrUndefined(this.pickupTime) || this.pickupTime == "") {
      this.openSnackBar("Please select PickUp Time");
      return;
    }

    if (isNullOrUndefined(this.transitDays) || "" + this.transitDays == "") {
      this.openSnackBar("Please fill Transit Days");
      return;
    }

    let centerDestinLat = 0;
    let centerDestinLng = 0;
    let boundaryDestin = [];

    let originList = [];

    let origin = {
      "freightLocation": this.originObj,
      "fromTime": null,
      "tillTime": null
    }

    originList.push(origin);

    let destinList = [];
    let destination = {
      "freightLocation": this.destinObj,
      "fromTime": null,
      "tillTime": null
    }

    destinList.push(destination);
    let selectedVehicleUUIDList=[];
    for(let i=0;i<this.vehicleList.length;i++)
    {
      for (let j = 0; j < this.selectedVehicleList.length; j++) {
        if(this.selectedVehicleList[j]==this.vehicleList[i]['vehicleRegistrationNumber']){
          selectedVehicleUUIDList.push({vehicleId:this.vehicleList[i]['uuid']})
        }
      }
    }
    console.log(selectedVehicleUUIDList);
    let tripData = {
      "vehicleId":this.vehicleId,
      "origin": originList,
      "destination": destinList,
      "material": this.material,
      "tripId": null,
      "actualStartTime": null,
      "actualEndTime": null,
      "plannedStartTime": this.pickupTime.getTime(),
      "plannedEndTime": (this.pickupTime.getTime() + this.transitDays * 86400000),
      "lane": null,
      "tripState": "Created",
      "driverMobNumber": this.driverMob,
      "driverName": this.driverName
    };
    let obj={
      "tripData":tripData,
      "overrideTrip":selectedVehicleUUIDList
    }

    let tripTemplateData = {

      "orgId": this.orgId,
      "origin": this.originObj,
      "destination": this.destinObj,
      "transitDays": this.transitDays,
      "distance": this.distance,
      "material": this.material,
      "quantity":this.quantity
    };

    let isMatchingTemplateFound:boolean=false;
    for(let i=0; i<this.tripTemplateList.length;i++)
    {
      if(this.tripTemplateList[i]['material']==tripTemplateData['material'] )
      {
        if(this.tripTemplateList[i]['origin']['name']==tripTemplateData['origin']['name'])
        {
          if(this.tripTemplateList[i]['destination']['name']==tripTemplateData['destination']['name'])
          {
            isMatchingTemplateFound=true;
            console.log("Matching template found to already exist");
            break;
          }
        }

      }
    }

    if(this.commandType=='skip' && !isMatchingTemplateFound) {
      console.log("template created from new trip");
      console.log(tripTemplateData);
      this.templateServices.createTripTemplate.emit(tripTemplateData);

    }

    console.log(obj);
    this.isClicked = true;
    this.tripServices.createQuickTrip.emit(obj);

  }



