      if (vno == this.vehicleList[ind]['vehicleRegistrationNumber']) {
        console.log(this.vehicleList[ind]);
        console.log(this.vehicleList[ind]['uuid']);
        this.vehicleList[ind]['select'] = true;
        this.vehicleNo = this.vehicleList[ind]['vehicleRegistrationNumber'];
        this.vehicleId = this.vehicleList[ind]['uuid'];
        let check=false;
        for (let i = 0; i < this.selectedVehicleList.length; i++) {
          if(this.selectedVehicleList[i]===this.vehicleNo)
            check=true;
        }
        if(!check)
        this.selectedVehicleList.push(this.vehicleNo);
      }
