import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {Services} from "./services";
// import {PATH, GEOCODE_PATH} from "./path-services";
import {config, paths} from "../../environments/environment";
// import {KEY} from "./config-services";

@Injectable()
export class geoCodeServices {
  // public defaultHeaders: Headers = new Headers();
  private geocodePath=paths.GEOCODE_PATH;
  private key:any=config.KEY;
  public getGeoCodeAddress=new EventEmitter();

  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>){

    //************ get vehicle list  ***************************************************
    this.getGeoCodeAddress.subscribe((obj)=> {

        let geoCodeUrl='';
      geoCodeUrl = this.geocodePath+'/json?latlng='  + obj.latitude+ ',' + obj.longitude +'&key='+this.key;
        this.getAddress(geoCodeUrl).subscribe(response =>{

          if(response.status == 'OK')
            this._store.dispatch({type: "FORMATTED_ADDRESS",payload:{error:'',address:response}});
          else
            this._store.dispatch({type: "FORMATTED_ADDRESS",payload:{error:'',address:''}});

        },err=>{
          console.log(err);
          this._store.dispatch({type: "FORMATTED_ADDRESS",payload:{error:'X',errorMsgObj:err}});
        });
    });
  }// constructor close here

  public getAddress(path){
    let headers = new Headers();
    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
    });

    return this.http.request(path, requestOptions).map((response: Response) => {
      console.log(response.json());
      return response.json();
    });
  }
}// class close




