import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import {ScheduleReportServices} from "../../../services/schedule-report-services";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import { Angulartics2Mixpanel } from 'angulartics2/dist';

@Component({
  selector: 'app-schedule-list',
  templateUrl: './schedule-list.component.html',
  styleUrls: ['./schedule-list.component.css']
})
export class ScheduleList implements OnInit {
  public isSpinner=false;
  public errorMsg ='';
  public state:boolean;
  // public scheduleLength = 0;
  public scheduleList:any =[];
  public unSub_scheduleReportList = null;
  public unSub_changeScheduleReportState = null;


  ngOnDestroy() {
    // this._store.dispatch({type: "IS_BACK",payload:{isBack:false,backToComponent:''}});
    // this._store.dispatch({type: "LOCATION_HISTORY",payload:null});
    if (typeof this.unSub_scheduleReportList != 'undefined' && this.unSub_scheduleReportList != null && this.unSub_scheduleReportList != undefined)
      this.unSub_scheduleReportList.unsubscribe();

    if (typeof this.unSub_changeScheduleReportState != 'undefined' && this.unSub_changeScheduleReportState != null && this.unSub_changeScheduleReportState != undefined)
      this.unSub_changeScheduleReportState.unsubscribe();
  }

  ngOnInit() {
    this._angularitics.eventTrack('Schedule_Reports_Opened', { category: 'ScheduleReports'});

  }

  constructor(private _store:Store<any>,private scheduleReportService:ScheduleReportServices,public snackBar: MdSnackBar,public _angularitics:Angulartics2Mixpanel) {
    _store.dispatch({type: "TITLE",payload:'Schedule Report'}); // set the header name
    // _store.dispatch({type: "IS_BACK",payload:{isBack:true,backToComponent:''}}); // replace the menu button with back arrow

    this.scheduleReportService.getScheduleReport.emit();
    this.isSpinner = true;

    this.unSub_scheduleReportList = _store.select('scheduleReportList').subscribe(value => {
      console.log(value);
      this.errorMsg='';
      if (value == null) {
        return;
      }

      if(value['error'] =='X'){
        console.log(value['msg']);
        this.errorMsg = value['msg'];
        this.isSpinner = false;
      }

      if(value['error'] ==''){
        this.scheduleList =value['list'];
        console.log(value['list']);

        for(let i=0;i<this.scheduleList.length;i++){
          if (this.scheduleList[i]['reportSchduleType'] == "Weekly") {
            let days = '';
            for(let j=0;j<this.scheduleList[i]['schedule']['daysOfWeek'].length;j++) {
              console.log(this.numberToDay(this.scheduleList[i]['schedule']['daysOfWeek'][j]));
              days = days+this.numberToDay(this.scheduleList[i]['schedule']['daysOfWeek'][j])+', ';
            }
            this.scheduleList[i]['_days']=days;
          }

          // this.scheduleList[i]['reportType'][i];
        }
        this.isSpinner = false;



      }

    });
  } // constructor close


  numberToDay(dayNo) {
    let dayList = ["Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday"];
    return dayList[dayNo];
  }


  changeScheduleState(selectedSchedule){


    console.log(selectedSchedule);
    this._store.dispatch({type: "CHANGE_SCHEDULE_REPORT_STATE_ACK",payload:null});
    this.state = JSON.parse(JSON.stringify(selectedSchedule['activeStatus']));
    console.log(this.state);

    let reqObj={
      scheduleId: selectedSchedule['scheduleId'],
      activeState:selectedSchedule['activeStatus'] = !selectedSchedule['activeStatus']
    };

    console.log(reqObj);
    this._angularitics.eventTrack('Schedule_State-Changed', reqObj);
    this.scheduleReportService.changeScheduleState.emit(reqObj);
    this.isSpinner = true;

    this.unSub_changeScheduleReportState = this._store.select('changeScheduleReportState').subscribe(value => {
      console.log(value);
      this.errorMsg='';
      if(value !=null){
        this.isSpinner = false;
        if(value['error'] == ''){
          console.log(value['errorMsg']);
          this.openSnackBar(value['errorMsg']);
        }else{
          console.log(value['errorMsg']);
          this.openSnackBar(value['errorMsg']);
          selectedSchedule['activeStatus'] = this.state;
        }
      }
    });
  }


  goToCreateNewSchedule(){
    this._store.dispatch(go(['/vtsdashboard/createSchedule']));
  }


  openSnackBar(message: string) {
    this.snackBar.open(message, '', <MdSnackBarConfig>{duration :  3000});
  }

}



