import {Inject, Injectable, Optional, EventEmitter, OnDestroy}                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";

// import * as models                                           from './model/models';
import {Services} from "./services";

@Injectable()
export class TimerServices implements OnDestroy {
  public unSub_customerDetails:any=null;
  public clearVehInterval:any=null;
  public customer:any=null;
  public vehicleListTimer=new EventEmitter();
  public stopTimerService=new EventEmitter();
  // private localStorageService: LocalStorageService;

  ngOnDestroy(){
    console.log("stop timer");
    clearInterval(this.clearVehInterval);
    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
    {

      this.unSub_customerDetails.unsubscribe();
    }
  }

  constructor(private _store:Store<any>,private _services:Services){
    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      console.log(value);
      // this.customer=null;
      // **********   uncomment please **********************
      if(value==null){
        if(this.clearVehInterval != null || this.clearVehInterval != undefined){
          clearInterval(this.clearVehInterval);
        }
        return;
      }

      if(value['error']==''){
         this.customer= value['customer'];
        console.log("user valid ");
        console.log(this.customer);
        console.log("call start timer methods");
        this.startTimer(); // this is use for first time load services when customer details get valid response

      }else{
        return;
      }
    });

    this.vehicleListTimer.subscribe(()=> { // this is called when vehicle list component load
      if(this.customer == '' || this.customer == null ||  this.customer == undefined) {
        console.log('received emitting vehicleListTimer');
        console.log(this.customer);
      }
      else{
        console.log('timer service start');
        this.stopTimer();
        this.startTimer();
        }
    });


    this.stopTimerService.subscribe(()=> { // this is called when vehicle list component ngOnDestroy methods call
      console.log('timer service stop');
      this.stopTimer();
      });

  }

 public startTimer(){
    console.log("starttimer");
    this._services.getVehicleList.emit(this.customer['id']);

  this.clearVehInterval= setInterval(()=> {
      if(this.customer !=null && this.customer['id']!=null && this.customer['id']!= undefined) {
        this._services.getVehicleList.emit(this.customer['id']);
      }
    }, 15000);
  }

 public stopTimer(){
    console.log('timer stop');
    if(this.clearVehInterval != null || this.clearVehInterval != undefined){
      clearInterval(this.clearVehInterval);
    }
  }



}



