import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";

@Component({
  selector: 'app-user-management-console',
  templateUrl: './user-management-console.component.html',
  styleUrls: ['./user-management-console.component.css']
})
export class UserManagementConsoleComponent implements OnInit {

  public view ='';

  constructor(private _store:Store<any>) {
    this.view=document['view'];
    this._store.dispatch({type: "TITLE",payload:'User Management'});// set the header name
  }

  ngOnInit() {
  }

  goToManageOrganisationView(){
    this._store.dispatch(go(['/vtsdashboard/manageOrganisation']));
  }


}



