import {Component, Injectable, Input, ViewChild, EventEmitter} from '@angular/core';
import { Router} from "@angular/router";
import {Store} from "@ngrx/store";
// import {Services} from "../../services/services";
import {Services} from "../../../services/services";
import {go,back} from "@ngrx/router-store";
import {LocalStorageService} from "angular-2-local-storage";
import * as mapTypes from '@agm/core/services/google-maps-types';
import {Polyline} from '@agm/core/services/google-maps-types';
import {PolylineOptions} from '@agm/core/services/google-maps-types';
import {MapsAPILoader} from '@agm/core/services/maps-api-loader/maps-api-loader';
declare var MyPolylineUtil : any;

// import * as polylineDecoder from 'decode-google-map-polyline';
import {} from '@types/googlemaps';
import {AgmMap} from "@agm/core";
import { Angulartics2Mixpanel } from 'angulartics2/dist';


@Component({
  selector: 'app-location-history',
  templateUrl: './location-history.component.html',
  styleUrls: ['./location-history.component.css']
})
export class LocationHistory {
  @ViewChild(AgmMap) agmMap;

  public isSpinner=false;
  public errorMsg='';
  title: string = 'maps';
  mapHeight='0px';
  lat = null;
  long = null;
  zoom :number = 10;
  isDrag = false;
  public startOffsetHeight=0;
  public isSelectDateDialog = false;
  public selectedDate=new Date(new Date().setHours(0,0,0,0));
  public selectdVehicle={};
  public dateList:any=[];
  // public selectdVehicleType='';
  public previousOffset=0;
  public divHeight='50%';
  public isFullScreen:boolean=false;
  public showFilterDialogue:boolean=false;
  public timeDurationUnit="Min";
  public timeDuration=5;
  public locationHislist:any=[];
  public unSub_locationHistory:any=null;
  public isFilterStatusSelected:boolean;
  public filterStatusList: any=[];
  public filterDuration=0;
  public startFilterTime=0;
  public endFilterTime=0;
  public polyLinePosList:any=[];
  public polyLineStoppedArray:any=[];
  public polyLineMovingArray:any=[];
  public stoppedPolyLineArray:any=[];
  public polyLineOverspeedArray:any=[];
  public decodedPolyline:any=[];
  public _map:Promise<mapTypes.GoogleMap>;
  private _mapResolver: (value?: mapTypes.GoogleMap) => void;
  public chkStopped:boolean=true;
  public chkOverspeed:boolean=true;
  public chkMoving:boolean=true;
  public chkDisconnected:boolean=true;
  public  isfooterDateBar:any=false;

  public unSub_selectedVehicle:any=null;
  public unSub_getTimeline:any=null;

  public view ='';

  ngOnDestroy() {

    if (typeof this.unSub_locationHistory != 'undefined' && this.unSub_locationHistory != null && this.unSub_locationHistory != undefined)
    { this.unSub_locationHistory.unsubscribe();}

    if (typeof this.unSub_selectedVehicle != 'undefined' && this.unSub_selectedVehicle != null && this.unSub_selectedVehicle != undefined)
    { this.unSub_selectedVehicle.unsubscribe();}


    if (typeof this.unSub_getTimeline != 'undefined' && this.unSub_getTimeline != null && this.unSub_getTimeline != undefined)
    { this.unSub_getTimeline.unsubscribe();}
  }

  ngAfterViewChecked(){
    // let mapOffset=document.getElementById("map").getBoundingClientRect();
    if(screen.width < 600){
      this.isfooterDateBar =false;
    }else{
      this.isfooterDateBar =true;
    }
    // console.log(screen.width );
  }

ngOnInit(){
  let offsets=document.getElementById("timeline_card").getBoundingClientRect();
  this.startOffsetHeight=offsets.top;
  this.previousOffset=offsets.top;
}


  constructor(private _store:Store<any>,private _router:Router,public _services:Services,private localStorageService: LocalStorageService,public _angularitics:Angulartics2Mixpanel) {

    this.view =document['view'];

    if(this._router.url =='/vtsdashboard/locationHistory' && this.view =='web'){
      this._store.dispatch(go(['vtsdashboard/vehicleDashboard']));
      return;
    }

    this._store.dispatch({type: "TITLE",payload:'Location History'}); // set the header name

    this._map = new Promise<mapTypes.GoogleMap>((resolve: () => void) => { this._mapResolver = resolve; });
    let oneMonthAgo = new Date(new Date().getFullYear(), new Date().getMonth() , new Date().getDate()).getTime();
    console.log(oneMonthAgo);
    // console.log(1000*60*60*24);

    for(let i=0; i<30;i++){
      this.dateList[i] =new Date(oneMonthAgo);
      oneMonthAgo =   oneMonthAgo -(1000*60*60*24);
    }

    if(this.view != 'web') {
      let value = this.localStorageService.get('SELECTED-LOCATION-HIS-VEHICLE');
      console.log(value);
      if (value != undefined || value != null) {
        this.selectdVehicle = value;
        this.lat = this.selectdVehicle['latitude'];
        this.long = this.selectdVehicle['longitude'];

        if (this.view != 'web') {
          console.log("only mobile");
          this.getLocationHistory(this.selectedDate);
        }
      }
    }

    // only use in desktop view
    if(this.view == 'web') {
      this.unSub_getTimeline = _store.select('getTimeline').subscribe(value => {
        if (value == null){
          return;
        }

        let objct= this.localStorageService.get('SELECTED-LOCATION-HIS-VEHICLE');
        console.log(objct);
        if(objct !=undefined || objct != null){
          this.selectdVehicle=objct;
          this.lat=this.selectdVehicle['latitude'];
          this.long=this.selectdVehicle['longitude'];
        }

        console.log( this.selectdVehicle);
          this.getLocationHistory(this.selectedDate);
      });
    }

    // else if (value==null){
    //   this.unSub_selectedVehicle = _store.select('selectedVehicleLocationHistory').subscribe(value => {
    //     console.log(value);
    //     if(value==null){
    //       return;
    //     }
    //
    //     this.selectdVehicle=value;
    //     this.lat=this.selectdVehicle['latitude'];
    //     this.long=this.selectdVehicle['longitude'];
    //     this.getLocationHistory(this.selectedDate);
    //   });
    // }


    this.unSub_locationHistory = _store.select('locationHistory').subscribe(value => {
      this.isSpinner=false;
      this.locationHislist=[];
      this.errorMsg='';

      console.log(value);
      if(value ==null){
        return;
      }

      if(value['error']==''){
        this.locationHislist = value['data'];
        // this.locationHislist = new Array['data'];
        console.log(this.locationHislist);
        this.setPolyLine(this.locationHislist);
      }else {
        this.errorMsg=value['errorMsg'];
        // console.log(value['errorMsg']);
      }

      let lessFiveKmhMovingDistance = 0;
      for(let i=0;i<this.locationHislist.length;i++){
        if(this.locationHislist[i]['state'] == "Stopped") {
          lessFiveKmhMovingDistance = this.locationHislist[i]['distance'];
        }

        this.locationHislist[i]['_duration']=this.msToTime(this.locationHislist[i]['duration']);

        if(this.locationHislist[i]['state'] == "Moving") {
          this.locationHislist[i]['_formattedDistance'] = this.locationHislist[i]['distance'] + lessFiveKmhMovingDistance;
          lessFiveKmhMovingDistance = 0;
        }
      }

    });
  } // constructor close

  fullScreen(){
    // if large screen then timeline swipe up event not work;
    if(document.getElementById("timeline_card").getBoundingClientRect().top == 35)
      return;
    // *****end here  if large screen then timeline swipe up event not work ****;

    let mapOffsets=document.getElementById("map").getBoundingClientRect();
    this.mapHeight=(-mapOffsets.height)+'px';
    this.isFullScreen=true;

      // if(this.isFullScreen){
      //   this.divHeight='100%';
      // }else{
      //   setTimeout(() => {
      //   this.divHeight='45%';
      //   }, 1000);
      // }
  }

 hideScreen(){
   // console.log("h");
    let mapOffsets=document.getElementById("map").getBoundingClientRect();
    this.mapHeight=(-mapOffsets.height)+'px';
    this.isFullScreen=false;
    //
    // if(this.isFullScreen){
    //   this.divHeight='100%';
    // }else{
    //   setTimeout(() => {
    //     this.divHeight='50%';
    //
    //   }, 500);
    // }
  }

  // card=0;
  // myFunction(event){
  //    // this.card=document.getElementById("timeline_card");
  //   let offsets=document.getElementById("timeline_card").getBoundingClientRect();
  //   console.log(offsets);
  // }


  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration='';

    if(days>0){
      totalDuration=days+"d ";
    }
    if(hours>0){
      totalDuration=totalDuration+hours+"hr ";
    }

    if(days >= 1){
      return totalDuration;
    }

    if(minutes>0){
      return totalDuration=totalDuration+minutes+"min";
    }
    if(seconds>0 && totalDuration == ''){
      totalDuration=totalDuration+seconds+" sec";
    }

    if(totalDuration==''){
      return '0 sec';
    }else {
      return totalDuration;
    }

  }//close methods msToTime

  previousDate(){
    this.selectedDate=new Date(this.selectedDate.setHours(-24,0,0,0));
    this._angularitics.eventTrack('Timeline_Backdate_Clicked', { category: 'TimelineDate'});
    this.getLocationHistory(this.selectedDate);
  }

  nextDate(){
    this.selectedDate=new Date(this.selectedDate.setHours(24,0,0,0));
    this._angularitics.eventTrack('Timeline_Nextdate_Clicked', { category: 'TimelineDate'});
    this.getLocationHistory(this.selectedDate);
  }



  getLocationHistory(selectedDate){
    this.chkStopped=true;
    this.chkOverspeed=true;
    this.chkMoving=true;
    this.chkDisconnected=true;
    this.selectedDate = new Date(selectedDate.getTime());

    this.isSpinner=true;
    this.locationHislist=[];
    this.polyLineMovingArray=[];
    this.polyLineStoppedArray=[];
    this.stoppedPolyLineArray=[];
    this.errorMsg='';
    let date=this.selectedDate;
    let obj={
      startTime:date.getTime(),
      endTime: (date.getTime() + (24*60*60*1000)),
      vehicleId:this.selectdVehicle['uuid'],
      minimumTime:300000,
      type:["Overspeeding","Stopped","Moving","Disconnected"]
    }
    // console.log(obj);

    this._angularitics.eventTrack('Specific_Date_Selected', selectedDate);
    this._services.getLocationHistory.emit(obj);
  }


  back(){
    this._store.dispatch(back());
  }

  getFilteredLocationHistory(data){

    this.isSpinner=true;
    this.locationHislist=[];
    this.errorMsg='';
    let date=this.selectedDate;
    let obj=data;
    console.log(obj);
    this._services.getFilterLocationHistory.emit(obj);

  }

  goToMapLocation(selectedlocation){

    // this.agmMap.mapReady.subscribe(map => {
    //   console.log(map.)
    // });

    console.log(selectedlocation);
    if(selectedlocation['state'] == 'Stopped') {
      console.log(this.polyLineStoppedArray);
      for (let i = 0; i < this.polyLineStoppedArray.length; i++) {
        this.polyLineStoppedArray[i].isInfoWindowOpen = false;
        if (selectedlocation['endPosition']['latitude'] == this.polyLineStoppedArray[i].lat && selectedlocation['endPosition']['longitude'] == this.polyLineStoppedArray[i].long) {
          console.log(i);
          this.lat=selectedlocation['endPosition']['latitude'];
          this.long=selectedlocation['endPosition']['longitude'];
          this.polyLineStoppedArray[i].isInfoWindowOpen = true;
          this.zoom = 12;
        }
      }
    }
  }

  setFilterValues(chk0,chk1,chk2,chk3)
  {
    this.polyLineMovingArray=[];
    this.filterStatusList=[];
    if(this.chkStopped==true)
    {
      this.filterStatusList.push(chk0.value);
      console.log(this.filterStatusList);
    }

    if(this.chkMoving==true)
    {
      this.filterStatusList.push(chk1.value);
      console.log(this.filterStatusList);
    }

    if(this.chkOverspeed==true)
    {
      this.filterStatusList.push(chk2.value);
      console.log(this.filterStatusList);
    }
    if(this.chkDisconnected==true)
    {
      this.filterStatusList.push(chk3.value);
      console.log(this.filterStatusList);

    }

    this.showFilterDialogue=false;

    let obj={};
    let date= new Date();

    // let testEndTime=1499229764310;




    if(this.timeDurationUnit=="Hrs")
    {
      let durationTime:any;
      durationTime=this.timeDuration*60*60*1000;

      let date=this.selectedDate;

      this.startFilterTime=date.getTime();
      this.endFilterTime=date.getTime()+86400000;

      // this.startFilterTime=1499228171604;
      // this.endFilterTime=testEndTime;



      obj={
        "startTime":this.startFilterTime,
        "endTime":this.endFilterTime,
        "vehicleId":this.selectdVehicle['uuid'],
        "minimumTime":durationTime,
        "type":this.filterStatusList
      }

      console.log(obj);

      this._angularitics.eventTrack('Timeline_Filter_Applied', obj);
      this.getFilteredLocationHistory(obj);

      // this.setPolyLine()
    }
    else if(this.timeDurationUnit=="Min")
    {
      let durationTime:any;
      durationTime=this.timeDuration*60*1000;
      let date=this.selectedDate;
      this.startFilterTime=date.getTime();
      this.endFilterTime=date.getTime()+86400000;


      obj={
        "startTime":this.startFilterTime,
        "endTime":this.endFilterTime,
        "vehicleId":this.selectdVehicle['uuid'],
        "minimumTime":durationTime,
        "type":this.filterStatusList
      }

      console.log(obj);
      this._angularitics.eventTrack('Timeline_Filter_Applied', obj);
      this.getFilteredLocationHistory(obj);

    }
  }


  setPolyLine(locationHislist)
  {
    // console.log("setPolyLine");
    // console.log(locationHislist);

    let posArrayStopped=[];
    let posArrayMoving=[];
    let posArrayOverspeed=[];
    let decodedPolyline=[],singlePolyline=[],allPolylines=[],decodedPolylineStop=[];

    let pos1={},pos2={};

    for(let i=0; i<this.locationHislist.length; i++)
    {
      if(this.locationHislist[i]['state']=='Stopped'|| this.locationHislist[i]['state']==0) {
        pos2 = {
          lat: this.locationHislist[i]['endPosition']['latitude'],
          long: this.locationHislist[i]['endPosition']['longitude'],
          address: (this.locationHislist[i]['endPosition']['address'] != null)?(this.locationHislist[i]['endPosition']['address']):(this.locationHislist[i]['startPosition']['address']),
          isInfoWindowOpen:false
        }
        decodedPolylineStop=MyPolylineUtil.decode(this.locationHislist[i]['encodedPolyline']);
        this.stoppedPolyLineArray.push(decodedPolylineStop);
        posArrayStopped.push(pos2);
      }
      if(this.locationHislist[i]['state']=='Moving'|| this.locationHislist[i]['state']==1) {
        decodedPolyline=MyPolylineUtil.decode(this.locationHislist[i]['encodedPolyline']);
        this.polyLineMovingArray.push(decodedPolyline);
      }

      if(this.locationHislist[i]['state']=='Overspeeding' || this.locationHislist[i]['state']==2) {
        pos1 = {
          lat: this.locationHislist[i]['startPosition']['latitude'],
          long: this.locationHislist[i]['startPosition']['longitude']
        }
        posArrayOverspeed.push(pos1);

        pos2 = {
          lat: this.locationHislist[i]['endPosition']['latitude'],
          long: this.locationHislist[i]['endPosition']['longitude']
        }
        posArrayOverspeed.push(pos2);

      }

      this.lat=this.locationHislist[i]['endPosition']['latitude'];
      this.long=this.locationHislist[i]['endPosition']['longitude'];

      this.polyLineStoppedArray=posArrayStopped;
      this.polyLineOverspeedArray=posArrayOverspeed;

    }

    console.log(pos1);
    console.log(pos1);
    console.log("posArray");
    console.log(this.polyLinePosList);
    console.log(this.stoppedPolyLineArray);


  }

  setValues(value)
  {
    console.log(value);
  }


  getNativeMap(): Promise<mapTypes.GoogleMap> { return this._map; }

}






