import {ActionReducer, Action, combineReducers} from '@ngrx/store';
import {ActionRecorderServices} from "../services/actionRecorder.service";
import {compose} from "@ngrx/core";
import {routerReducer} from "../app-states/reducer";
import {token, otpGenaration, customerDetails} from "./loginStore";
import {
  title, vehicleListRes, vehRunningReport, vehStoppageReport, vehOverspeedReport,
  locationHistory, formattedAddress, currentVehicle, shareVehicle, selectedTripSource, selectedTripDestination,
  selectedTripAddressType, saveDetailsAddTrip, isBack, PickedVehicles, configAlertFieldLocation, closeDialog,
  etaDistanceStore, etaListDistanceStore,freightStore
} from "./info_store";
import {authorization} from "./authorization-store";
import {signupTokenValidation, signUpOtpGenaration, signUpNewUserData} from "./signup-store";
import {
  addFretTripDetails, createTripDetails, searchLocationName, tripStatusList,
  vehicleTripDetails, updateTripData, createQuickTripDetails, refreshTripTemplate
} from "./trip-store";
import {scheduleReportList, scheduleCreatedAck, changeScheduleReportState} from "./schedule-report-store";
import {
  selectedFilterVehicleStatus, selectedVehicleLocationHistory, showLocationHistory,
  vehicleListSearchFliter, getTimeline
} from "./vehicleDashboardStore";
import {
  alertDeleteServiceResponse,
  alertsCreatedServiceResponse,
  configuredAlertsList, createdPowerDisconnectAlert, getAllAlertsNotification, saveDetailsConfigAlert,
  selecetdProximityLocation,
  selectedAlertForEdit,
  selectedInsideLocation
} from "./alerts-store";
import {userInfo} from "./google-auth-store";
import {tripTemplateList,tripTemplateDetail,createTripTemplate,addTripTemplateSaveDetails,updateTripTemplate,saveEditTripTemplateDetails,deleteTripTemplate} from "./tripTemplate-store";

import {
  createOrganisationResponse,
  deleteUserResponse, existingUserResponse, getAllUserList, switchOrgResp,
  updateUserStatusResponse
} from "./user-management-store";
import {
  addVehicleResponse,
  addVehiclesInGroup,
  calledEditVehicleSource,
  createGroupResponse,
  deleteGroupResponse, editVehicleResponse, getAllVehiclesGroupList, getVehiclesByGroup, getVehiclesByOrg,
  refreshVehicleList,
  selectedVehicleForEdit,
  selectedVehicleGroupForEdit
} from "./vehicle-management-store";
var obj=new ActionRecorderServices();

export const reducers = {
  token : token,
  // deviceList:deviceList,
  title:title,
  isBack,
  router: routerReducer,
  vehicleListRes,
  otpGenaration,
  authorization,
  customerDetails,
  vehRunningReport,
  vehStoppageReport,
  vehOverspeedReport,
  locationHistory,
  formattedAddress,
  currentVehicle,
  signupTokenValidation,
  signUpOtpGenaration,
  signUpNewUserData,
  shareVehicle,
  selectedTripSource,
  selectedTripDestination,
  configAlertFieldLocation,
  selectedTripAddressType,
  saveDetailsAddTrip,
  tripStatusList,
  vehicleTripDetails,
  addFretTripDetails,
  createTripDetails,

  createQuickTripDetails,
  searchLocationName,
  scheduleReportList,
  PickedVehicles,
  scheduleCreatedAck,
  changeScheduleReportState,
  selectedFilterVehicleStatus,
  selectedVehicleLocationHistory,
  vehicleListSearchFliter,
  showLocationHistory,
  closeDialog,
  getTimeline,
  etaDistanceStore,
  freightStore,
  saveDetailsConfigAlert,
  configuredAlertsList,
  createdPowerDisconnectAlert,
  selectedInsideLocation,
  selecetdProximityLocation,
  alertsCreatedServiceResponse,
  alertDeleteServiceResponse,
  etaListDistanceStore,
  updateTripData,
  selectedAlertForEdit,
  userInfo,
  tripTemplateList,tripTemplateDetail,createTripTemplate,saveEditTripTemplateDetails,
  deleteTripTemplate,
  addTripTemplateSaveDetails,
  updateTripTemplate,
  deleteUserResponse,
  getAllUserList,
  updateUserStatusResponse,
  existingUserResponse,
  getAllVehiclesGroupList,
  selectedVehicleGroupForEdit,
  deleteGroupResponse,
  getVehiclesByGroup,
  selectedVehicleForEdit,
  editVehicleResponse,
  getVehiclesByOrg,
  createGroupResponse,
  addVehiclesInGroup,
  calledEditVehicleSource,
  createOrganisationResponse,
  switchOrgResp,
  refreshVehicleList,
  refreshTripTemplate,
  getAllAlertsNotification,
  addVehicleResponse
};

export const rootReducer = compose(combineReducers)(reducers)
export function mainReducer(state, action) {
  return rootReducer(state,action);
}








