import {ActionReducer, Action, combineReducers} from '@ngrx/store';
import {ActionRecorderServices } from '../services/actionRecorder.service';

var obj=new ActionRecorderServices();

export function scheduleReportList(state: any = null, action:Action) {

  switch (action.type) {

    case "SCHEDULE_REPORT_LIST" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}


export function scheduleCreatedAck(state: any = null, action:Action) {

  switch (action.type) {

    case "SCHEDULE_CREATED_ACK" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}

export function changeScheduleReportState(state: any = null, action:Action) {

  switch (action.type) {
    case "CHANGE_SCHEDULE_REPORT_STATE_ACK" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }
}



