import {Pipe, PipeTransform} from '@angular/core';

@Pipe({name: 'locHisVehListfilter', pure: true})
export class LocHisVehListfilter implements PipeTransform {
  transform(input:any[], config:any):any {
    if (config === undefined || config[2] === undefined || config[1] == undefined || config[0] == undefined) return [];
    if (!Array.isArray(input)) return input;

    if(config[2]=='')
    {
      return input;
    }
    // console.log(config);
    var list =  input.filter(u => u[config[1]].toUpperCase().indexOf(config[2].toUpperCase()) != -1 || u[config[0]].toUpperCase().indexOf(config[2].toUpperCase()) != -1);
    {
      return list ;
    }
  }
}


@Pipe({name: 'vehicleFilter', pure: true})
export class VehicleFilter implements PipeTransform {
  transform(input:any[], config:any):any {
    if (config === undefined || config[1] == undefined || config[0] == undefined ) return [];
    if (!Array.isArray(input)) return input;

    if(config[1]=='')
    {
      return input;
    }
    // console.log(config);
    var list =  input.filter(u => u[config[0]].toUpperCase().indexOf(config[1].toUpperCase()) != -1 );
    {
      return list ;
    }
  }
}


@Pipe({name: 'vehicleFilterByStatus', pure: true})
export class VehicleFilterByStatus implements PipeTransform {
  transform(input:any[], config:any):any {
    if (config === undefined || config[1] == undefined || config[0] == undefined ) return [];
    if (!Array.isArray(input)) return input;

    if(config[1]=='')
    {
      return input;
    }
    var list =  input.filter(u => (""+u[config[0]])==(""+config[1]) );
    {
      return list ;
    }
  }
}

// use for filter trip status
@Pipe({name: 'tripFilter', pure: true})
export class TripFilter implements PipeTransform {
  transform(input:any[], config:any):any {
    if (config === undefined || config[1] == undefined || config[0] == undefined ) return [];
    if (!Array.isArray(input)) return input;

    // console.log(config[1]);
    if(config[1]=='')
    {
      return input;
    }

    if(config[1]=='EnrouteForPickUp')
    {
      var list1 =  input.filter(u => ((""+u[config[0]])==(""+config[1]) || (""+u[config[0]])=="AtPickUpPoint")  );
      {
        return list1 ;
      }
    }

    var list =  input.filter(u => (""+u[config[0]])==(""+config[1]) );
    {
      return list ;
    }
  }
}


