import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';
import * as io from 'socket.io-client';
import {Injectable} from "@angular/core";
import {LocalStorageService} from "angular-2-local-storage";
import {Store} from "@ngrx/store";
// import {TRIP_DASH_SOCKET_PATH, TRIP_LIST_SOCKET_PATH} from "./path-services";
import {Services} from "./services";
import {TripServices} from "./tripServices";
import {paths} from "../../environments/environment";
// import {Http} from "@angular/http";

@Injectable()
export class TripDetailSocketService {
  private url = paths.TRIP_DASH_SOCKET_PATH;
  private tripURL = paths.TRIP_LIST_SOCKET_PATH;
  private socket ;
  private tripSocket ;
  private token ;
  public tripStatusList :any=[];
  public customer:any=null;
  public unSub_customerDetails:any=null;

  ngOnDestroy(){
    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
    {
      this.unSub_customerDetails.unsubscribe();
    }
  }

  constructor(private tripServices:TripServices,private _store:Store<any>,private localStorageService:LocalStorageService){
    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      // console.log(value);
      if(value==null){
        return;
      }

      if(value['error']==''){
        this.customer = value['customer'];
      }else{
        return;
      }

      this.token = this.localStorageService.get('TOKEN');
    // console.log(this.token);
  });


    }// constructor close

  joinUpdates(vehcleId){
    // console.log(vehcleId);
    this.socket = io(this.url);

    if( this.customer == null || this.token == null){
      console.log("Customer info not valid please login again!");
      return;
    }

    // this.socket.on("connection" ,() => {
    //   console.log("Hurreey , Connected trip services");
    // });

    this.tripServices.getTripDetails.emit({vehicleID:vehcleId}); // for call http sevices

    let tokenVehicle = {vehicleId : vehcleId , token : this.token};

    // console.log(tokenVehicle);

    this.socket.emit('joinTripDetail', tokenVehicle);
    // let isCallHttp = true;
    // this.socket.on("init", (data) =>{
    //   console.log("connected");
    //   isCallHttp = false;
    //   // console.log(JSON.parse(data));
    //   this._store.dispatch({type: "VEHICLE_TRIP_DETAILS",payload:data});
    // });

    this.socket.on("update", (data) => {
      // console.log(JSON.parse(data));
      // console.log(typeof data);

      if(typeof data =='string'){
        data = JSON.parse(data)
      }
      this._store.dispatch({type: "VEHICLE_TRIP_DETAILS",payload:data});
    });
  }

  disconnectTripSocket(){
    console.log("disconnected");
    if(this.socket != null || this.socket != undefined)
    this.socket.disconnect();
  }


  getTripList() {
    console.log('get TripList');
    this.tripSocket = io(this.tripURL);
    //
    if( this.customer == null || this.token == null){
      // console.log("no details found please login again!");
      return;
    }

    this.tripSocket.on("connection" ,() => {
      console.log("Hurreey , Connected");
    }) ;

    console.log('get TripList token');
    this.tripServices.getTripStatus.emit();

    this.tripSocket.emit('joinTripDashboard', this.token);
    this.tripSocket.on("update", (data) => {
      // console.log(data);
      //
      // if(this.tripStatusList ==null){
      //   return;
      // }

      // console.log(typeof data);
      if(typeof data =='string'){
        data =JSON.parse(data);
      }

      this._store.dispatch({type: "UPDATE_TRIP_DATA",payload:data}); // for getting updated data of vehicle // subscribe in trip-status-desktop component
    });
  }


  disconnectTripListSocket(){
    console.log("disconnected list");
    if(this.tripSocket != null || this.tripSocket != undefined)
      this.tripSocket.disconnect();
  }


}  // class close



