import {Component} from '@angular/core';
import { Router} from "@angular/router";
import {Store} from "@ngrx/store";
import {Services} from "../../services/services";
import {LocalStorageService} from "angular-2-local-storage";

@Component({
  selector: 'trip-history',
  templateUrl: './trip-history.component.html',
  styleUrls: ['./trip-history.component.css']
})

export class TripHistory {
  public isSpinner=false;
  public errorMsg='';
  public title: string = 'maps';
  public mapHeight='0px';
  public isfooterDateBar = false;
  public isDrag = false;
  public lat = null;
  public long = null;
  public isSelectDateDialog = false;
  public startOffsetHeight=0;
  public selectedDate=new Date(new Date().setHours(0,0,0,0));
  public selectdVehicle={};
  // public selectdVehicleType='';
  public previousOffset=0;
  public isFullScreen:boolean=false;
  public locationHislist:any=[];
  public dateList:any=[];
  public unSub_locationHistory:any=null;

  ngOnDestroy() {
    // this._store.dispatch({type: "LOCATION_HISTORY",payload:null});
    if (typeof this.unSub_locationHistory != 'undefined' && this.unSub_locationHistory != null && this.unSub_locationHistory != undefined)
      this.unSub_locationHistory.unsubscribe();

  }

  ngAfterViewChecked(){
    // let mapOffset=document.getElementById("map").getBoundingClientRect();
    if(screen.width < 600){
      this.isfooterDateBar =false;
    }else{
      this.isfooterDateBar =true;
    }
    // console.log(screen.width );
  }

  ngOnInit(){
    let offsets=document.getElementById("timeline_card").getBoundingClientRect();
    this.startOffsetHeight=offsets.top;
    this.previousOffset=offsets.top;
  }

  constructor(private _store:Store<any>,private _router:Router,public _services:Services,private localStorageService: LocalStorageService) {
    this._store.dispatch({type: "TITLE",payload:'Location History'}); // set the header name

    let oneMonthAgo = new Date(new Date().getFullYear(), new Date().getMonth() , new Date().getDate()).getTime();
    console.log(oneMonthAgo);
    // console.log(1000*60*60*24);

    for(let i=0; i<30;i++){
      this.dateList[i] =new Date(oneMonthAgo);
      oneMonthAgo =   oneMonthAgo -(1000*60*60*24);
    }
    // console.log( this.dateList);


    let value= this.localStorageService.get('SELECTED-LOCATION-HIS-VEHICLE');
    console.log(value);
    if(value !=undefined || value != null){
      this.selectdVehicle=value;
      this.lat=this.selectdVehicle['latitude'];
      this.long=this.selectdVehicle['longitude'];
      this.getLocationHistory(this.selectedDate);
    }

    this.unSub_locationHistory = _store.select('locationHistory').subscribe(value => {
      this.isSpinner=false;
      this.locationHislist=[];
      this.errorMsg='';

      console.log(value);
      if(value==null){
        return;
      }

      if(value['error']==''){
        this.locationHislist = value['data'];
        console.log(this.locationHislist);
      }else {
        this.errorMsg=value['errorMsg'];
        // console.log(value['errorMsg']);
      }

      for(let i=0;i<this.locationHislist.length;i++){
        this.locationHislist[i]['_duration']=this.msToTime(this.locationHislist[i]['duration']);
      }

    });
  }

  fullScreen(){
    // if large screen then timeline swipe up event not work;
    if(document.getElementById("timeline_card").getBoundingClientRect().top == 35)
      return;
    // *****end here  if large screen then timeline swipe up event not work ****;

    let mapOffsets=document.getElementById("map").getBoundingClientRect();
    // console.log(mapOffsets);

    this.mapHeight=(-mapOffsets.height)+'px';
    this.isFullScreen=true;
  }

  hideScreen(){
    // console.log("h");
    let mapOffsets=document.getElementById("map").getBoundingClientRect();
    this.mapHeight=(-mapOffsets.height)+'px';
    this.isFullScreen=false;
  }

  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration='';

    if(days>0){
      totalDuration=days+"d ";
    }
    if(hours>0){
      totalDuration=totalDuration+hours+"hr ";
    }

    if(days >= 1){
      return totalDuration;
    }

    if(minutes>0){
      return totalDuration=totalDuration+minutes+"min";
    }
    if(seconds>0 && totalDuration == ''){
      totalDuration=totalDuration+seconds+" sec";
    }

    if(totalDuration==''){
      return '0 sec';
    }else {
      return totalDuration;
    }

  }//close methods msToTime

  previousDate(){
    this.selectedDate=new Date(this.selectedDate.setHours(-24,0,0,0));
    this.getLocationHistory(this.selectedDate);
  }

  nextDate(){
    this.selectedDate=new Date(this.selectedDate.setHours(24,0,0,0));
    this.getLocationHistory(this.selectedDate);
  }

  getLocationHistory(selectedDate){
    this.selectedDate = new Date(selectedDate.getTime());
    this.isSpinner=true;
    this.locationHislist=[];
    this.errorMsg='';
    let date=new Date(selectedDate.getTime());
    let obj={
      startTime:date.getTime(),
      endTime: date.getTime() + (24*60*60*1000),
      vehicle:this.selectdVehicle['vtsDeviceId']
    }
    console.log(obj);
    this._services.getLocationHistory.emit(obj);
  }

  goToMapLocation(selectedlocation){
    console.log(selectedlocation);
    this.lat=selectedlocation['startPosition']['latitude'];
    this.long=selectedlocation['startPosition']['longitude'];
  }

}



