import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import {LocalStorageService} from "angular-2-local-storage";

@Component({
  selector: 'app-vehicle-management',
  templateUrl: './vehicle-management.component.html',
  styleUrls: ['./vehicle-management.component.css']
})
export class VehicleManagementComponent implements OnInit {

  public status:any="";
  public view='';
  public isShowVehiclesGroup:boolean=false;

  constructor(private _store:Store<any>,private localStorageService: LocalStorageService) {
    this.view =document['view'];
    this._store.dispatch({type: "TITLE",payload:'Vehicle Management'});// set the header name
    let objct= this.localStorageService.get('LAST_CALLED_FROM_ORG_MANAGEMENT');

    console.log("From localstorage services : ");
    console.log(objct);

    if(objct == "vehicle Group")
    {
      this.isShowVehiclesGroup=true;
    }
    else
    {
      this.isShowVehiclesGroup=false;
    }

  }

  ngOnInit() {
  }

  toggleVehiclesView()
  {
    if(this.status == '')
    {
      this.isShowVehiclesGroup=false;
    }

    if(this.status == 'Vehicle Group')
    {
      this.isShowVehiclesGroup=true;
    }
  }

  goToManageOrganisationView(){
    this._store.dispatch(go(['/vtsdashboard/manageOrganisation']));
  }

}



