import { Action } from '@ngrx/store';
// import { routerActions } from './actions';
import {ActionRecorderServices} from "../services/actionRecorder.service";
import {routerActions} from "@ngrx/router-store";

export interface RouterState {
  path: string;
}

export const initialState: RouterState = {
  path: ''
};

export const obj=new ActionRecorderServices();
export function routerReducer(state: RouterState = initialState, action: Action) {
  // console.log(action);

  switch (action.type) {
    case routerActions.UPDATE_LOCATION:
    {
      // console.log('UPDATE_LOCATION');
      obj.addAction(action);
      return action.payload;
    }

    case routerActions.GO :
    case routerActions.REPLACE:
    case routerActions.SEARCH:
    case routerActions.SHOW:
    case routerActions.BACK:
    case routerActions.FORWARD:
    {
      // console.log('go');
      obj.addAction(action);
      return state;
    }

    case "@ngrx/store/init":
    {
      obj.addAction(action);
      return state;
    }

    default:
    {
      return state;
    }

  }
}



