import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";
import {paths} from "../../environments/environment";
// import {  } from "./path-services";
// import { TEMPLATE_DETAIL_PATH_BY_LATLNG,} from "./path-services";

@Injectable()
export class TripTemplateServices {
  // protected basePath = 'http://35.189.162.187:8080/command';
  public defaultHeaders: Headers = new Headers();
  public tripTemplateStatus=new EventEmitter();
  public tripTemplateDetails=new EventEmitter();
  public createTripTemplate= new EventEmitter();
  public updateTripTemplate= new EventEmitter();
  public deleteTripTemplate= new EventEmitter();

  public token;

  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>,private localStorageService: LocalStorageService){
    //************ getTripStatus all list of trip  ***************************************************
    this.token = this.localStorageService.get("AUTH_TOKEN");

    this.tripTemplateStatus.subscribe((orgId)=> {
      let path =paths.TRIP_TEMPLATE_PATH+'/get';
      this.getAllTripTemplateStatus(path).subscribe(response =>{
        console.log(response);
        //todo
        if(response.status==200)
        this._store.dispatch({type: "TRIP_TEMPLATE_STATUS_LIST",payload:{error:'',data:response}});
      },err=>{
        this._store.dispatch({type: "TRIP_TEMPLATE_STATUS_LIST",payload:{error:err,data:null}});
        console.log(err);

      });
    });

    //@todo handle error cases
    this.tripTemplateDetails.subscribe((latlng)=> {
      let path=paths.TEMPLATE_DETAIL_PATH_BY_LATLNG;
      // console.log(path);
      this.getTripTemplateDetails(path,latlng).subscribe(response =>{
        console.log(response);
        console.log(response['status']);
        
        if(response['status']==200)
        this._store.dispatch({type: "TEMPLATE_DETAILS",payload:{error:'',data:response}});
        else
        this._store.dispatch({type: "TEMPLATE_DETAILS",payload:{error:'X',data:null}});
        
      },err=>{
        console.log(err);
      });
    });



    //@todo handle error cases
    this.createTripTemplate.subscribe((templateData)=> {
      let path=paths.TRIP_TEMPLATE_PATH+'/create';
      console.log(path);
      //@todo error handling
      this.createTripTemplateService(path,templateData).subscribe(response =>{
        console.log(response);
        this._store.dispatch({type: "CREATE_TRIP_TEMPLATE" ,payload:response});
      },err=>{
        console.log(err);
      });
    });

    this.updateTripTemplate.subscribe((templateData)=> {
      let path=paths.TRIP_TEMPLATE_PATH+'/update';
      // console.log(path);
      //@todo error handling
      this.doUpdateTripTemplate(path,templateData).subscribe(response =>{
        console.log(response);
        this._store.dispatch({type: "UPDATE_TRIP_TEMPLATE" ,payload:response});
      },err=>{
        console.log(err);
      });
    });
    this.deleteTripTemplate.subscribe((templateData)=> {
      let path=paths.TRIP_TEMPLATE_PATH+'/delete';
      // console.log(path);
      console.log(templateData);
      //@todo error handling
      this.doDeleteTripTemplate(path,templateData).subscribe(response =>{
        console.log(response);
        this._store.dispatch({type: "DELETE_TRIP_TEMPLATE" ,payload:response});
      },err=>{
        console.log(err);
      });
    });
  }// constructor close here

  // *********************** getAllTripStatus  **********************
  public getAllTripTemplateStatus(path){
     
     let headers = new Headers(this.defaultHeaders.toJSON());
     console.log(path);
    let bearerToken="Bearer "+this.token;
    console.log(bearerToken);
     headers.set('Content-Type', 'application/json');
     headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      console.log(response.status);
       console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }
    });
  }

  // *********************** getVehicleTripDetails of selected vehicle **********************
  public getTripTemplateDetails(path,latlng){

    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);
    // console.log(headers);
    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body:latlng,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      // console.log(response);
      // if(response.status==200){
        return response.json();
      // }else{
      //   console.log("not 200");
      //   return {error:'X'};
      // }
    });
  }

  // *********************** create Trip service  **********************
  public createTripTemplateService(path,templateData)
  {
    // let queryParameters = new URLSearchParams();
    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);
    console.log(templateData);
    console.log(headers);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: templateData,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
     console.log(response);
      return response.json();

    });
  }

  // *********************** update trip  **********************
  public doUpdateTripTemplate(path,updateTrip){

    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: updateTrip,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {

      return response.json();

    });
  }
  // *********************** update trip  **********************
  public doDeleteTripTemplate(path,deleteTrip){

        let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

        let requestOptions: RequestOptionsArgs = new RequestOptions({
          method: RequestMethod.Post,
          headers: headers,
          body: deleteTrip,
        });
        return this.http.request(path, requestOptions).map((response: Response) => {

          return response.json();

        });
      }
}// class close



