import { Component, OnInit } from '@angular/core';
import {TranslateService} from "../../locale/translate.service";
import {LocalStorageService} from "angular-2-local-storage";
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";

@Component({
  selector: 'app-setting',
  templateUrl: './setting.component.html',
  styleUrls: ['./setting.component.css']
})
export class SettingComponent  {
  public selectedLanguage='en';

  public supportedLangs: any=[ { name: 'English', code: 'en' },{ name: 'हिंदी', code: 'hi' }
    // { name: 'Español', code: 'es' },
    // { name: '华语', code: 'zh' },
  ];


  ngOnInit() {
    this.selectedLanguage= (this.localStorageService.get("LANGUAGE"))?(""+this.localStorageService.get("LANGUAGE")):'en';
    this.selectLang();
  }


  constructor(private _translate: TranslateService,private localStorageService: LocalStorageService,private _store:Store<any>) {
    this._store.dispatch({type: "TITLE",payload:'Settings'}); // set the header name

    console.log("setting constructor");
  }


  selectLang() {
    this._translate.use(this.selectedLanguage);
  }

  goToManageOrganisation(){
    this._store.dispatch(go(['/vtsdashboard/manageOrganisation']));
  }

}



