import {Inject, Injectable, Optional, EventEmitter}                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";

// import * as models                                           from './model/models';
// import { BASE_PATH, COLLECTION_FORMATS }                     from '../variables';
// import { Configuration }                                     from '../configuration';

@Injectable()
export class ActionRecorderServices {
  // protected basePath = 'http://track123.chickenkiller.com:8082/api';
  public defaultHeaders: Headers = new Headers();
  private actions:any=[];
  static instance: ActionRecorderServices;
  public UIActions=new EventEmitter();
  // private localStorageService: LocalStorageService;

  ngOnDestroy(){

  }

  constructor(){
    this.UIActions.subscribe(data=>{
      this.addAction(data);
    });

    return ActionRecorderServices.instance = ActionRecorderServices.instance || this;
  }

  addAction(action:any){
    // console.log(action);
    this.actions.push(action);
    // console.log(this.actions);
  }

}



