import {Component} from '@angular/core';
import { Router} from "@angular/router";
import {Store} from "@ngrx/store";
// import {AuthenticationServices} from "../../services/authentication.service";
import {LocalStorageService} from "angular-2-local-storage";
// import {go} from "../../app-states/actions";
import {go, show, RouterState} from "@ngrx/router-store";
import {LoginServices} from "../../services/login-services";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import {TranslateService} from "../../locale/translate.service";
import {ObservableMedia} from "@angular/flex-layout";

@Component({
  selector: 'app-user-login',
  templateUrl: './user-login.component.html',
  styleUrls: ['./user-login.component.css'],
})

export class UserLogin {
  public isSpinner=false;
  public mobileNo:any='';
  public countryCode:any='+91';
  public countryCodeList:any=[{countryID:'IND',countryCode:'+91'}];

  public selectedLanguage='en';
  public supportedLangs: any=[ { name: 'English', code: 'en' },{ name: 'हिंदी', code: 'hi' }
   // { name: 'Español', code: 'es' },
   // { name: '华语', code: 'zh' },
  ];



  ngOnInit() {
    this.selectedLanguage= (this.localStorageService.get("LANGUAGE"))?(""+this.localStorageService.get("LANGUAGE")):'en';
    this.selectLang();
  }

  // isCurrentLang(lang: string) {
  //   return lang === this._translate.currentLang;
  // }
  //

  selectLang() {
    this._translate.use(this.selectedLanguage);
  }

  // refreshText() {
  //   this.translatedText = this._translate.instant('hello world');
  // }



  constructor(public media: ObservableMedia,private _translate: TranslateService,public snackBar: MdSnackBar,private _store:Store<any>,private localStorageService: LocalStorageService,private _loginServices:LoginServices) {

    let token = this.localStorageService.get("TOKEN");
    console.log(token);
    if(token!=null && token !=undefined){
      // console.log("navigation");
      // if(media.isActive('gt-xs')){ // gt-xs means width  screen width > 600px
      //   console.log('desktop view');
      //   this._store.dispatch(go(['/dashboardDesktop']));
      //   return;
      // }else{
      //   this._store.dispatch(go(['/vtsdashboard']));
      // }


      this._store.dispatch(go(['/vtsdashboard']));
    }


    // this._store.select('otpGenaration').subscribe(value => {
    //   console.log(value);
    //
    //   if(value==null){
    //     return;
    //   }
    //
    //   if(value['error']=='X'){
    //     console.log(value['msg']);
    //     return;
    //   }
    //
    //   if(value['error']==''){
    //     this._store.dispatch(go(['/sendOTP']));
    //   }
    // });
  }

  sendOtp(){
    this._store.dispatch({type: "OTP_GENERATION",payload:null});
    if(this.fieldsValidation()){
      // this.isSpinner=true;
      // let parent = this;
      let user=this.mobileNo;
      // this.localStorageService.set("USER-MOB-NO",this.mobileNo);
      console.log(this.mobileNo);
      console.log(this.countryCode);
      this._loginServices.sendOTP.emit({mobileNo:this.mobileNo,countryCode:this.countryCode});
      // this.openSnackBar("OTP sent to your Mobile Number");
      this.localStorageService.set("USER-MOB-NO",this.mobileNo); // set mobile no in local storage
      this.localStorageService.set("COUNTRY-CODE",this.countryCode); // set mobile no in local storage
      this._store.dispatch(go(['/sendOTP']));
    }else{
      console.log('Required parameter not blank.');
      return;
    }
  }// login methods close


  fieldsValidation(){
    if(this.mobileNo==''  ||this.mobileNo==undefined){
      console.log("please fill mobile no");
      return false;
    }

    if(this.countryCode==''  ||this.countryCode==undefined){
      console.log("please fill country code");
      return false;
    }

    return true;
  }




  // openSnackBar(message: string) {
  //   this.snackBar.open(message, '', <MdSnackBarConfig>{duration :  3000});
  // }

}




