import {Component} from '@angular/core';
import { Router} from "@angular/router";
import {Store} from "@ngrx/store";

@Component({
  selector: 'app-statistics-mobile',
  templateUrl: './statistics-mobile.component.html',
  styleUrls: ['./statistics-mobile.component.css']
})
export class StatisticsMobileComponent  {

  public isSpinner=false;
  public isShowVehicle_0_4:boolean=false;
  public isShowVehicle_4_8:boolean=false;
  public isShowVehicle_8_12:boolean=false;
  public isShowVehicle_above_12:boolean=false;



  constructor(private _store:Store<any>) {
    this._store.dispatch({type: "TITLE",payload:'Statistics'});
  }
}


















