  constructor(private _store:Store<any>,public _services:Services, public alertsService:AlertService, private tripServices:TripServices,private localStorageService: LocalStorageService, public snackBar: MdSnackBar) {
    this._store.dispatch({type: "TITLE",payload:'Add Alerts'});// set the header name


    this.uuid="";

    this.unSub_PickedVehicles = _store.select('PickedVehicles').subscribe(value => {

        console.log(value);
        this.vehicleList = value;

        this.vehicles=[];
        for(let i=0;i<this.vehicleList.length;i++){
          this.vehicles.push(this.vehicleList[i]['uuid']);
        }

        console.log(this.vehicles);
    });

    this.unSub_customerDetails =  this._store.select('customerDetails').subscribe(value => {
      console.log("customer datails in alerts");
      console.log(value);
      if(value==null)
        return;

      // this.customerId=value['customer']['id'];
      this.customerId=value['customer']['orgId'];
      // this.customerMobile=value['customer']['mobileNumber'];
    });

    this.unSub_createdServiceResponse = _store.select('alertsCreatedServiceResponse').subscribe(value => {

      if(value==null)
      {
        return;
      }
      console.log('Create Alert Response');
      console.log(value);

      if(value['status']== 200)
      {
        if(this.forEdit==true){
          this.openSnackBar("Alert Updated Successfully!");
        }
        else{
          this.openSnackBar("Alert Created Successfully!");
        }
        this.goBack();
      }

      console.log(value);

    });



    this.unSub_PickedVehicles = _store.select('configAlertFieldLocation').subscribe(value => {


      console.log(value);
      this.filedLocationObj=value;
      if(this.filedLocationObj!=={})
      {
        this.showMapLocation=false;
      }

      console.log(this.filedLocationObj);
    });


    this.unSub_editSelecetdAlert = _store.select('selectedAlertForEdit').subscribe(value => {
      if(value==null)
      {
        return;
      }

      console.log(value);
      this.editAlert(value)
    });

    this.unSub_searchName = _store.select('searchLocationName').subscribe(value => {

      if(value == null) {
        return;
      }
      this.locationNameList = [];

      if(value['status']==200)
      {
        console.log(value);
        this.waitForLastResp = false;
        if(value['data'].length>0) {
          let locationList=[];
          for(let i=0 ;i<value['data'].length; i++)
          {
            let obj={
              'name':value['data'][i]['freightLocation']['name'],
              'locObj':value['data'][i]
            }
            locationList.push(obj);
          }
          this.locationNameList = locationList;

        }
      }

    });


    this.unSub_AlertsFieldDetails = _store.select('selecetdProximityLocation').subscribe(value => {
      if(value == null)
        return;
      console.log(value);
      this.proximityLocationName=value['name'];
      this.showProximityAddressList=false;
    });

    this.unSub_InsideLocation = _store.select('selectedInsideLocation').subscribe(value => {
      if(value == null)
        return;

      console.log(value);
      this.insideLocationName=value['name'];
      // this.insideLocationObj=value;
      this.showInsideAddressList=false;
    });

    // this.placesServices();

  }

