  saveVehicles()
  {
    if(this.selectedAction=="Email Notification")
    {
      this.notificationAction='EMAIL';
      this.receipient=this.emailId;
    }

    if(this.selectedAction=="SMS Notification")
    {
      this.notificationAction='SMS';
      this.receipient=this.mobileno;
    }

    if(this.snoozeTimeUnit == 'min')
    {
      this.finalSnoozeTime=this.snoozeTime*60*1000;
      console.log(this.finalSnoozeTime);
    }

    if(this.snoozeTimeUnit == 'hours')
    {
      this.finalSnoozeTime=this.snoozeTime*60*60*1000;
    }

    if(this.selectedEvent=="Power disconnected")
    {
      this.powerDisconnectAlert();
    }

    if(this.selectedEvent=="Periodic Alert")
    {
      let periodicAlertString="";
      let windowTimeStr ="";
      let periodicDistanceStr ="";
      let runningDurationStr ="";
      let stopDurationStr ="";
      let disconnectDurationStr ="";

      if(this.windowTimeDuration > 0) {
        let w=this.windowTimeDuration;
        windowTimeStr=" in last "+ w + " hours."
        this.finalWindowTimeDuration = this.windowTimeDuration * 60 * 60 * 1000;
      }

      if(this.periodicDistance > 0) {
        let d = this.periodicDistance;
        if (this.periodicDistanceUnit == 'km') {

          this.periodicDistance = this.periodicDistance * 1000;
        }

        if (this.periodicDistanceUnit == 'm') {
          this.periodicDistance = this.periodicDistance;
        }
          if (this.periodicDistanceOperator == 'GT') {
            periodicDistanceStr = ", Covered more than "
          }
          if (this.periodicDistanceOperator == 'LT') {
            periodicDistanceStr = ", Covered less than "
          }


          periodicDistanceStr = periodicDistanceStr + d + this.periodicDistanceUnit

      }

      if(this.runningDuration> 0)
      {
        if(this.runningDurationOperator== 'GT')
        {
          runningDurationStr=", Moved more than "
        }
        if(this.runningDurationOperator== 'LT')
        {
          runningDurationStr=", Moved less than "
        }

        runningDurationStr=runningDurationStr+ this.runningDuration + " hours"
        this.runningDuration=this.runningDuration*60*60*1000;
      }

      if(this.stopDuration> 0)
      {
        if(this.stopDurationOperator== 'GT')
        {
          stopDurationStr=", Stopped for more than "
        }
        if(this.stopDurationOperator== 'LT')
        {
          stopDurationStr=", Stopped for less than "
        }

        stopDurationStr=stopDurationStr+ this.stopDuration + " hours"
        this.stopDuration=this.stopDuration*60*60*1000;
      }


      if(this.disconnectDuration> 0)
      {
        disconnectDurationStr= ", was disconnected for more than "
        disconnectDurationStr=disconnectDurationStr+ this.disconnectDuration + " hours";
        this.disconnectDuration=this.disconnectDuration*60*60*1000;
      }

      if(periodicDistanceStr !== "")
      {
        periodicAlertString=periodicAlertString+periodicDistanceStr;
      }

      if(runningDurationStr !== "")
      {
        periodicAlertString=periodicAlertString+runningDurationStr;
      }

      if(stopDurationStr !== "")
      {
        periodicAlertString=periodicAlertString+stopDurationStr;
      }

      if(disconnectDurationStr !== "")
      {
        periodicAlertString=periodicAlertString+disconnectDurationStr;
      }

      periodicAlertString=periodicAlertString+windowTimeStr;

      console.log(periodicAlertString)

      this.periodicAlert(periodicAlertString);
    }

    if(this.selectedEvent== 'Vehicle Stop Event')
    {
      let stopAlertString= ", is Stopped ";
      let durationUnit=this.durationTimeUnit;
      let d= this.selecetdDurationTime;

      if(this.durationTimeUnit == 'Min')
      {
        this.selecetdDurationTime=this.selecetdDurationTime*60*1000;
      }

      if(this.durationTimeUnit == 'Hrs')
      {
        this.selecetdDurationTime=this.selecetdDurationTime*60*60*1000;
      }

      if(this.durationTimeUnit == 'Days')
      {
        this.selecetdDurationTime=this.selecetdDurationTime*24*60*60*1000;
      }

      if(this.locationProximity == 'Inside')
      {
        this.locationProximityOperator='IN';
      }

      if(this.insideLocationName!== "")
      {
        stopAlertString=stopAlertString+"at "+this.insideLocationName
      }

      stopAlertString=stopAlertString +", since "+d+durationUnit;

      // console.log(stopAlertString);
      this.stopAlert(stopAlertString);
    }
  }

