  setInsideLocation(value)
  {

    this.showInsideAddressList=false;
    // console.log("value clicked");
    // console.log(value);


    let obj={
      'detailsObj':value['locObj']
    }

    let boundary=value['locObj']['freightLocation']['geofence']['boundry'];
    let boundString="";
    for(let i=0; i<boundary.length; i++)
    {
      if(boundString ==""){
        boundString = boundString + value['name']+'@'+ boundary[i]['latitude']+'#'+boundary[i]['longitude'];
    }else {
        boundString = boundString + "##" + boundary[i]['latitude']+'#'+boundary[i]['longitude'];
      }
    }

    let locationBoundaryObj={
      "boundary"    : boundString,
      "valueType": 'LOCATION_GEOFENCE',
      "name": value['name']
    }

    this.insideLocationObj=locationBoundaryObj;
    this.insideLocationName=value['name'];

    console.log("+++++ insideLocationObj++++++ :");
    console.log(this.insideLocationObj);

  }

