import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";
import {paths} from "../../environments/environment";
// import {PATH,SIGNUP_PATH} from "./path-services";

@Injectable()
export class UserSignupServices {
  // protected basePath = 'http://35.189.162.187:8080/command';
  public defaultHeaders: Headers = new Headers();
  // private token:any='';
  // private unSub_token:any=null;
  public signupTokenValidation=new EventEmitter();
  public sendOTPSignUp= new EventEmitter();
  public signUpUser= new EventEmitter();

  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>,private localStorageService: LocalStorageService){
    this.defaultHeaders.append('Access-Control-Allow-Origin', '*');
    this.defaultHeaders.append('Access-Control-Allow-Methods', 'GET,POST, OPTIONS, PUT, PATCH, DELETE');

    //************ login  ***************************************************
    this.signupTokenValidation.subscribe((token)=> {
      const path =paths.SIGNUP_PATH+'/validate?token='+token;
      console.log(path);
      this.checkSignupTokenValidation(path).subscribe(response =>{
        console.log(response);
        // console.log(response['error']);

        let res=response;
        this._store.dispatch({type: "SIGNUP-TOKEN-VALIDATION", payload: {error:'',data:res}});
      },err=>{
        this._store.dispatch({type: "SIGNUP-TOKEN-VALIDATION",payload:{error:'X',errorMsg:err.json()}});
        // this.localStorageService.set("TOKEN",false);
        console.log(err);
        console.log(err.json());
      });
    });

    this.generateSignUpOtp();
    this.signUpNewUser();
  }// constructor close here

  // ***********************  token authorization**********************
  public checkSignupTokenValidation(path){

    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: Id,
    });


    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
        return response.json();
    });
  }


  // **************************** Generate OTP ****************************

  public generateSignUpOtp(){
    console.log('generateSignUpOtp')
    this.sendOTPSignUp.subscribe((obj)=> {
      const path = paths.PATH+':8094/signup/otp?mobileNumber='+obj;
      this.sendSignUpOTP(path).subscribe(response =>{
        console.log(response);
        console.log(response['error']);

        if(response['error']=='X'){
          this._store.dispatch({type: "SIGNUP_OTP_GENERATION",payload:{error:'X',mobNo:obj, msg:"OTP not send"}});
          return;
        }

        let res=response;
        this._store.dispatch({type: "SIGNUP_OTP_GENERATION",payload:{error:'',mobNo:obj,msg:res.success}});

      },err=>{
        if(err.status == 0){
          this._store.dispatch({type: "SIGNUP_OTP_GENERATION",payload:{error:'X',mobNo:obj, msg:'No Internet Connection'}});
        }else {
          this._store.dispatch({type: "SIGNUP_OTP_GENERATION",payload:{error:'X',mobNo:obj, msg:err.json().error}});
        }
        console.log(err);
        console.log(err.json());
      });
    });
  }

  public sendSignUpOTP(path){
    console.log(path);

    // let queryParameters = new URLSearchParams();
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');

    // console.log(headers);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: Id,
    });

    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      // note :- if otp send than it return status code 200 and if mobile no not register then it return 409 with http error
      console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }
    });
  }


  // **************************** Signup New User ****************************

  public signUpNewUser(){
    console.log('signUpNewUser')
    this.signUpUser.subscribe((data)=> {
      const path = paths.PATH+':8094/signup/user';
      this.sendSignUpData(path,data).subscribe(response =>{
        console.log(response);
        console.log(response['error']);

        if(response['error']=='X'){
          this._store.dispatch({type: "SIGNUP_NEW_USER",payload:{error:'X', msg:"Error while SignUp"}});
          return;
        }

        let res=response;
        this._store.dispatch({type: "SIGNUP_NEW_USER",payload:{error:'',data:res}});
      },err=>{
        if(err.status == 0){
          this._store.dispatch({type: "SIGNUP_NEW_USER",payload:{error:'X', errorMsg:""}});

        }else{
          this._store.dispatch({type: "SIGNUP_NEW_USER",payload:{error:'X', errorMsg:"error occur while fetching data"+err}});
        }
        console.log(err);
        console.log(err.json());
      });
    });
  }

public sendSignUpData(path,data){

  let headers = new Headers(this.defaultHeaders.toJSON());
  headers.set('Content-Type', 'application/json');

  let token=this.localStorageService.get('TOKEN');
  console.log('token');
  console.log(token);
  let bearerToken="Bearer "+token;
  headers.set('authorization', bearerToken);

  // console.log(headers);

  let requestOptions: RequestOptionsArgs = new RequestOptions({
    method: RequestMethod.Post,
    headers: headers,
    body:data ,
  });




  return this.http.request(path, requestOptions).map((response: Response) => {
    // console.log(response.status);
    // console.log(response);
    if(response.status==200){
      console.log("response.json():");
      console.log(response.json());
      return response.json();
    }else if(response.status==204){
      return {error:'N'};
    }else {
      return {error:'X'};
    }
  });
}

}// class close




