import { Component, OnInit, ViewChild} from '@angular/core';
import { MapsAPILoader, AgmMap, GoogleMapsAPIWrapper } from "@agm/core";
import * as mapTypes from '@agm/core/services/google-maps-types';
import {Store} from "@ngrx/store";
import {go, replace,back} from "@ngrx/router-store";
import {} from '@types/googlemaps';
import {Polygon} from "angular2-google-maps/esm/core/services/google-maps-types";
import poly = google.maps.geometry.poly;
import {geoCodeServices} from "../../services/geoCode-services";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import {TripServices} from "../../services/tripServices";
import {isNullOrUndefined} from "util";


@Component({
  selector: 'app-add-trip-location',
  templateUrl: './add-trip-location.component.html',
  styleUrls: ['./add-trip-location.component.css']
})
export class AddTripLocationComponent implements OnInit {

  @ViewChild(AgmMap) agmMap;

  public latitude: number = null;
  public longitude: number = null;
  public zoom: number =5;
  public radius: number =5;
  public isMarkerDragable: boolean=false;
  public isPolygonEditable: boolean=false;
  public polygon : any;
  public circle : any;
  public map: any;
  public mapType = 'roadmap';
  public markers :any= [];
  public locationAddress:string="";
  public locationNameObj:any;
  public unSub_formattedAddress=null;
  public unSub_freightDetailsLocation=null;
  public unSub_selectedTripAddressType=null;
  public _calledFrom:any;
  public isAddressSelected:boolean=false;
  public showSpinner:boolean=false;
  public fretDetails:any;
  public uuid=null;
  public name:string="";
  public freightObj:any={};

  ngOnDestroy() {
    this._store.dispatch({type: "IS_BACK",payload:{isBack:false,backToComponent:''}});

    if (typeof this.unSub_selectedTripAddressType != 'undefined' && this.unSub_selectedTripAddressType != null && this.unSub_selectedTripAddressType != undefined)
    {
      // console.log(" unSub_sourceAddress");
      this.unSub_selectedTripAddressType.unsubscribe();
    }
  }

  constructor(public mapApiWrapper: GoogleMapsAPIWrapper,public snackBar: MdSnackBar,private _store:Store<any>,public _geoServices:geoCodeServices,private tripServices:TripServices)
  {
    this.latitude = 21.15083373;
    this.longitude = 79.081145;
    this.zoom = 14;


    _store.dispatch({type: "IS_BACK",payload:{isBack:true,backToComponent:"addTrip"}});
   this.unSub_selectedTripAddressType = this._store.select('selectedTripAddressType').subscribe((value) => {

      console.log(value);
      if(value == null)
      {
        this._store.dispatch(go(['/vtsdashboard/addTrip']));
          return;
      }else{
        this._calledFrom = value['caller'];
        console.log(this._calledFrom);
        if (this._calledFrom == "origin") {
          this._store.dispatch({type: "TITLE",payload:'Pickup point'});
        }else if (this._calledFrom == "destination") {
          this._store.dispatch({type: "TITLE",payload:'Destination point'});
        }
      }
    //   console.log("this._calledFrom");
    //   console.log(this._calledFrom);
    });

  } // constructor close

  ngOnInit() {
    if(this._calledFrom != null) {
      console.log("oninit");
      this.agmMap.mapReady.subscribe(map => {
        this.map = map;
        // let points = this.getNPointsCircularPolygone({lat : this.latitude , lng : this.longitude} , 0.5 , 6);
        let points = [
          // {lat: 21.15083373, lng: 79.084234},
          // {lat: 21.1537726, lng: 79.09427709},
          // {lat: 21.1466711482, lng: 79.08886976}
        ];
        console.log(points);
        this.polygon = this.addPolygone(this.map, points, false, '#858585', '#353535');
        this.editLocation();
      });
    }
  }



  addPolygone(map,points,editable,fillColor,strokeColor)
{
    // Map Center
    // Styling & Controls
    let myPolygon = new google.maps.Polygon({
      paths: points,
      // draggable: true, // turn off if it gets annoying
      editable: editable,
      strokeColor: strokeColor,
      // strokeColor: '#353535',
      strokeOpacity: 0.8,
      strokeWeight: 2,
      // fillColor: '#858585',
      fillColor: fillColor,
      fillOpacity: 0.35
    });
    myPolygon.setMap(map);
    return myPolygon;
  }

  getNPointsCircularPolygone(center, dist, n) {
    var i = 0;
    var polygonePoint = [];
    for (i = 1; i <= n; i++) {
      polygonePoint.push(getPointAt(center, (360.0 / n) * (i), dist))
    }

    function toRAD(NUM) {
      return NUM * Math.PI / 180;
    }

    function toDeg(NUM) {
      return NUM * 180 / Math.PI;
    }

    function getPointAt(latlng, brng, dist) {
      // console.log(latlng);
      // console.log(latlng);
      dist = dist / 6371;
      brng = toRAD(brng);

      var lat1 = toRAD(latlng.latitude), lon1 = toRAD(latlng.longitude);
      var lat2 = Math.asin(Math.sin(lat1) * Math.cos(dist) + Math.cos(lat1) * Math.sin(dist) * Math.cos(brng));
      var lon2 = lon1 + Math.atan2(Math.sin(brng) * Math.sin(dist) * Math.cos(lat1), Math.cos(dist) - Math.sin(lat1) * Math.sin(lat2));

      if (isNaN(lat2) || isNaN(lon2)) return null;
      return new google.maps.LatLng(toDeg(lat2), toDeg(lon2))
    };
    return polygonePoint;
  }

  editLocation(){
    // this.isMarkerDragable=true;
    // this.polygon.setMap(null);//

    this.isPolygonEditable =true;
    this.polygon.setEditable(true);
    this.polygon.setOptions({fillColor: '#FF0000',strokeColor : '#FF0000'});
    this.placesServices();
  }

  back(){

    if ((this._calledFrom == "alertsProximityLocation")||(this._calledFrom == "alertsInsideLocation"))
    {
      let freight = {
        "name": "",
        "address": "",
        "detailsObj": "",
        "uuid": ""
      }
      this._store.dispatch({type: "ALERT_FIELD_LOCATION", payload: freight});
    }

    else
    this._store.dispatch(back());
  }

  save(name)
  {
    this.name=name.toString();
    console.log(this.name);
    // this.showSpinner=true;
    if(this.isAddressSelected == false || name == '')
    {
      // this.showSpinner=false;
      this.openSnackBar("Unable to save location.Please Try Again!")
      return;
    }

    let  vertices= this.polygon.getPath();
    console.log(vertices);
    let polygonPoints:any[]=[];
    // Iterate over the vertices.
    for (let i =0; i < vertices.getLength(); i++) {
      let xy = vertices.getAt(i);
      polygonPoints.push({latitude:xy.lat(), longitude:xy.lng()});
      console.log(xy.lat()+"     "+xy.lng());
    }

    // let locationObj:any={
    //   "name": name,
    //   "address":this.locationAddress,
    //   "polygon":polygonPoints
    // }

    let centerPointLat=0;
    let centerPointLng=0;


    for(let i=0; i<polygonPoints.length; i++)
    {
     centerPointLat=centerPointLat+polygonPoints[i].latitude;
     centerPointLng=centerPointLng+polygonPoints[i].longitude;
    }

    let centerPoint={
      "latitude":(centerPointLat/polygonPoints.length),
      "longitude":(centerPointLng/polygonPoints.length)
    };

    let fretObj={
      "freightLocation":{
        "address":this.locationAddress,
        "geofence":{
          "boundry":polygonPoints,
          "center":centerPoint
        },
        "material":[""],
        "name":name
      },
      "accessibilityLevel":null,
      "type":null,
      "addedByCustomer":"A001"
    };

    console.log("*************FRET OBJ****************");
    console.log(fretObj);

    this.freightObj=fretObj;

    this.tripServices.addTripFreightLocation.emit(fretObj);


    this._store.select('addFretTripDetails').subscribe(value => {
      console.log("addFretTripDetails");
      this.fretDetails=value;
      console.log(this.fretDetails);

      if(this.fretDetails ==null || this.fretDetails =="")
      {
        return;
      }
      else
      {
        console.log('uuid is here');
        console.log(this.fretDetails['uuid']);
        console.log(this.fretDetails);
        this.uuid=this.fretDetails['uuid'];
        this.showSpinner=false;

        let freight = {
          "name": this.name,
          "address": this.locationAddress,
          "detailsObj": this.freightObj,
          "uuid": this.uuid
        }

        if (this._calledFrom == "origin") {
          this._store.dispatch({type: "TRIP_SOURCE", payload: freight});
          this._store.dispatch(go(['/vtsdashboard/addTrip']));
        }

        if (this._calledFrom == "destination") {
          this._store.dispatch({type: "TRIP_DESTINATION", payload: freight});
          this._store.dispatch(go(['/vtsdashboard/addTrip']));
        }

        if (this._calledFrom == "alertsInsideLocation") {
          this._store.dispatch({type: "ALERT_FIELD_LOCATION", payload: freight});
          this._store.dispatch({type: "SELECTED_INSIDE_LOCATION", payload: freight});
          // this._store.dispatch(go(['/dashboard/addAlerts']));
        }

        if (this._calledFrom == "alertsProximityLocation") {
          this._store.dispatch({type: "ALERT_FIELD_LOCATION", payload: freight});
          this._store.dispatch({type: "SELECTED_PROXIMITY_LOCATION", payload: freight});
          // this._store.dispatch(go(['/dashboard/addAlerts']));
        }


      }
    });

   //TODO: Wait till response arrives with _id then navigate back to Add Trip Component
    // console.log(locationObj);
  }

  placesServices(){

    // Create the search box and link it to the UI element.
    let input:any = document.getElementById('pac-input');
    console.log(input);
    let searchBox = new google.maps.places.SearchBox(input);
    this.map.addListener('bounds_changed', ()=>{
      searchBox.setBounds(this.map.getBounds());
    });

    this.markers = [];
    // Listen for the event fired when the user selects a prediction and retrieve
    // more details for that place.
    searchBox.addListener('places_changed', () =>{
      var places = searchBox.getPlaces();

      if (places.length == 0) {
        return;
      }

      // Clear out the old markers.
      this.markers.forEach((marker)=> {
        marker.setMap(null);
      });
      this.markers = [];

      // For each place, get the icon, name and location.
      let bounds = new google.maps.LatLngBounds();
      places.forEach((place) =>{
        if (!place.geometry) {
          console.log("Returned place contains no geometry");
          return;
        }
        let icon = {
          url: place.icon,
          size: new google.maps.Size(71, 71),
          origin: new google.maps.Point(0, 0),
          anchor: new google.maps.Point(17, 34),
          scaledSize: new google.maps.Size(25, 25)
        };


        // Create a marker for each place.
        let currentMarker = new google.maps.Marker({
          // map: this.map,
          // icon: icon,
          title: place.formatted_address,
          position: place.geometry.location
        });

       console.log(place.formatted_address);
        this.markers.push(currentMarker);

        // ********** for show marker accoeding to screen ********
        if (place.geometry.viewport) {
          // Only geocodes have viewport.
          bounds.union(place.geometry.viewport);
        } else {
          bounds.extend(place.geometry.location);
        }
        // ****** for show marker accoeding to screen  ************
      });
      this.map.fitBounds(bounds);
    });
  }


  setNewLocation(selectedMarker){
    console.log(selectedMarker);
    this.latitude = selectedMarker.position.lat();
    this.longitude = selectedMarker.position.lng();
    let obj={latitude:this.latitude,longitude:this.longitude};
    console.log('Set New Location Obj');
    console.log(obj);

    // this._geoServices.getGeoCodeAddress.emit(obj);

    // this.polygon.setMap(null); // remove previous polygon
    let points = this.getNPointsCircularPolygone({latitude : this.latitude , longitude : this.longitude} , 0.5 , 5);
    this.polygon.setPath(points);
    //this.polygon == this.addPolygone(this.map,points,true,'#FF0000','#FF0000'); // set this to  selected polygon
    this.map.setZoom(14);
    this.map.setCenter(selectedMarker.getPosition());

    // **************************** clear all marker *******************
    for (let i = 0; i < this.markers.length; i++) {
      this.markers[i].setMap(null);
    }

    this.markers = [];

    this.locationAddress = selectedMarker.title;
    this.isAddressSelected = true;
    //   *********************************************************

    console.log(this.fretDetails);

  }


  drawCircle(radius) {
    this.radius = radius;
    // this.isCircle = true;
    if (this.circle != null)
      this.circle.setMap(null);

    if (this.polygon != null)
      this.polygon.setMap(null);

    this.circle = new google.maps.Circle({
      radius: radius,
      center: new google.maps.LatLng(this.latitude, this.longitude)
    })


    this.circle.setMap(this.map)
  }

  openSnackBar(message: string) {
    this.snackBar.open(message, '', <MdSnackBarConfig>{duration :  4000});
  }

}



