import {ActionReducer, Action, combineReducers} from '@ngrx/store';
import {ActionRecorderServices } from '../services/actionRecorder.service';

// export const LOGIN_DETAILS = 'LOGIN_DETAILS';
export const TOKEN = 'TOKEN';


var obj=new ActionRecorderServices();

export function token(state: any = null, action:Action) {

  switch (action.type) {
    case "TOKEN" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}


export function otpGenaration(state: any = null, action:Action) {

  switch (action.type) {
    case "OTP_GENERATION" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}


export function customerDetails(state: any = null, action:Action) {

  switch (action.type) {
    case "CUSTOMER_DETAILS" : {

      console.log("customer Details value in store");
      console.log(action.payload);
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}



