import {Component, OnDestroy} from '@angular/core';
import { Router} from "@angular/router";
import {Store} from "@ngrx/store";
// import {AuthenticationServices} from "../../services/authentication.service";
import {IMyOptions} from "mydatepicker";
// import {Services} from "../../../services/services";

import {go} from "@ngrx/router-store";
import {LocalStorageService} from "angular-2-local-storage";
import {TimerServices} from "../../../services/timer-services";
import { Angulartics2Mixpanel } from 'angulartics2/dist';
// import {SocketService} from "../../../services/socket-service";
import {browser} from "protractor";
import {Services} from "../../../services/services";
import {SocketService} from "../../../services/socket-service";
import {isNullOrUndefined} from "util";
import {config} from "../../../../environments/environment";
// import {SUSPENDED_TIME} from "../../../services/config-services";

@Component({
  selector: 'app-vehicle-list',
  templateUrl: './vehicle-list.component.html',
  styleUrls: ['./vehicle-list.component.css']
})

export class VehicleList implements OnDestroy{
  public isSpinner=true;
  public errorMsg='';
  public search='';
  public isSearch = false;
  public selectedVeh = ''; // for change selected card color
  public status='';
  public todeyDate:string='';
  public isShowLocationHistory:string='';
  public newDate:String="";
  // public view:String="list";
  public vehicleList=[];
  public configAlertsList=[];
  public unSub_vehicleList=null;
  public unsub_getAlertsNotification=null;
  public unSub_customerDetails=null;
  public unSub_vehicleStatusFilter=null;
  public unSub_searchFilter=null;
  public vehicleCount = {all:0,stopped:0,disconnected:0,moving:0};

  public view ='';
  // public unsub_interval:any=null;


  // date=new Date();
  // private selectedDate: Object = { date: { year: this.date.getFullYear(), month: this.date.getMonth(), day: this.date.getDate() } };
  // private dateFormat: IMyOptions = {
  //   dateFormat: 'dd-mm-yyyy',
  // };

  ngOnDestroy() {
    this.socketService.disconnectSocket();

    if (typeof this.unSub_vehicleList != 'undefined' && this.unSub_vehicleList != null && this.unSub_vehicleList != undefined){
      console.log("unsusVehicle list");
      this.unSub_vehicleList.unsubscribe();
    }

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined){
      console.log("unsusVehicle list");
      this.unSub_customerDetails.unsubscribe();
    }

    if (typeof this.unSub_vehicleStatusFilter != 'undefined' && this.unSub_vehicleStatusFilter != null && this.unSub_vehicleStatusFilter != undefined){
      console.log("unSub_vehicleStatusFilter");
      this.unSub_vehicleStatusFilter.unsubscribe();
    }

    if (typeof this.unSub_searchFilter != 'undefined' && this.unSub_searchFilter != null && this.unSub_searchFilter != undefined){
      console.log("unSub_searchFilter");
      this.unSub_searchFilter.unsubscribe();
    }


  }

  ngOnInit()
  {
    // let inter;
    // document.addEventListener('visibilitychange',  ()=> {
    //  if (document.hidden) {
    //     // stop running expensive task
    //     clearInterval(inter);
    //     console.log('not visible');
    //   } else {
    //     // page has focus, begin running task
    //     inter =   setInterval(()=> {
    //    console.log('visible');
    //     }, 100);
    //   }
    // });
  }

  constructor(private socketService:SocketService,private _store:Store<any>,private _services:Services,private localStorageService: LocalStorageService,
  public _angularitics:Angulartics2Mixpanel)
  {
    this._store.dispatch({type: "TITLE",payload:'Vehicle Dashboard'}); // set the header name
    console.log('emitting vehicleListTimer');

    this.view=document['view'];

    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      console.log(value);
      if(value==null){
        return;
      }

      this.socketService.getVehicleList();
      // console.log("value['orgId']");
      // console.log(value['customer']['orgId']);
      // this._services.getVehicleList.emit(value['customer']['orgId']);
    });

    // ***************************vehicle status filter store**********************************
    this.unSub_vehicleStatusFilter = _store.select('selectedFilterVehicleStatus').subscribe(value => {
      if(isNullOrUndefined(value)) {
        return;
      }
      else{
        console.log(value.toString());
        console.log('In vehicle list status filter:');
        this.status = value.toString();
      }
    });


    // *************************** search bar filter **********************************
    this.unSub_searchFilter = _store.select('vehicleListSearchFliter').subscribe(value => {
      if(isNullOrUndefined(value)) {
        return;
      }
      else{
        console.log(value.toString());
        console.log('In search filter:');
        this.search = value.toString();
      }

    });


      // ***************  vehicle list ************************************
      this.unSub_vehicleList = _store.select('vehicleListRes').subscribe(value => {
        // console.log(value);
        this.errorMsg='';
        if(value==null)
        {
          return;
        }

        if(value['error']==''){
          console.log(value);
          this.vehicleCount = {all:0,stopped:0,disconnected:0,moving:0};
          this.vehicleList = value['data'];
          this.vehicleCount['all'] = this.vehicleList.length;
          for(let i=0;i<this.vehicleList.length; i++)
          {
            this.vehicleList[i]['_duration'] = this.msToTime(this.vehicleList[i]['duration'] );
            if(this.vehicleList[i]['state'] == null || (this.vehicleList[i]['state'] == 'Disconnected' && this.vehicleList[i]['duration'] > config.SUSPENDED_TIME )){
              this.vehicleList[i]['state'] = "Suspended"
            }

            if(this.vehicleList[i]['state'] == 'Stopped'){
              this.vehicleCount['stopped'] = this.vehicleCount['stopped'] + 1;
            }

            if(this.vehicleList[i]['state'] == 'Disconnected'){
              this.vehicleCount['disconnected'] = this.vehicleCount['disconnected'] + 1;
            }

            if(this.vehicleList[i]['state'] == 'Moving'){
              this.vehicleCount['moving'] = this.vehicleCount['moving'] + 1;
            }

            if(this.vehicleList[i]['state'] == "Suspended"){
              this.vehicleCount['suspended'] = this.vehicleCount['suspended'] + 1;
            }
          }
          console.log(this.vehicleList);

          if(this.vehicleList.length==0){
            this.errorMsg="No records found."
          }
          // this.angularitics.eventTrack('Dashboard_VehicleList',  { category: 'VehicleList' });
          // console.log(this.vehicleList);
        }else {
          // console.log(value['errorMsg']);
          this.errorMsg=value['errorMsg'];
        }

        this.isSpinner=false;
      });

    this.unsub_getAlertsNotification=this._store.select('getAllAlertsNotification').subscribe(value => {

      console.log("Alerts in Notification :");
      console.log(value);

      if(value==null){
        return;
      }

      let notificationArray=[];

      notificationArray=value['data'];

      if((notificationArray!== null && notificationArray!= undefined)&& (notificationArray.length > 0)  )
      {

        notificationArray=notificationArray.reverse();
        this.configAlertsList=[];

        for(let i=0; i<notificationArray.length; i++)
        {
          this.configAlertsList.push(notificationArray[i]);
        }

      }
    });


  }// constructor close


  goToLoactionHisView(vehicle){

    if(this.view!='web') {
      console.log(vehicle);
      this._angularitics.eventTrack('VehicleList_ItemClicked_Mobile', vehicle);
      this._store.dispatch({type: "LOCATION_HISTORY", payload: null}); // clear previous records
      this.localStorageService.set('SELECTED-LOCATION-HIS-VEHICLE', vehicle); // put value in local storage
      this._store.dispatch(go(['/vtsdashboard/locationHistory']));
    }
    else{
          let show={
            "isShow":true
          }
          this._store.dispatch({type:"SHOW_LOCATION_HISTORY",payload:show});
          // this._store.dispatch({type: "LOCATION_HISTORY", payload: null}); // clear previous records
          // this._store.dispatch({type: "SELECTED_VEHICLE_LOCATION_HISTORY", payload: vehicle}); // clear previous records
          this._angularitics.eventTrack('VehicleList_ItemClicked_Web', vehicle);
           this._store.dispatch({type: "LOCATION_HISTORY", payload: null}); // clear previous records
           this.localStorageService.set('SELECTED-LOCATION-HIS-VEHICLE', vehicle); // put value in local storage

           this._store.dispatch({type: "GET_TIMELINE", payload: {callFrom:'web'}});
        }
  }

  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration='';

    if(days>0){
      totalDuration=days+"d ";
    }
    if(hours>0){
      totalDuration=totalDuration+hours+"hr ";
    }

    if(days >= 1){
      return totalDuration;
    }

    if(minutes>0){
     return totalDuration=totalDuration+minutes+"min";
    }
    if(seconds>0 && totalDuration == ''){
      totalDuration=totalDuration+seconds+" sec";
    }

    if(totalDuration==''){
      return '0 sec';
    }else {
      return totalDuration;
    }

  }//close methods msToTime



}




