import { Component, OnInit } from '@angular/core';
import {TripTemplateServices} from "../../../services/tripTemplate-services";
import { Store } from "@ngrx/store";
import {go} from "@ngrx/router-store";
import {LocalStorageService} from "angular-2-local-storage";


@Component({
  selector: 'app-trip-template-status',
  templateUrl: './trip-template-status.component.html',
  styleUrls: ['./trip-template-status.component.css']
})
export class TripTemplateStatusComponent implements OnInit {
  public orgId:any;
  public token:any;
  public view ='';
  public tripTemplateList:any[]=[];
  public isNotemplatesAvailable:boolean=false;
  public selectedTripTemplate:any;
  public unSub_templateList:any=null;
  public unSub_refreshTemplateList:any=null;
  constructor(private _store: Store<any>,public templateServices:TripTemplateServices,public localStorageService:LocalStorageService)
  {

    this.view=document['view'];
    this.token = this.localStorageService.get("AUTH_TOKEN");
    console.log(this.token);
    this.customerDetail(this.token);
    this._store.dispatch({ type: "TITLE", payload: 'Browse Trip Template'});
    this.orgId=this.localStorageService.get("ORGID");
    this.templateServices.tripTemplateStatus.emit();

    setTimeout(()=>{
      console.log("list refreshed after 1 sec");
      this.templateServices.tripTemplateStatus.emit();
    },1000);

    setTimeout(()=>{
      console.log("list refreshed after 5 sec");
      this.templateServices.tripTemplateStatus.emit();
    },5000);

    this.unSub_refreshTemplateList = _store.select('refreshTripTemplate').subscribe(value => {
      console.log(value);
      if(value==null){
        return;
      }
      if(value == 'Reload Template List') {
        this.templateServices.tripTemplateStatus.emit();
      }
    });

    this.unSub_templateList = this._store.select('tripTemplateList').subscribe(value => {
      // console.log(value);

      if (value == null) {
        return;
      }

      let obj :any=[];
      obj = value['data'];
      if (value['error'] == "") {

        if(obj['status']== 200)
        {
          this.tripTemplateList=obj['list'];
          console.log(this.tripTemplateList);

          if(this.tripTemplateList.length ==0)
          {
            this.isNotemplatesAvailable = true;
          }
        }
        // console.log(obj);
      }
    });
  }

  ngOnInit() {
  }

  customerDetail(token){
    let userDetails ={};
    let base64Url = token.split('.')[0];
    let base64 = base64Url.replace('-', '+').replace('_', '/');
    userDetails['userType']=JSON.parse(window.atob(base64));

    base64Url = token.split('.')[1];
    base64 = base64Url.replace('-', '+').replace('_', '/');
    userDetails['details']=JSON.parse(window.atob(base64));
    userDetails['details']['id'] =userDetails['details']['userId'];
    this.orgId=userDetails['details']['orgId'];
    this.localStorageService.set("ORGID",this.orgId);

    console.log(userDetails);
    return userDetails;
  }



  addTripTemplateView() {
    this._store.dispatch(go(['/vtsdashboard/addTripTemplate']));
  }
  goToTripTemplateEdit(template)
  {
    console.log(template);
    this.localStorageService.set("TRIP-TEMPLATE-DETAIL-OBJ",template);
    this._store.dispatch(go(['/vtsdashboard/editTripTemplate']));
  }
  goToManageOrganisationView(){
    this._store.dispatch(go(['/vtsdashboard/manageOrganisation']));
  }
}



