import {Component} from '@angular/core';
import { Router} from "@angular/router";
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import {AuthorizationServices} from "../../services/authorization-services";
import {LocalStorageService} from "angular-2-local-storage";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import {TranslateService} from "../../locale/translate.service";
import { Angulartics2Mixpanel } from 'angulartics2/dist';
import {MediaChange, ObservableMedia} from "@angular/flex-layout";
import {LogoutServices} from "../../services/logout-service";
import {AlertService} from "app/services/alert-services";

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
})
export class Dashboard {
  // ********** desktop view variables  ***************
  public selectedViewDesktop='vehicles';
  //***************************************************

  public isSpinner=false;
  public view ='mobile';
  public isReport=false;
  public showBack={isBack:false,backToComponent:'vehicleList'};
  public selectedView:string='dashboard';
  public title:any='';
  public isAddDeviceDialog:boolean=false;

  public isShowNotificationBadge:boolean=true;
  public allDeviceStatistics:any=[];
  public deviceStatistics:any=[];
  public allAlertsNotificationList:any[]=[];
  public startPageNo:number=1;
  public endPageNo:number=10;
  public totalPage:number=0;
  public customerName:string='';
  public customerMobile:string='';
  public customerEmail:string='';
  public selectedTableHeight:any='10';
  public isActivities:boolean=false;
  public unsub_token:any=null;
  public unsub_authorization:any=null;
  public unSub_customerDetails:any=null;
  public unsub_title:any=null;
  public unsub_isBack:any=null;
  public unsub_getAlertsNotification:any=null;

  ngOnDestroy(){
    if (typeof this.unsub_token != 'undefined' && this.unsub_token != null && this.unsub_token != undefined)
      this.unsub_token.unsubscribe();

    if (typeof this.unsub_authorization != 'undefined' && this.unsub_authorization != null && this.unsub_authorization != undefined)
      this.unsub_authorization.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();

    if (typeof this.unsub_title != 'undefined' && this.unsub_title != null && this.unsub_title != undefined)
      this.unsub_title.unsubscribe();

    if (typeof this.unsub_isBack != 'undefined' && this.unsub_isBack != null && this.unsub_isBack != undefined)
        this.unsub_isBack.unsubscribe();

    if (typeof this.unsub_getAlertsNotification != 'undefined' && this.unsub_getAlertsNotification != null && this.unsub_getAlertsNotification != undefined)
      this.unsub_getAlertsNotification.unsubscribe();
  }

  ngOnInit(){
      //*************** for localization ********************
      let langua= ""+this.localStorageService.get("LANGUAGE");
      this._translate.use(langua);
      //  ***************************************************

    this.unSub_customerDetails =  this._store.select('customerDetails').subscribe(value => {
      console.log("customer datails in dashboard");
      console.log(value);
      if(value==null)
        return;

      this.customerName=value['customer']['name'];
      this.customerMobile=value['customer']['mobileNumber'];
      this.customerEmail=value['customer']['email'];
      let  userStr=this.customerEmail+'('+this.customerMobile+')';

      this._angularitics.setUserPropertiesOnce({"customer":userStr});
      // this._angularitics.setUserPropertiesOnce(userStr);
      this._angularitics.setAlias({"customer":userStr});
      // this._angularitics.setAlias(userStr);
      this._angularitics.setUsername(userStr);
    });
  }

  constructor(private logoutService:LogoutServices,public media: ObservableMedia,private _translate: TranslateService,public snackBar: MdSnackBar,private _store:Store<any>,private authorization:AuthorizationServices,private localStorageService: LocalStorageService,private _router:Router, public _angularitics:Angulartics2Mixpanel, public alertServices:AlertService) {
    // navigate to desktop view
//    @todo
    // this.localStorageService.set("AUTH_TOKEN","eyJhbGciOiJIUzI1NiJ9.eyJpYXQiOjE1MTI2MzY1NDAsInVzZXJJZCI6IjdjYjQwYWQ2LWVmZjMtNGJiNS05MmE5LTA4ZDEwMWU4N2E2NSIsImVtYWlsIjoic3VuaWxAZnJldHJvbi5jb20iLCJtb2JpbGVOdW1iZXIiOiI5MDg1NDMyOTUxIiwib3JnSWQiOiI5ODJlNjczMy1lMTMyLTRhOWMtOTM4OC00ZmE0NzQ0Zjg3ODMiLCJuYW1lIjoiU3VuaWwiLCJvcmdUeXBlIjoiRkxFRVRfT1dORVIifQ.Bzr9EBP_kxALtDK_MD5mDPYvTh2_paOCwhUArfewtVI");
    if(media.isActive('gt-xs')){
      // console.log('desktop view');
      document['view']='web'; // web view
      this.view =document['view'];
      // this._store.dispatch(go(['/dashboardDesktop']));
      // return;
    }else {
      document['view']='mobile'; // mobile view
      this.view =document['view'];
    }

    //**************** for highlight menu button ****************
    this.setMenuButtonValue();
    //***********************************************************

    this.unsub_title = this._store.select('title').subscribe(value => {
      this.title=value;
      this.selectedView = this.title;
      // console.log( this.title);
    });


    // this.authorization.tokenAuthorization.emit(token);
    this.unsub_token=this._store.select('token').subscribe(value => {
      // console.log(this.localStorageService.get("TOKEN"));
      // let token = this.localStorageService.get("TOKEN");
      let token = this.localStorageService.get("AUTH_TOKEN");//check this
      console.log(token);
      //@todo clear this token when final test
      // token ='eyJhbGciOiJIUzI1NiJ9.eyJpYXQiOjE1MTI3MzI2NzUsInVzZXJJZCI6IjdjYjQwYWQ2LWVmZjMtNGJiNS05MmE5LTA4ZDEwMWU4N2E2NSIsImVtYWlsIjoic3VuaWxAZnJldHJvbi5jb20iLCJtb2JpbGVOdW1iZXIiOiI5MDg1NDMyOTUxIiwib3JnSWQiOiI5MWQ4MDcwMC0zZGM4LTQ3ZmEtYjk3Yy0xMWFhNzBkZGZmNTAiLCJuYW1lIjoiU3VuaWwiLCJvcmdUeXBlIjoiRkxFRVRfT1dORVIifQ.z3ksOzNtrg4pKuGxTcqk_zZ63iLVulxy9MOonMPEZOA';
      //close @todo clear this token when final test

      if(value==null && token==null)
      {
        // console.log("token not present in either localstorage or store");
        this.openSnackBar("you are currently logout, please login first");
        // this._store.dispatch(go(['/userLogin'])); ///// for check token
       this.logoutService.logout();

        return;
      }

      if(value!=null){ // if value exist in store
        if(value['error']==''){
          token=value['token'];
          console.log(token);
          this.authorization.tokenAuthorization.emit(token);
          console.log(token);
        }
      }else{ // if value not exist in store but in localstorage
        console.log(token);
        console.log(this._router.url);
        this.authorization.tokenAuthorization.emit(token);
      }
    });


    this.unsub_authorization=this._store.select('authorization').subscribe(value => {
      console.log(value);

      if(value==null){
        return;
      }

      if(value['error']=='X'){
        // console.log(value['msg']);// set error msg to user
        // console.log("you are not a valid user");// set error msg to user
        this.openSnackBar("you are not a valid user");
        // this._store.dispatch(go(['/userLogin']));
        console.log("Error in authorisation store ; error == 'X'");
        this.logoutService.logout();
        return;
      }

      if(value['error']=='' || value['valid']==true){
        // console.log("user authorized subscription");
        // console.log(value['valid']);
        let currentPath=this._router.url;
        if(currentPath==='/vtsdashboard'){
          this._angularitics.eventTrack('Vehicles_Dashboard_Opened',  { category: 'VehicleList' });
           this._store.dispatch(go(['/vtsdashboard/vehicleDashboard']));
        }
      }
    });

    //**************** for replace menu icon to back icon *********************88
    this.unsub_isBack=this._store.select('isBack').subscribe(value => {
      // console.log(value);

      if(value==null){
        return;
      }

      this.showBack.isBack = value['isBack'];
      this.showBack.backToComponent = value['backToComponent'];
    });
    //****************  close for replace menu icon to back icon  *******************88

    this.unsub_getAlertsNotification=this._store.select('getAllAlertsNotification').subscribe(value => {

      console.log("Alerts in Notification :");
      console.log(value);

      if(value==null){
        return;
      }

      let notificationArray=[];

      notificationArray=value['data'];

      if((notificationArray!== null && notificationArray!= undefined)&& (notificationArray.length > 0)  )
      {

        notificationArray=notificationArray.reverse();
        let len= (notificationArray.length >4)? 4: notificationArray.length;
        this.allAlertsNotificationList=[];

        for(let i=0; i<len; i++)
        {
          this.allAlertsNotificationList.push(notificationArray[i]);
        }

      }
    });

    this.alertServices.getAlertsNotification.emit();

  }// constructor close

  goBack(){
    this._store.dispatch(go(['/vtsdashboard/'+this.showBack.backToComponent]));
  }

  gotoDashboardView(){
    // this._router.navigate(['/vtsdashboard/vehicleList']);
    this._store.dispatch(go(['/vtsdashboard/vehicleDashboard']));
  }

  // gotoAllVehiclaeView(){
  //   // this._router.navigate(['/vtsdashboard/vehicleList']);
  //   this._store.dispatch(go(['/vtsdashboard/vehicleList']));
  // }

  gotoStatisticsView(){
    // this._router.navigate(['/vtsdashboard/statisticsMobile']);
    this._store.dispatch(go(['/vtsdashboard/statisticsMobile']));
  }

  // Report view
  gotoStoppagesReportView(){
    // console.log("hello stopage");
    this._angularitics.eventTrack('Stoppage_Report_Selected', { category: 'Reports'});
    // this._router.navigate(['/vtsdashboard/stoppageReport']);
    this._store.dispatch(go(['/vtsdashboard/stoppageReport']));
  }

  gotoOverspeedReportView(){
    this._angularitics.eventTrack('OverSpeed_Report_Selected', { category: 'Reports'});
    // this._router.navigate(['/vtsdashboard/overspeedReport']);
    this._store.dispatch(go(['/vtsdashboard/overspeedReport']));
  }

  gotoRunningReportView(){
    this._angularitics.eventTrack('Running_Report_Selected', { category: 'Reports'});
    // this._router.navigate(['/vtsdashboard/runningReport']);
    this._store.dispatch(go(['/vtsdashboard/runningReport']));
  }
  //  ******************

  gotoLocationHisView(){
    // this._router.navigate(['/vtsdashboard/locHisVehicleList']);
    this._store.dispatch(go(['/vtsdashboard/locHisVehicleList']));
  }

  logout(){
    this.logoutService.logout();
    // // console.log(localStorage)
    // // this.localStorageService.remove("TOKEN");
    // // this.localStorageService.remove("USER-MOB-NO");
    // // this.localStorageService.remove("COUNTRY-CODE");
    // // this.localStorageService.remove("VEHICLE_TIMELINE_REQUEST_OBJ");
    // // this.localStorageService.remove("VEHICLE-TRIP-DETAIL-OBJ"); //created in vehicle trip status details
    // // this.localStorageService.remove("SELECTED-LOCATION-HIS-VEHICLE");
    // // this._store.dispatch({type: "LOGOUT",payload:null});
    // this.localStorageService.clearAll();
    // this._store.dispatch({type: "CUSTOMER_DETAILS", payload:null});
    // this._store.dispatch({type: "CURRENT_TRIP_VEHICLE", payload:null});
    // this._store.dispatch({type: "AUTHORIZATION", payload: null});
    // this._store.dispatch({type: "VEHICLE_LIST",payload:null});
    // this._store.dispatch({type: "TOKEN",payload:null});
    // this._store.dispatch(go(['/userLogin']));
  }

  goToAddTripView(){
    this._store.dispatch(go(['/vtsdashboard/addTrip']));
  }

  goToVehicleStatusView(){
    this._store.dispatch(go(['/vtsdashboard/trip/status']));
  }


  goToselectVehicleTimeline(){
    this._angularitics.eventTrack('Vehicle_Timeline_View', { category: 'Reports'});
    this._store.dispatch(go(['/vtsdashboard/selectVehicleTimelineReport']));
  }

  goTosettingView(){
    this._store.dispatch(go(['/vtsdashboard/settingComponent']));
  }

  goToConfigureAlertsView(){
    this._store.dispatch(go(['/vtsdashboard/configureAlerts']));
  }

  goToVehicleSharingView(){
    this._store.dispatch(go(['/vtsdashboard/vehicleSharing']));
  }

  goToScheduleReportView(){
    this._store.dispatch(go(['/vtsdashboard/scheduleList']));
  }

  goToUserManagementView(){
    this._store.dispatch(go(['/vtsdashboard/userManagementConsole']));
  }

  goToManageOrganisationView(){
    this._store.dispatch(go(['/vtsdashboard/manageOrganisation']));
  }

  goToAlertsNotification()
  {
    this._store.dispatch(go(['/vtsdashboard/alertsNotification']));
  }

  // AddShipper(){
  //   this._store.dispatch(go(['/vtsdashboard/addShipperLocation']));
  // }

  openSnackBar(message: string) {
    this.snackBar.open(message, '', <MdSnackBarConfig>{duration :  3000});
  }


  setMenuButtonValue(){
    if(this._router.url =='/vtsdashboard/trip/status'){
      this.selectedViewDesktop ='Trip Status';
    }
    else if(this._router.url =='/vtsdashboard/stoppageReport' || this._router.url =='/vtsdashboard/runningReport' || this._router.url =='/vtsdashboard/selectVehicleTimelineReport' || this._router.url =='/vtsdashboard/vehicleTimelineReport'){
      this.selectedViewDesktop ='Reports';
    }
    else if(this._router.url =='/vtsdashboard/scheduleList'){
      this.selectedViewDesktop ='Schedule';
    }   else if(this._router.url =='/vtsdashboard/settingComponent'){
      this.selectedViewDesktop ='Setting';
    }
  }

}




