  constructor(private _store: Store<any>,public templateServices:TripTemplateServices, private tripServices: TripServices,public localStorageService:LocalStorageService,private eta_distance: ETA_DistanceService)
  {
     this.orgId=this.localStorageService.get("ORGID");
      this._store.dispatch({ type: "TITLE", payload: 'Edit Trip Template' });
      this.templateDetail=this.localStorageService.get("TRIP-TEMPLATE-DETAIL-OBJ");
      if(this.templateDetail!=null){
        this.originObj=this.templateDetail['origin'];
        this.destinObj=this.templateDetail['destination'];
        if(this.originObj!== null)
        {
          this.originName=this.originObj.name;
        }

        if(this.destinObj!== null)
        {
          this.destinationName=this.destinObj.name;
        }

        this.transitDays = this.templateDetail.transitDays;
        this.quantity = this.templateDetail.quantity;
        this.distance = this.templateDetail.distance,
        this.material =this.templateDetail.material;
        this.templateId=this.templateDetail['uuid'];
        console.log(this.distance);
        setTimeout(()=>{
          this.selectedTemplateLoc.emit({source:this.originObj['name'],destination:this.destinObj['name']});
        },500);
      }



    this.unSub_etaDistance = this._store.select("etaDistanceStore").subscribe(res => {
      if (res == null)
        return;

      console.log(res['value'])

      this.etaDistanceData = res['value'];
      if (this.etaDistanceData != null) {
        console.log(this.etaDistanceData[0]['status'])
        if (this.etaDistanceData[0]['status'] == 'OK') {
          this.setETA_Distance()
        }
        else {
          this.transitDays = 0;
          this.distance = "0";
        }
        console.log(this.etaDistanceData);
      }

    });

  }

