import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {Services} from "./services";
// import {PATH, GEOCODE_PATH,PATHv2} from "./path-services";
// import {KEY} from "./config-services";
import {LocalStorageService} from "angular-2-local-storage";
import {config, paths} from "../../environments/environment";

@Injectable()
export class VehicleReportServices {
  public defaultHeaders: Headers = new Headers();
  private geocodePath=paths.GEOCODE_PATH;
  private key:any=config.KEY;
  public getRunningReport=new EventEmitter();
  public getStoppageReport=new EventEmitter();
  public getOverspeedReport=new EventEmitter();

  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>,private _service:Services,private localStorageService: LocalStorageService){
    this.defaultHeaders.append('Access-Control-Allow-Origin', '*');
    this.defaultHeaders.append('Access-Control-Allow-Methods', 'GET,POST, OPTIONS, PUT, PATCH, DELETE');


    //************ get vehicle running report  ***************************************************
    this.getRunningReport.subscribe((obj)=> {
      console.log(obj);
      const path = paths.PATHv2+'/report/v2/running';
      console.log(path);

      this.getReport(path,obj).subscribe(response =>{
        console.log(response);
        // console.log(response['error']);
        if(response['error']=='X'){
          this._store.dispatch({type: "VEH_RUNNING_REPORT",payload:{error:'X', errorMsg:"No any record Found"}});
          return;
        }

        let res=response;

        if(response['error']=='') {

          this._store.dispatch({type: "VEH_RUNNING_REPORT", payload: {error: '', data: res}});
        }

        //******************** get formatted address from latitude and lognitude ***************888
        let geoCodeUrl='';
        for(let i=0;i<res.length;i++){
          res[i]['destinationAddress']={};
          res[i]['sourceAddress']={};
          if((res[i]['endPosition']['lat']!=undefined || res[i]['endPosition']['lat']!= null) && (res[i]['endPosition']['long']!=undefined || res[i]['endPosition']['long']!=null)) {
            geoCodeUrl = this.geocodePath+'/json?latlng=' + res[i]['endPosition']['lat'] + ',' + res[i]['endPosition']['long']+'&key='+this.key;;
            // console.log(geoCodeUrl);
            //********************************* call google geoCode api ************************************************
            _service.getVehicleAddress(geoCodeUrl).subscribe(response =>{
              // console.log(response['results'][0]);
              res[i]['destinationAddress']=response['results'][0];

              // this._store.dispatch({type: "VEH_RUNNING_REPORT",payload:{error:'',data:res}});
            },err=>{
              console.log(err);
            });
          }

          // ****************  get source address  ***********************
          if((res[i]['startPosition']['lat']!=undefined || res[i]['startPosition']['lat']!= null) && (res[i]['startPosition']['long']!=undefined || res[i]['startPosition']['long']!=null)) {
            geoCodeUrl = this.geocodePath+'/json?latlng=' + res[i]['startPosition']['lat'] + ',' + res[i]['startPosition']['long']+'&key='+this.key;;
            // console.log(geoCodeUrl);
            //********************************* call google geoCode api ************************************************
            _service.getVehicleAddress(geoCodeUrl).subscribe(response =>{
              res[i]['sourceAddress']=response['results'][0];

              // this._store.dispatch({type: "VEH_RUNNING_REPORT",payload:{error:'',data:res}});
            },err=>{
              console.log(err);
            });
          }
        }
        // *************************************************************************************

        this._store.dispatch({type: "VEH_RUNNING_REPORT",payload:{error:'',data:res}});
      },err=>{
        this._store.dispatch({type: "VEH_RUNNING_REPORT",payload:{error:'X', errorMsg:"error occur while fetching data"}});
        console.log(err);
        console.log(err.json());
      });
    });


    //************ get vehicle stoppage report  ***************************************************
    this.getStoppageReport.subscribe((obj)=> {
      console.log(obj);
      const path = paths.PATHv2+'/report/v2/stoppage';

      this.getReport(path,obj).subscribe(response =>{
        console.log(response);
        // console.log(response['error']);

        if(response['error']=='X'){
          this._store.dispatch({type: "VEH_STOPPAGE_REPORT",payload:{error:'X', errorMsg:"No any record Found"}});
          return;
        }

        let res=response;
        // //******************** get formatted address from latitude and lognitude ***************
        let geoCodeUrl='';
        for(let i=0;i<res.length;i++){
          for(let j=0;j<res[i]['value'].length;j++){
            res[i]['value'][j]['formattedSourceAddress']={};
            if((res[i]['value'][j]['startPosition']['lat']!=undefined || res[i]['value'][j]['startPosition']['lat']!= null) && (res[i]['value'][j]['startPosition']['long']!=undefined || res[i]['value'][j]['startPosition']['long']!=null)) {
              geoCodeUrl = this.geocodePath+'/json?latlng=' + res[i]['value'][j]['startPosition']['lat'] + ',' + res[i]['value'][j]['startPosition']['long']+'&key='+this.key;;
              // console.log(geoCodeUrl);
              //********************************* call google geoCode api ************************************************
              _service.getVehicleAddress(geoCodeUrl).subscribe(response => {
                // console.log(response['results'][0]);
                res[i]['value'][j]['formattedSourceAddress']= response['results'][0];
                // this._store.dispatch({type: "VEH_OVERSPEED_REPORT", payload: {error: '', data: res}});
              }, err=> {
                console.log(err);
              });
            }
          }
        }

        this._store.dispatch({type: "VEH_STOPPAGE_REPORT",payload:{error:'',data:res}});
      },err=>{
        this._store.dispatch({type: "VEH_STOPPAGE_REPORT",payload:{error:'X', errorMsg:"error occur while fetching data"}});
        console.log(err);
        console.log(err.json());
      });
    });


    //************ get vehicle Overspeed report  ***************************************************
    this.getOverspeedReport.subscribe((obj)=> {
      console.log(obj);
      const path = paths.PATHv2+'/report/v2/overspeed';

      this.getReport(path,obj).subscribe(response =>{
        console.log(response);
        console.log(response['error']);

        if(response['error']=='X'){
          this._store.dispatch({type: "VEH_OVERSPEED_REPORT",payload:{error:'X', errorMsg:"No any record Found"}});
          return;
        }

         // response[response.length-1]['value']=response[response.length-1]['value'].slice(0,3); //temporary data
        let res=response;

        // //******************** get formatted address from latitude and lognitude ***************
        let geoCodeUrl='';
        for(let i=0;i<res.length;i++){
          for(let j=0;j<res[i]['value'].length;j++){
          res[i]['value'][j]['formattedSourceAddress']={};
          if((res[i]['value'][j]['startPosition']['lat']!=undefined || res[i]['value'][j]['startPosition']['lat']!= null) && (res[i]['value'][j]['startPosition']['long']!=undefined || res[i]['value'][j]['startPosition']['long']!=null)) {
            geoCodeUrl = this.geocodePath+'/json?latlng=' + res[i]['value'][j]['startPosition']['lat'] + ',' + res[i]['value'][j]['startPosition']['long']+'&key='+this.key;;
            // console.log(geoCodeUrl);
            //********************************* call google geoCode api ************************************************
            _service.getVehicleAddress(geoCodeUrl).subscribe(response => {
              // console.log(response['results'][0]);
              res[i]['value'][j]['formattedSourceAddress']= response['results'][0];
              // this._store.dispatch({type: "VEH_OVERSPEED_REPORT", payload: {error: '', data: res}});
            }, err=> {
              console.log(err);
            });
          }
          }
        }

        this._store.dispatch({type: "VEH_OVERSPEED_REPORT",payload:{error:'',data:res}});
      },err=>{
        this._store.dispatch({type: "VEH_OVERSPEED_REPORT",payload:{error:'X', errorMsg:"error occur while fetching data"}});
        console.log(err);
        console.log(err.json());
      });
    });



  }// constructor close here


// ***********************  get recordes(ruunning/stoppage) **********************
  public getReport(path,obj){
    // let queryParameters = new URLSearchParams();
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');

    console.log(headers);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body:obj
    });

    let token=this.localStorageService.get('TOKEN');

    let bearerToken="Bearer "+token;
    headers.set('authorization', bearerToken);

    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }
    });
  }
}// class close




