import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {Services} from "../../services/services";
import {ActivatedRoute} from "@angular/router";
import {go} from "@ngrx/router-store";
import {geoCodeServices} from "../../services/geoCode-services";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import {MapsAPILoader} from "@agm/core";

@Component({
  selector: 'app-vehicle-activity',
  templateUrl: './vehicle-activity.component.html',
  styleUrls: ['./vehicle-activity.component.css']
})
export class VehicleActivity implements OnInit {
  public isEditDeliveryPointDialog =false;
  public currentVehicle:any;
  public vehicleState:any;
  public  selectedDeliveryPoint='delhi new road';
  public  selectedPickupPoint='mc Donald ,noida sec 15';
  latitude =  21.149633;
  longitude = 79.081145;
  formated_Address = null;
  private unSub_formattedAddress=null;
  private unSub_currentVehicle=null;

  ngOnDestroy() {
    if (typeof this.unSub_formattedAddress != 'undefined' && this.unSub_formattedAddress != null && this.unSub_formattedAddress != undefined)
    {
      console.log(" unSub_formattedAddress");
      this.unSub_formattedAddress.unsubscribe();
    }
    // if (typeof this.unSub_currentVehicle != 'undefined' && this.unSub_currentVehicle != null && this.unSub_currentVehicle != undefined)
    // {
    //   console.log(" unSub_currentVehicle");
    //   this.unSub_currentVehicle.unsubscribe();
    // }
  }

  constructor(private mapsAPILoader: MapsAPILoader,public snackBar: MdSnackBar,private _store:Store<any>,private _services:Services,private route: ActivatedRoute,private _geoServices:geoCodeServices) {
    route.queryParams.subscribe(params => {
      this.currentVehicle = params['vehicle'];
      this.vehicleState = params['state'];
      console.log(this.currentVehicle);
      console.log(this.vehicleState);
    });

   // this.unSub_currentVehicle = _store.select('currentVehicle').subscribe(value => {
   //    this.currentVehicle = value;
   //    console.log(this.currentVehicle);
   //  });

    this.unSub_formattedAddress = _store.select('formattedAddress').subscribe(value => {
      if(value == null)
        return;

      let obj:any = value;
      console.log(obj);
      if(obj['error'] == ''){
        if(obj['address']['status'] == 'OK') { // if address gets successfully
             console.log(obj['address'].results[0].formatted_address);
          this.formated_Address = obj['address'].results[0].formatted_address;
        }else{ // if address not define or not return by google map (when pointing marker out of land)
          this.openSnackBar(obj['address']['status']);
          this.formated_Address =null;
        }
      }else{
        if(obj['errorMsgObj']['status']== 0){  // if no internet connection
          this.openSnackBar("No Network Connection");
        }else{ // if any error occur
          this.openSnackBar(obj['error']['errorMsgObj']);
        }
        this.formated_Address =null;
        // console.log(obj['errorMsgObj']);
      }
    });
  }

  ngOnInit() {

  }

  back(){
    this._store.dispatch(go(['dashboard/selectedVehicle']));
  }

// when maker dragEnd then this method called
  markerDragPosition(event){
    this.formated_Address ='';
    console.log(event);
      this._geoServices.getGeoCodeAddress.emit({lat:event.coords.lat,lng:event.coords.lng});
  }

  openSnackBar(message: string) {
    this.snackBar.open(message, 'OK', <MdSnackBarConfig>{duration : 4000});
  }

  changePickupLocation(pickupPoint){
    this.selectedPickupPoint =pickupPoint;
  }

  changeDeliveryLocation(deliveryPoint){
      this.selectedDeliveryPoint =deliveryPoint;
  }

  previousActivity(){

  }

  nextActivity(){

  }

}// class close



