import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import {VehicleManagementService} from "../../../services/vehicle-management-services";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import {LocalStorageService} from "angular-2-local-storage";

@Component({
  selector: 'app-grouped-vehicles-list',
  templateUrl: './grouped-vehicles-list.component.html',
  styleUrls: ['./grouped-vehicles-list.component.css']
})
export class GroupedVehiclesListComponent implements OnInit {

  public view = '';
  public orgId = '';
  public groupId:string = '';
  public isShowConfirmDelete: boolean = false;
  public selectedGroup: any = {};
  public vehiclesByGroupList: any = [];


  private unSub_SelectedGroup: any = null;
  private unSub_DeleteGroupResponse: any = null;
  private unSub_vehiclesByGroupList: any = null;
  private unSub_customerDetails: any = null;

  // public allVehicleGroupList:any=[];


  constructor(private _store: Store<any>, public vehicleMgmntService: VehicleManagementService,public snackBar: MdSnackBar,private localStorageService: LocalStorageService) {
    this.view = document['view'];

    // this.vehicleMgmntService.getAllVehicleGroupsList.emit(this.orgId);

    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      console.log(value);
      if(value==null){
        return;
      }

      this.orgId=value['customer']['orgId'];

    });

    this.unSub_SelectedGroup = _store.select('selectedVehicleGroupForEdit').subscribe(value => {

      if (value == null) {
        return;
      }
      console.log('selected Vehicle Group For Edit in component');
      console.log(value);
      this.selectedGroup = value;
      this.groupId=value['uuid'];
      // this.groupId='62313ad5-cdff-4a41-9e02-254b62bc7827';
      this.vehicleMgmntService.getAllVehiclesByGroup.emit(this.groupId);
    });


    this.unSub_DeleteGroupResponse = _store.select('deleteGroupResponse').subscribe(value => {

      if (value == null) {
        return;
      }
      console.log('Delete group response:');
      console.log(value);

      this.openSnackBar("Group Deleted Successfully !!");

      this.localStorageService.set('LAST_CALLED_FROM_ORG_MANAGEMENT','vehicle Group');
      this._store.dispatch(go(['/vtsdashboard/manageVehicles']));
    });

    this.unSub_vehiclesByGroupList = _store.select('getVehiclesByGroup').subscribe(value => {

      if (value == null) {
        return;
      }

      if(value['status'] == 200)
      console.log('Vehicles by group list');
      console.log(value);

      this.vehiclesByGroupList=value['list'];

    });

  }

  ngOnDestroy() {
    if (typeof this.unSub_SelectedGroup != 'undefined' && this.unSub_SelectedGroup != null && this.unSub_SelectedGroup != undefined)
      this.unSub_SelectedGroup.unsubscribe();

    if (typeof this.unSub_DeleteGroupResponse != 'undefined' && this.unSub_DeleteGroupResponse != null && this.unSub_DeleteGroupResponse != undefined)
      this.unSub_DeleteGroupResponse.unsubscribe();

    if (typeof this.unSub_vehiclesByGroupList != 'undefined' && this.unSub_vehiclesByGroupList != null && this.unSub_vehiclesByGroupList != undefined)
      this.unSub_vehiclesByGroupList.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();
  }



  ngOnInit() {
  }

  goToManageVehicles()
  {
    this._store.dispatch(go(['/vtsdashboard/manageVehicles']));
  }

  goToEditVehicles(vehicleObj)
  {
    this._store.dispatch({type: "CALLED_EDIT_VEHICLE_SOURCE",payload: "GroupedVehicleList"});
    this._store.dispatch({type: "SELECTED_VEHICLE_FOR_EDIT",payload: vehicleObj});
    this._store.dispatch(go(['/vtsdashboard/editVehicles']));
  }

  showAddNewGroup()
  {

  }

  showDeleteGroupConfirmation()
  {
    this.isShowConfirmDelete=true;
  }

  deleteSelecetedGroup()
  {
    console.log("selected group for DELETE ==> :");
    console.log(this.selectedGroup);
    this.isShowConfirmDelete=false;
    this.vehicleMgmntService.deleteGroup.emit(this.selectedGroup);
  }

  openSnackBar(message: string) {
    this.snackBar.open(message , '', <MdSnackBarConfig>{duration :  3000});
  }

}



