import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";

// import { ETA_PATH } from "./path-services";
import {config, paths} from "../../environments/environment";

// import {KEY} from "./config-services"

@Injectable()
export class ETA_DistanceService {
  public defaultHeaders: Headers = new Headers();
  private token:any='';
  public res : any = null;

  public isEnableETA = config.IS_ENABLE_ETA;
  public getEtaDistance = new EventEmitter();
  public getEtaDistanceList = new EventEmitter();


  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>){
    this.getEtaDistance.subscribe((value)=>{
      // if production mode is on then distance service calculated
      if(this.isEnableETA ==false){
        _store.dispatch({type :"ETA_DISTANCE" ,payload : null});
        return;
      }
      console.log(value);
      let path = paths.ETA_PATH+"origins=" + value['oLat']+","+value['oLng']+"&destinations="+value['dLat']+','+value['dLng']+"&key=AIzaSyAiKyto-33wx4RvJw7kplHqSdyacUseGZw";

     this.etaDistanceCalaculater(path).subscribe(response=>{
       // console.log(response["rows"]);
        _store.dispatch({type :"ETA_DISTANCE" ,payload : { value:response["rows"][0]["elements"]}})
      },err=>{
       console.log(err);
     });

    // }
   });

   this.getEtaDistanceList.subscribe((value)=>{
     // if production mode is on then distance service calculated
     if(this.isEnableETA ==false){
       console.log(this.isEnableETA + "    **************** test mode ***********");
       _store.dispatch({type :"ETA_DISTANCE_LIST" ,payload : null});
       return;
     }
     console.log("*************");
     console.log(this.isEnableETA +"     prod mode *************");

    // console.log(value);
    for(let i=0; i<value.length; i++) {
      if(value[i]['oLat']!=null && value[i]['oLng']!=null) {
        let path = paths.ETA_PATH +"origins=" + value[i]['oLat']+","+value[i]['oLng']+"&destinations="+value[i]['dLat']+','+value[i]['dLng']+"&key=AIzaSyAiKyto-33wx4RvJw7kplHqSdyacUseGZw";

        this.etaDistanceCalaculater(path).subscribe(response=>{
          // console.log(response["rows"]);
          _store.dispatch({type :"ETA_DISTANCE_LIST" ,payload : {index : value[i]['index'], value:response["rows"][0]["elements"]}})
        },err=>{
          console.log(err);
        });
      }
    }
  });
  }// constructor close here

  //********************* send Request *****/
  public etaDistanceCalaculater(path) {
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');

    // console.log(headers);
    let requestOptions : RequestOptionsArgs = new RequestOptions({
      method : RequestMethod.Get,
      headers: headers,
    });

    // console.log(this.http);
    return this.http.request(path,requestOptions).map((response : Response)=> {
      // console.log(response);
      if(response.status == 200)
       return response.json();
    })
  }

}// class close



