import { Component, OnInit } from '@angular/core';
import { Router} from "@angular/router";
import {Store} from "@ngrx/store";
import {Services} from "../../services/services";

@Component({
  selector: 'app-gps-disconnected',
  templateUrl: './gps-disconnected.component.html',
  styleUrls: ['./gps-disconnected.component.css']
})
export class GpsDisconnectedComponent implements OnInit {

  public lat: number = 28.4504417;
  public lng: number = 77.0705279;
  public lat1: number = 28.248635;
  public lng1: number = 77.070920;


  constructor(private _store:Store<any>,private _router:Router,_services:Services) {
    this._store.dispatch({type: "TITLE",payload:'GPS Disconnected'});
  }

  ngOnInit() {
  }

}



