import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {Services} from "./services";
// import {PATH, GEOCODE_PATH, PATHv2, CONFIGURE_ALERTS_PATH} from "./path-services";
// import {KEY} from "./config-services";
import {LocalStorageService} from "angular-2-local-storage";
import {paths} from "../../environments/environment";


@Injectable()
export class AlertService {
  // protected basePath = 'http://35.189.162.187:8080/command';
  public defaultHeaders: Headers = new Headers();
  public createPowerDisconnectAlert=new EventEmitter();
  public editPowerDisconnectAlert=new EventEmitter();
  public editStopAlert=new EventEmitter();
  public editPeriodicAlert=new EventEmitter();
  public createPeriodicAlert=new EventEmitter();
  public createStopAlert=new EventEmitter();
  public getAllAlertsList=new EventEmitter();
  public deleteSelectedAlert=new EventEmitter();
  public getAlertsNotification=new EventEmitter();
  public token:any;

  ngOnDestroy() {

  }


  constructor(protected http: Http,private _store:Store<any>,private localStorageService: LocalStorageService)
  {
    this.token=this.localStorageService.get("AUTH_TOKEN");


    //@todo handle error cases
    this.createPowerDisconnectAlert.subscribe((PowDisconAlertObj)=> {
      let path=paths.CONFIGURE_ALERTS_PATH+"/create";
      console.log(path);
      console.log(PowDisconAlertObj);
      this.createAlert(PowDisconAlertObj,path).subscribe(response =>{
        console.log(response);
        this._store.dispatch({type: "ALERT_CREATED_SERVICE_RESPONSE" ,payload:response});
      },err=>{
        console.log(err);
      });
    });



    this.createPeriodicAlert.subscribe((PeriodicAlertObj)=> {
      let path=paths.CONFIGURE_ALERTS_PATH+"/create";
      console.log(path);
      console.log(PeriodicAlertObj);
      this.createAlert(PeriodicAlertObj,path).subscribe(response =>{
        console.log(response);
        this._store.dispatch({type: "ALERT_CREATED_SERVICE_RESPONSE" ,payload:response});
      },err=>{
        console.log(err);
      });
    });

    this.deleteSelectedAlert.subscribe((deleteAlertObj)=> {
      let path=paths.CONFIGURE_ALERTS_PATH+"/delete";
      console.log(path);
      console.log(deleteAlertObj);
      this.deleteAlert(deleteAlertObj,path).subscribe(response =>{
        console.log(response);
        this._store.dispatch({type: "ALERT_DELETE_SERVICE_RESPONSE" ,payload:response});
      },err=>{
        console.log(err);
      });
    });

    this.createStopAlert.subscribe((StopAlertObj)=> {
      let path=paths.CONFIGURE_ALERTS_PATH+"/create";
      console.log(path);
      console.log(StopAlertObj);
      this.createAlert(StopAlertObj,path).subscribe(response =>{
        console.log(response);
        this._store.dispatch({type: "ALERT_CREATED_SERVICE_RESPONSE" ,payload:response});
      },err=>{
        console.log(err);
      });
    });

    this.editPowerDisconnectAlert.subscribe((PowDisconAlertObj)=> {
      let path=paths.CONFIGURE_ALERTS_PATH+"/update";
      console.log(path);
      console.log(PowDisconAlertObj);
      this.updateAlert(PowDisconAlertObj,path).subscribe(response =>{
        console.log(response);
        this._store.dispatch({type: "ALERT_CREATED_SERVICE_RESPONSE" ,payload:response});
      },err=>{
        console.log(err);
      });
    });


    this.editStopAlert.subscribe((StopAlertObj)=> {
      let path=paths.CONFIGURE_ALERTS_PATH+"/update";
      console.log(path);
      console.log(StopAlertObj);
      this.updateAlert(StopAlertObj,path).subscribe(response =>{
        console.log(response);
        this._store.dispatch({type: "ALERT_CREATED_SERVICE_RESPONSE" ,payload:response});
      },err=>{
        console.log(err);
      });
    });


    this.editPeriodicAlert.subscribe((PeriodicAlertObj)=> {
      let path=paths.CONFIGURE_ALERTS_PATH+"/update";
      console.log(path);
      console.log(PeriodicAlertObj);
      this.updateAlert(PeriodicAlertObj,path).subscribe(response =>{
        console.log(response);
        this._store.dispatch({type: "ALERT_CREATED_SERVICE_RESPONSE" ,payload:response});
      },err=>{
        console.log(err);
      });
    });


    //@todo handle error cases
    this.getAllAlertsList.subscribe((customerId)=> {
      // let path=paths.CONFIGURE_ALERTS_PATH+"/all?"+customerId;
      let path=paths.CONFIGURE_ALERTS_PATH+"/all/organisation";
      console.log(path);
      this.getAllAlerts(path).subscribe(response =>{
        console.log(response);
        this._store.dispatch({type: "CONFIGURED_ALERTS_LIST" ,payload:response});
      },err=>{
        console.log(err);
      });
    });

    this.getAlertsNotification.subscribe(() => {
      let path= paths.CONFIGURE_ALERTS_PATH+"/incidents";
      console.log(path);
      this.getAllAlertNotification(path).subscribe(response =>{
        console.log(response);
        this._store.dispatch({type: "ALL_ALERTS_NOTIFICATION" ,payload:response});
      },err=>{
        console.log(err);
      });
    });


  }// constructor close here

  public createAlert(PowDisconAlertObj,path){
    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: PowDisconAlertObj,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if(response.status ==200) {
        return response.json();
      }

    });
  }


  public updateAlert(PowDisconAlertObj,path){
    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: PowDisconAlertObj,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if(response.status ==200) {
        return response.json();
      }

    });
  }



  public deleteAlert(deleteAlertObj,path){
    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: deleteAlertObj,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if(response.status ==200) {
        return response.json();
      }

    });
  }



  public getAllAlerts(path){
    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if(response.status ==200) {
        return response.json();
      }

    });
  }


  public getAllAlertNotification(path){
    let headers = new Headers(this.defaultHeaders.toJSON());
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if(response.status ==200) {
        return response.json();
      }

    });
  }
}



