

import {Component, Injectable, Input,ViewChild} from '@angular/core';
import { Router} from "@angular/router";
import {Store} from "@ngrx/store";
import {Services} from "../../services/services";
import {go,back} from "@ngrx/router-store";
import {LocalStorageService} from "angular-2-local-storage";
import * as mapTypes from '@agm/core/services/google-maps-types';
import {Polyline} from '@agm/core/services/google-maps-types';
import {PolylineOptions} from '@agm/core/services/google-maps-types';
import {MapsAPILoader} from '@agm/core/services/maps-api-loader/maps-api-loader';
