declare var MyPolylineUtil : any;

// import * as polylineDecoder from 'decode-google-map-polyline';

import {} from '@types/googlemaps';
import {AgmMap} from "@agm/core";
import {IMyDrpOptions, IMyDateRangeModel, IMyCalendarViewChanged, IMyInputFocusBlur} from "mydaterangepicker";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import {config} from "../../../environments/environment";
