@Component({
  selector: 'app-vehicle-timeline-report',
  templateUrl: './vehicle-timeline-report.component.html',
  styleUrls: ['./vehicle-timeline-report.component.css']
})
export class VehicleTimelineReport {
  @ViewChild(AgmMap) agmMap;

  public view='';
  public isSpinner=false;
  public errorMsg='';
  title: string = 'maps';
  mapHeight='0px';
  lat = null;
  long = null;
  zoom :number = 10;
  isDrag = false;
  mapType = 'roadmap';
  public startOffsetHeight=0;
  public selectedVehicle={};
  public dateList:any=[];
  public previousOffset=0;
  public divHeight='50%';
  public isFullScreen:boolean=false;
  public showFilterDialogue:boolean=false;
  public timeDurationUnit="Min";
  public timeDuration=5;
  public locationHislist:any=[];
  public unSub_locationHistory:any=null;
  public isFilterStatusSelected:boolean;
  public filterStatusList: any=[];
  public filterDuration=0;
  public startFilterTime=0;
  public endFilterTime=0;
  public polyLinePosList:any=[];
  public polyLineStoppedArray:any=[];
  public polyLineMovingArray:any=[];
  public stoppedPolyLineArray:any=[];
  public polyLineOverspeedArray:any=[];
  public decodedPolyline:any=[];
  public _map:Promise<mapTypes.GoogleMap>;
  private _mapResolver: (value?: mapTypes.GoogleMap) => void;
  public chkStopped:boolean=true;
  public chkOverspeed:boolean=true;
  public chkMoving:boolean=true;
  public chkDisconnected:boolean=true;
  public  isfooterDateBar:boolean=false;
  // public  startTime ='';
  // public  endTime ='';
  public  totalDistance = 0;
  public  totalDisconnectDuration = 0;
  public  totalStopDuration = null;
  public  totalMovingDuration = null;

  public  isDownload = false;

  currenDate = new Date();
  public myDateRangePickerOptions: IMyDrpOptions = {
    dateFormat: 'dd.mmm.yyyy',
    showClearDateRangeBtn: false,
    width:'100%',
    showApplyBtn: false,
    markCurrentDay : true,
    editableDateRangeField : false,
    openSelectorOnInputClick : true,
    indicateInvalidDateRange : true,
    disableSince : {year: this.currenDate.getFullYear(), month: this.currenDate.getMonth()+1, day: this.currenDate.getDate()+1}
  };

  public dateRange: Object = {beginDate: {year: this.currenDate.getFullYear(), month: this.currenDate.getMonth()+1, day: this.currenDate.getDate()},
    endDate: {year: this.currenDate.getFullYear(), month: this.currenDate.getMonth()+1, day: this.currenDate.getDate()}};


  ngOnDestroy() {
    // this._store.dispatch({type: "LOCATION_HISTORY",payload:null});
    if (typeof this.unSub_locationHistory != 'undefined' && this.unSub_locationHistory != null && this.unSub_locationHistory != undefined)
      this.unSub_locationHistory.unsubscribe();
  }

  ngAfterViewChecked(){

  }

  ngOnInit(){
    let offsets=document.getElementById("timeline_card").getBoundingClientRect();
    this.startOffsetHeight=offsets.top;
    this.previousOffset=offsets.top;
  }

  constructor(public snackBar: MdSnackBar,private _store:Store<any>,private _router:Router,public _services:Services,private localStorageService: LocalStorageService,public _angularitics:Angulartics2Mixpanel) {
    this._store.dispatch({type: "TITLE",payload:'Vehicle Timeline Report'}); // set the header name
    this.view =document['view'];
    console.log(this.view);

    this._map = new Promise<mapTypes.GoogleMap>((resolve: () => void) => { this._mapResolver = resolve; });

    let value = this.localStorageService.get('VEHICLE_TIMELINE_REQUEST_OBJ');

    console.log(value);

    console.log(value);
    if(value !=undefined || value != null){
      this.selectedVehicle = value['selectedVehicle'];
      this.setTimeFormat(value['startTime'],value['endTime']); // to set date value in date picker
      this.lat=this.selectedVehicle['latitude'];
      this.long=this.selectedVehicle['longitude'];
      this.getLocationHistory(value);
    }else{
      this.localStorageService.clearAll();
      this._store.dispatch(go(['']));
    }

    console.log(this.selectedVehicle);
    this.unSub_locationHistory = _store.select('locationHistory').subscribe(value => {
      this.locationHislist=[];
      this.errorMsg='';

      // console.log(value);
      if(value==null){
        return;
      }
      this.isSpinner=false;

      if(value['error']==''){
        this.locationHislist = JSON.parse(JSON.stringify(value['data']));
        // this.locationHislist = new Array['data'];
        console.log(this.locationHislist);
        this.setPolyLine(this.locationHislist);
      }else {
        this.errorMsg=value['errorMsg'];
        // console.log(value['errorMsg']);
      }

      let totalDistance = 0;
      let totalStopDuration = 0;
      let totalMovingDuration = 0;
      let lessFiveKmhMovingDistance = 0;
      for(let i=0;i<this.locationHislist.length;i++){

        if(this.locationHislist[i]['state'] == "Stopped") {
          totalStopDuration = totalStopDuration + this.locationHislist[i]['duration']; // for count all stop time
          lessFiveKmhMovingDistance = this.locationHislist[i]['distance'];
        }

        this.locationHislist[i]['_duration'] = this.msToTime(this.locationHislist[i]['duration']); //

        // if(this.locationHislist[i].length >= 1)
        totalDistance  = totalDistance + this.locationHislist[i]['distance'];  // count total distance

        if(this.locationHislist[i]['state'] == "Moving") {
          totalMovingDuration = totalMovingDuration + this.locationHislist[i]['duration'];  // for count all drive time
          this.locationHislist[i]['_formattedDistance'] = this.locationHislist[i]['distance'] + lessFiveKmhMovingDistance;
          lessFiveKmhMovingDistance = 0;
        }
      }

      this.isDownload = true;

      console.log(totalDistance);
      console.log(totalStopDuration);
      this.totalDistance = totalDistance;
      this.totalStopDuration = this.msToTime(totalStopDuration);
      this.totalMovingDuration = this.msToTime(totalMovingDuration);

    });
  }


  setTimeFormat(startmiliTime,endMiliTime){
    this.dateRange['beginDate'] ={year: new Date(startmiliTime).getFullYear(), month:new Date(startmiliTime).getMonth()+1, day: new Date(startmiliTime).getDate()};
    this.dateRange['endDate'] ={year: new Date(endMiliTime).getFullYear(), month:new Date(endMiliTime).getMonth()+1, day: new Date(endMiliTime).getDate()};

  }

  fullScreen(){
    // if large screen then timeline swipe up event not work;
    if(document.getElementById("timeline_card").getBoundingClientRect().top == 51)
      return;

    // *****end here  if large screen then timeline swipe up event not work ****;
    let mapOffsets=document.getElementById("map").getBoundingClientRect();
    this.mapHeight=(-mapOffsets.height)+'px';
    this.isFullScreen=true;
  }

  hideScreen(){
    let mapOffsets=document.getElementById("map").getBoundingClientRect();
    this.mapHeight=(-mapOffsets.height)+'px';
    this.isFullScreen=false;
  }

  getLocationHistory(value){

   this.totalDistance = 0;
   this.totalStopDuration = null;
   this.totalMovingDuration = null;

    this.chkStopped=true;
    this.chkOverspeed=true;
    this.chkMoving=true;
    this.chkDisconnected=true;

    this.isSpinner=true;
    this.locationHislist=[];
    this.polyLineMovingArray=[];
    this.polyLineStoppedArray=[];
    this.errorMsg='';
    let obj={
      startTime:value.startTime,
      endTime: value.endTime,
      vehicleId:value.selectedVehicle['uuid'],
      minimumTime:300000,
      type:["Overspeeding","Stopped","Moving","Disconnected"]
    }
    console.log(obj);
    this.isDownload = false;
    this._services.getLocationHistory.emit(obj);
  }


  back(){
    this._store.dispatch(go(['/vtsdashboard/selectVehicleTimelineReport']));
  }

  goToMapLocation(selectedlocation){
    console.log(selectedlocation);
    if(selectedlocation['state'] == 'Stopped') {
      console.log(this.polyLineStoppedArray);
      for (let i = 0; i < this.polyLineStoppedArray.length; i++) {
        this.polyLineStoppedArray[i].isInfoWindowOpen = false;
        if (selectedlocation['endPosition']['latitude'] == this.polyLineStoppedArray[i].lat && selectedlocation['endPosition']['longitude'] == this.polyLineStoppedArray[i].long) {
          console.log(i);
          this.lat=selectedlocation['endPosition']['latitude'];
          this.long=selectedlocation['endPosition']['longitude'];
          this.polyLineStoppedArray[i].isInfoWindowOpen = true;
          this.zoom = 12;
        }
      }
    }
  }

  setPolyLine(locationHislist)
  {
    // console.log("setPolyLine");
    // console.log(locationHislist);

    let posArrayStopped=[];
    let posArrayMoving=[];
    let posArrayOverspeed=[];
    let decodedPolyline=[],singlePolyline=[],allPolylines=[],decodedPolylineStop=[];

    let pos1={},pos2={};

    for(let i=0; i<this.locationHislist.length; i++)
    {
      if(this.locationHislist[i]['state']=='Stopped'|| this.locationHislist[i]['state']==0) {
        pos2 = {
          lat: this.locationHislist[i]['endPosition']['latitude'],
          long: this.locationHislist[i]['endPosition']['longitude'],
          address: (this.locationHislist[i]['endPosition']['address'] != null)?(this.locationHislist[i]['endPosition']['address']):(this.locationHislist[i]['startPosition']['address']),
          isInfoWindowOpen:false
        }
        decodedPolylineStop=MyPolylineUtil.decode(this.locationHislist[i]['encodedPolyline']);
        this.stoppedPolyLineArray.push(decodedPolylineStop);
        posArrayStopped.push(pos2);
      }
      if(this.locationHislist[i]['state']=='Moving'|| this.locationHislist[i]['state']==1) {
        decodedPolyline=MyPolylineUtil.decode(this.locationHislist[i]['encodedPolyline']);
        this.polyLineMovingArray.push(decodedPolyline);
      }

      if(this.locationHislist[i]['state']=='Overspeeding' || this.locationHislist[i]['state']==2) {
        pos1 = {
          lat: this.locationHislist[i]['startPosition']['latitude'],
          long: this.locationHislist[i]['startPosition']['longitude']
        }
        posArrayOverspeed.push(pos1);

        pos2 = {
          lat: this.locationHislist[i]['endPosition']['latitude'],
          long: this.locationHislist[i]['endPosition']['longitude']
        }
        posArrayOverspeed.push(pos2);

      }

      this.lat=this.locationHislist[i]['endPosition']['latitude'];
      this.long=this.locationHislist[i]['endPosition']['longitude'];

      this.polyLineStoppedArray=posArrayStopped;
      this.polyLineOverspeedArray=posArrayOverspeed;

    }

    console.log(pos1);
    console.log(pos1);
    // console.log("posArray");
    // console.log(this.polyLinePosList);
    // console.log(this.stoppedPolyLineArray);


  }

  setValues(value)
  {
    console.log(value);
  }


  onDateRangeChanged(event: IMyDateRangeModel) {
    console.log(event.beginEpoc);
    this.errorMsg = '';

    if(event.beginEpoc == null || event.beginEpoc == 0 || event.endEpoc == null || event.endEpoc == 0){
      console.log("Please select Date");
      this.openSnackBar("dates should not be blank");
      return;
    }

    if(((event.endEpoc+86400) - 1) - event.beginEpoc >(86400*config.MAX_RANGE_DAYS) ){
      // this.openSnackBar("you can select max  15 day range");
      this.errorMsg = "you can select max  15 day range";
      return;
    }

    let dates={
      startTime:event.beginEpoc*1000,
      endTime: (((event.endEpoc+86400) - 1)*1000),
    };



    this.setTimeFormat(dates.startTime,dates.endTime); // set current value in date object

    let obj ={
      startTime:dates.startTime,
      endTime: dates.endTime,
      selectedVehicle:this.selectedVehicle
    };

    this.localStorageService.set('VEHICLE_TIMELINE_REQUEST_OBJ',obj);

    this.getLocationHistory(obj);
    console.log(obj)
    console.log(this.dateRange);
  }

  getNativeMap(): Promise<mapTypes.GoogleMap> { return this._map; }

  //***************************** for download csv file *********************
  download(){

    this._angularitics.eventTrack('Download_Report_Clicked', { category: 'DashboardMaps'});
    console.log(this.selectedVehicle);
    console.log(this.locationHislist);
    let locationHislist = JSON.parse(JSON.stringify(this.locationHislist)).reverse();
    // locationHislist = locationHislist.;

    console.log(locationHislist);

    let startLocation =null;
    let endLocation =null;
    if(locationHislist.length > 0) {
      if ((locationHislist[0]['startPosition']['address']) != null) {
        startLocation = (locationHislist[0]['startPosition']['address']);
      } else if (locationHislist[0]['endPosition']['address'] != null) {
        startLocation = locationHislist[0]['endPosition']['address'];
      } else {
        startLocation = locationHislist[0]['mean']['address'];
      }

      if ((locationHislist[locationHislist.length - 1]['endPosition']['address']) != null) {
        endLocation = locationHislist[locationHislist.length - 1]['endPosition']['address'];
      } else if (locationHislist[locationHislist.length - 1]['startPosition']['address'] != null) {
        endLocation = locationHislist[locationHislist.length - 1]['startPosition']['address'];
      } else {
        endLocation = locationHislist[locationHislist.length - 1]['mean']['address'];
      }
    }

    let obj = [{
      'Vehicle Number':this.selectedVehicle['vehicleRegistrationNumber'],
      'Start Time': this.dateRange['beginDate']['day']+" "+this.getMonthName(this.dateRange['beginDate']['month'])+" "+this.dateRange['beginDate']['year'],
      'Start Location':startLocation,
      'End Time':this.dateRange['endDate']['day']+" "+this.getMonthName(this.dateRange['endDate']['month'])+" "+this.dateRange['endDate']['year'],
      'End Location':endLocation,
      'Distance':(this.totalDistance/1000).toFixed(2)+" km",
      'Drive Time':this.totalMovingDuration,
      'Stop Time':this.totalStopDuration,
      'Disconnect Time':0,
    },
      {'Vehicle Number':'Details',
        'Start Time':'',
        'Start Location':'',
        'End Time':'',
        'End Location':'',
        'Distance':'',
        'Drive Time':'',
        'Stop Time':'',
        'Disconnect Time':'',
      }
    ];
    //***** for details array  ****
    let startLoc =' ';
    let endLoc =' ';
    let startTime :any;
    let endTime :any;
    let driveDuration =0;
    let stopDuration =0;
    let driveDistance =0;
    let disConnectDuration =0;
    let isGotStartPoint = false;

    console.log("done");
    for(let i=0;i<locationHislist.length; i++){
      if(isGotStartPoint == false){
        if ((locationHislist[i]['startPosition']['address']) != null) {
          startLoc = (locationHislist[i]['startPosition']['address']);
        } else if (locationHislist[i]['endPosition']['address'] != null) {
          startLoc = locationHislist[i]['endPosition']['address'];
        } else {
          startLoc = locationHislist[i]['mean']['address'];
        }

         startTime = new Date(Number(locationHislist[i].startTime));
         isGotStartPoint = true;
      }

        if(locationHislist[i]['state'] == "Moving"){
          driveDuration = driveDuration+locationHislist[i].duration;
          driveDistance =driveDistance +locationHislist[i]._formattedDistance;
        }

        if(locationHislist[i]['state'] == "Disconnected"){
          disConnectDuration = disConnectDuration+locationHislist[i].duration;
          this.totalDisconnectDuration =this.totalDisconnectDuration +locationHislist[i].duration;
        }

        if(locationHislist[i]['state'] == "Stopped" ){
          stopDuration = locationHislist[i].duration;

          if ((locationHislist[i]['endPosition']['address']) != null) {
            endLoc = (locationHislist[i]['endPosition']['address']);
          } else if (locationHislist[i]['startPosition']['address'] != null) {
            endLoc = locationHislist[i]['startPosition']['address'];
          } else {
            endLoc = locationHislist[i]['mean']['address'];
          }

          endTime = new Date(Number(locationHislist[i].endTime));

          console.log(this.to12HourFormat(startTime));
          console.log(startTime);
          let temp :any={
            'Vehicle Number':' ',
            'Start Time':startTime.getDate()+" "+this.getMonthName(startTime.getMonth())+" "+startTime.getFullYear()+" "+this.to12HourFormat(startTime),
            'Start Location':startLoc,
            'End Time':endTime.getDate()+" "+this.getMonthName(endTime.getMonth())+" "+endTime.getFullYear()+" "+this.to12HourFormat(endTime),
            'End Location':endLoc,
            'Distance':(driveDistance/1000).toFixed(2)+" km",
            'Drive Time':this.msToTime(driveDuration),
            'Stop Time':this.msToTime(stopDuration),
            'Disconnect Time':this.msToTime(disConnectDuration),
          };
          obj.push(temp);

           startLoc =' ';
           endLoc =' ';
           driveDuration =0;
           stopDuration =0;
           driveDistance =0;
           disConnectDuration =0;
           isGotStartPoint = false;
        }

      //**** when last element of array is not stopped ****
      if(i == locationHislist.length - 1 && locationHislist[locationHislist.length - 1]['state'] != "Stopped" ){

        if ((locationHislist[i]['endPosition']['address']) != null) {
          endLoc = (locationHislist[i]['endPosition']['address']);
        } else if (locationHislist[i]['startPosition']['address'] != null) {
          endLoc = locationHislist[i]['startPosition']['address'];
        } else {
          endLoc = locationHislist[i]['mean']['address'];
        }

        endTime = new Date(Number(locationHislist[i].endTime));
        let temp :any={
          'Vehicle Number':' ',
          'Start Time':startTime.getDate()+" "+this.getMonthName(startTime.getMonth())+" "+startTime.getFullYear(),
          'Start Location':startLoc,
          'End Time':endTime.getDate()+" "+this.getMonthName(endTime.getMonth())+" "+endTime.getFullYear(),
          'End Location':endLoc,
          'Distance':(driveDistance/1000).toFixed(2)+" km",
          'Drive Time':this.msToTime(driveDuration),
          'Stop Time':this.msToTime(stopDuration),
          'Disconnect Time':this.msToTime(disConnectDuration),
        };
        obj.push(temp);
      }
    }

    obj[0]['Disconnect Time'] = this.msToTime(this.totalDisconnectDuration);



    console.log(obj);
    let csvData = this.ConvertToCSV(obj);
    // console.log(csvData);
    var a = document.createElement("a");
    a.setAttribute('style', 'display:none;');
    document.body.appendChild(a);
    var blob = new Blob([csvData], { type: 'text/csv' });
    var url= window.URL.createObjectURL(blob);
    a.href = url;
    a.download = 'vehicle_report.csv';
    a.click();
  }


  to12HourFormat(date) {
    let hours = ((date.getHours() + 11) % 12 + 1);
    let minutes = date.getMinutes();
    let meridian = (date.getHours() >= 12) ? 'PM' : 'AM';

    return hours+":"+minutes+" "+meridian;
  }


  ConvertToCSV(obj) {

    let array = obj;
    let str = '';
    let row = "";

    for (let index in obj[0]) {
      //Now convert each value to string and comma-separated
      row += index + ',';
    }

    row = row.slice(0, -1);
    //append Label row with line break
    str += row + '\r\n';

    for (let i = 0; i < array.length; i++) {
      var line = '';

      for (let index in array[i]) {

        if (line != '') line += ',';
        line += JSON.stringify(array[i][index]);
      }
      str += line + '\r\n';
    }
    return str;
  }
  //***************************** close for download csv file *********************
  getMonthName(month)
{
  return ["January","February","March","April","May","June","July","August","September", "October","November","December"][parseInt(month)]
}


  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration='';

    if(days>0){
      totalDuration=days+" d ";
    }
    if(hours>0){
      totalDuration=totalDuration+hours+" hr ";
    }

    if(days >= 1){
      return totalDuration;
    }

    if(minutes>0){
      return totalDuration=totalDuration+minutes+" min";
    }
    if(seconds>0 && totalDuration == ''){
      totalDuration=totalDuration+seconds+" sec";
    }

    if(totalDuration==''){
      return '0 sec';
    }else {
      return totalDuration;
    }

  }//close methods msToTime

  openSnackBar(message: string) {
    this.snackBar.open(message , '', <MdSnackBarConfig>{duration :  3000});
  }

}






