import { Component, OnInit } from '@angular/core';
import {Services} from "../../services/services";
import {Store} from "@ngrx/store";
import {go,back} from "@ngrx/router-store";
import {ActivatedRoute} from "@angular/router";
import {VehicleActivity} from "../vehicle-activity/vehicle-activity.component";

@Component({
  selector: 'app-selected-vehicle-status',
  templateUrl: './selected-vehicle-status.component.html',
  styleUrls: ['./selected-vehicle-status.component.css']
})
export class SelectedVehicleStatus implements OnInit {

  public currentVehicle:any;
  private unSub_currentVehicle=null;

  ngOnDestroy() {
    if (typeof this.unSub_currentVehicle != 'undefined' && this.unSub_currentVehicle != null && this.unSub_currentVehicle != undefined)
    {
      console.log(" unSub_currentVehicle");
      this.unSub_currentVehicle.unsubscribe();
    }
  }

  constructor(private _store:Store<any>,private _services:Services,private route: ActivatedRoute) {

    // route.queryParams.subscribe(params => {
    //     this.currentVehicle = params['vehicle'];
    //   console.log(params);
    //   });

    this.unSub_currentVehicle = _store.select('currentVehicle').subscribe(value => {
      this.currentVehicle = value;
      console.log(this.currentVehicle);
    });
  }// constructor close

  ngOnInit() {

  }

  back(){
    this._store.dispatch(go(['/vtsdashboard/vehicleStatus']));
  }

  goToVehicleActivityView(state){
    this._store.dispatch(go(['vtsdashboard/vehicleActivity'],{ vehicle: this.currentVehicle.selectedVehicle,state:state}));
  }

}



