import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";
import {paths} from "../../environments/environment";
// import {GET_SCHEDULE_REPORTS_PATH,CREATE_SCHEDULE_REPORTS_PATH,CHANGE_SCHEDULE_STATE_PATH} from "./path-services";

@Injectable()
export class ScheduleReportServices {
  public defaultHeaders: Headers = new Headers();
  private token:any='';
  public getScheduleReport =new EventEmitter();
  public createNewSchedule =new EventEmitter();
  public changeScheduleState =new EventEmitter();

  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>,private localStorageService: LocalStorageService){
    this.defaultHeaders.append('Access-Control-Allow-Origin', '*');
    this.defaultHeaders.append('Access-Control-Allow-Methods', 'GET,POST, OPTIONS, PUT, PATCH, DELETE');

    //************  get Schedule list  ***************************************************
    this.getScheduleReport.subscribe(()=> {
      this.token =this.localStorageService.get("TOKEN"); // get token from local storage
      if( this.token != null ){
        this.getScheduleReportList().subscribe(response =>{
          console.log(response);
          this._store.dispatch({type: "SCHEDULE_REPORT_LIST",payload:{error:'',list:response}});
        },err=>{
          this._store.dispatch({type: "SCHEDULE_REPORT_LIST",payload:{error:'X',msg:"error occur : "+err.json().message}});
          console.log(err);
          console.log(err.json().message);
        });
      }else{
        console.log("please login");
      }
    });

    //************  create new Schedule ***************************************************
    this.createNewSchedule.subscribe((reqObj)=> {
      this.token =this.localStorageService.get("TOKEN"); // get token from local storage
      if( this.token != null ){
        this.createSchedule(reqObj).subscribe(response =>{
          if(response.status ==200){
            console.log(response.json());
            this._store.dispatch({type: "SCHEDULE_CREATED_ACK",payload:{error:'',errorMsg:'successfull created'}});
          }else{
            this._store.dispatch({type: "SCHEDULE_CREATED_ACK",payload:{error:'X',errorMsg:'Not created'}});
          }
        },err=>{
          this._store.dispatch({type: "SCHEDULE_CREATED_ACK",payload:{error:'X',errorMsg:'Not created'}});
          console.log(err);
          console.log(err.json().message);
        });
      }else{
        console.log("please login");
      }
    });




    //************  change Schedule state***************************************************
    this.changeScheduleState.subscribe((reqObj)=> {
    let path =paths.CHANGE_SCHEDULE_STATE_PATH+reqObj['scheduleId']+'&active='+reqObj['activeState'];
      console.log(path);

      this.token =this.localStorageService.get("TOKEN"); // get token from local storage
      if( this.token != null ){
        this.changeState(path,reqObj).subscribe(response =>{
          if(response.status ==200){
            console.log(response.json());
            this._store.dispatch({type: "CHANGE_SCHEDULE_REPORT_STATE_ACK",payload:{error:'',errorMsg:response.json().message}});
          }else{
            console.log(response.json());
            this._store.dispatch({type: "CHANGE_SCHEDULE_REPORT_STATE_ACK",payload:{error:'X',errorMsg:response.json().message}});
          }
        },err=>{
          this._store.dispatch({type: "CHANGE_SCHEDULE_REPORT_STATE_ACK",payload:{error:'X',errorMsg:'error occur : Not updated'}});
          console.log(err);
          console.log(err.json().message);
        });
      }else{
        console.log("please login first");
      }
    });

  }// constructor close here

  // ***********************  get schedule list**********************
  public getScheduleReportList(){

    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let bearerToken="Bearer "+this.token;
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: Id,
    });

    let path =paths.GET_SCHEDULE_REPORTS_PATH;
    console.log(this.token);
    console.log(path);
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
        return response.json(); // success full status code with 200
    });
  }


  // ***********************  create new schedule **********************
  public createSchedule(reqObj){

    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let bearerToken="Bearer "+this.token;
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body:reqObj,
    });

    let path =paths.CREATE_SCHEDULE_REPORTS_PATH;
    console.log(this.token);
    console.log(path);
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      return response; // success full status code with 200
    });
  }

  // ***********************  create new schedule **********************
  public changeState(path,reqObj){

    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let bearerToken="Bearer "+this.token;
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body:reqObj,
    });

    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      return response; // success full status code with 200
    });
  }


}// class close




