import {Component, OnInit, ElementRef} from '@angular/core';
import {Store} from "@ngrx/store";
// import {Services} from "../../services/services";
import {go, replace} from "@ngrx/router-store";
import {MdProgressBar, MdProgressBarModule} from "@angular/material";
import {LocalStorageService} from "angular-2-local-storage";
import {TripServices} from "../../../../services/tripServices";
import {TripDetailSocketService} from "../../../../services/trip.socket.service";
import {ETA_DistanceService} from "../../../../services/eta-distance.service";
import { Angulartics2Mixpanel } from 'angulartics2/dist';
// import {TripServices} from "../../services/tripServices";
// import {SocketService} from "../../services/socket-service";

@Component({
  selector: 'app-vehicle-status',
  templateUrl: './vehicle-status.component.html',
  styleUrls: ['./vehicle-status.component.css']
})
export class VehicleStatus implements OnInit {
  public index_latLng: any[] = [];
  public oLat: any;
  public oLng: any;
  public dLat: any;
  public dLng: any;

  public isSearch = false;
  public  search:string='';
  public isSpinner:boolean=false;
  public errorMsg:string="";
  public viewTrip ='';
  public tripStatusList:any =[];
  public unSub_TripStatus =null;
  public unSub_customerDetails = null;
  public unSub_updateTripData = null;
  public unSub_ETA_DistanceStore = null;

  public connection:any =null;
  public vehicleTripCount = {all:0,unassigned:0,underLoading:0,underUnloading:0,inTransite:0,atPickupPt:0};


  ngOnDestroy() {
    this.tripSocketObj.disconnectTripListSocket();
    if (typeof this.unSub_TripStatus != 'undefined' && this.unSub_TripStatus != null && this.unSub_TripStatus != undefined)
      this.unSub_TripStatus.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();

    if (typeof this.unSub_updateTripData != 'undefined' && this.unSub_updateTripData != null && this.unSub_updateTripData != undefined)
      this.unSub_updateTripData.unsubscribe();

    if (typeof this.unSub_ETA_DistanceStore != 'undefined' && this.unSub_ETA_DistanceStore != null && this.unSub_ETA_DistanceStore != undefined)
      this.unSub_ETA_DistanceStore.unsubscribe();
  }


  constructor(private tripEtaDistance: ETA_DistanceService,private tripSocketObj: TripDetailSocketService,private localStorageService:LocalStorageService,private _store:Store<any>,private tripServices:TripServices,public _angularitics:Angulartics2Mixpanel) {
    this._store.dispatch({type: "TITLE",payload:'Vehicle Trip Status'}); // set the header name

    // this.tripServices.getTripStatus.emit();
    this.isSpinner = true;

    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      // console.log(value);
      if (value == null) {
        return;
      }
      // console.log(value);
      this.tripSocketObj.getTripList();
    });


    //dispatch data from trip-socket.service
    this.unSub_updateTripData = _store.select('updateTripData').subscribe(res => {

      let value =res;
      // console.log(value);
      if (value == null || this.tripStatusList == null) {
        return;
      }

      for (let i = 0; i < this.tripStatusList.length; i++) {
        if(this.tripStatusList[i]['vts'] != value['vts'] && this.tripStatusList[i]['uuid'] == value['vehicleId']){
          this.merge(this.tripStatusList[i], value);
          // console.log(this.tripStatusList[i]);
          this.updateList();
        }
      }
    });


    this.unSub_TripStatus = _store.select('tripStatusList').subscribe(value => {
      console.log(value);
      if(value == null)
        return;
      this.isSpinner = false;

      this.tripStatusList = JSON.parse(JSON.stringify(value)); // create new array
      this.updateList();

      // console.log(this.tripStatusList);
      // console.log(this.vehicleTripCount);
    });



    this.unSub_ETA_DistanceStore = _store.select("etaListDistanceStore").subscribe(data=> {
      if (data == null){
        return;
      }

      // console.log(data);
      let distance = (data['value'][0]['distance']['value'] / 1000).toFixed(2) + " km";
      // console.log(data);
      // console.log(this.tripStatusList);
      let ind = data['index'];
      if (this.tripStatusList.length > ind) {
        // console.log(this.tripStatusList[ind]['distanceToGo'])

        this.tripStatusList[ind].distanceToGo = distance;
        this.tripStatusList[ind]['eta'] = this.msToTime(data['value'][0]['duration']['value'] * 1000);
        this.tripStatusList[ind]['timeValue'] = data['value'][0]['duration']['value'];

        // console.log(this.tripStatusList);
      }
    });

  }// close constructor

  updateList(){
    // if(value['status'] != 200){
    //   console.log(value['statusText']);
    //   this.errorMsg = value['statusText'];
    //   return;
    // }
    // console.log(value);
    this.vehicleTripCount ={all:0,unassigned:0,underLoading:0,underUnloading:0,inTransite:0,atPickupPt:0};
    // this.tripStatusList =JSON.parse(JSON.stringify(value)); // create new array

    this.vehicleTripCount['all'] = this.tripStatusList.length;
    for(let i=0;i<this.tripStatusList.length;i++){
      // create new variable in array
      this.tripStatusList[i]._vtsDuration = this.msToTime(this.tripStatusList[i].vtsDuration);
      this.tripStatusList[i]._isExpandText = false;
      this.tripStatusList[i]._isExpandFromAddress = false;
      this.tripStatusList[i]._isExpandDestAddress = false;

      if(this.tripStatusList[i]['vts'] == 'WaitingForLoad'){
        this.vehicleTripCount['unassigned'] = this.vehicleTripCount['unassigned'] + 1;
      }

      if(this.tripStatusList[i]['vts'] == 'AtDestination'){
        this.vehicleTripCount['underUnloading'] = this.vehicleTripCount['underUnloading'] + 1;
      }

      if(this.tripStatusList[i]['vts'] == 'EnrouteForPickUp'){
        this.vehicleTripCount['underLoading'] = this.vehicleTripCount['underLoading'] + 1;

        this.oLat = this.tripStatusList[i]['latitude'];
        this.oLng = this.tripStatusList[i]['longitude'];

        this.dLat = this.tripStatusList[i]['tripStartLocation']['geofence']['center']['latitude'];
        this.dLng = this.tripStatusList[i]['tripStartLocation']['geofence']['center']['longitude'];
        this.index_latLng.push({index: i, oLat: this.oLat, oLng: this.oLng, dLat: this.dLat, dLng: this.dLng});
      }

      if(this.tripStatusList[i]['vts'] == 'EnrouteForDestination'){
        this.vehicleTripCount['inTransite'] = this.vehicleTripCount['inTransite'] + 1;

        this.oLat = this.tripStatusList[i]['latitude'];
        this.oLng = this.tripStatusList[i]['longitude'];

        this.dLat = this.tripStatusList[i]['tripEndLocation']['geofence']['center']['latitude'];
        this.dLng = this.tripStatusList[i]['tripEndLocation']['geofence']['center']['longitude'];

        this.index_latLng.push({index: i, oLat: this.oLat, oLng: this.oLng, dLat: this.dLat, dLng: this.dLng});
      }

      if(this.tripStatusList[i]['vts'] == 'AtPickUpPoint'){
        this.vehicleTripCount['atPickupPt'] = this.vehicleTripCount['atPickupPt'] + 1;
      }
    }

    this.tripEtaDistance.getEtaDistanceList.emit(this.index_latLng); // for getting eta distance
  }

  merge(original, delta) {
    // return Object.assign(original, delta);
    for (var attrname in delta) {
      original[attrname] = delta[attrname];
    }
  }
  // ************************ for socket connection ******************************************8
  // sendMessage(){
  //   this.socketService.sendMessage("testing");
  //   // this.message = '';
  // }
  //
  ngOnInit() {
  //   this.connection = this.socketService.getMessages().subscribe(message => {
  //     // this.messages.push(message);
  //   })
  }

  // ********************************************************************************


  goToselectedVehView(vehicle){
    console.log(vehicle);
    let obj = JSON.parse(JSON.stringify(vehicle));
    this.localStorageService.set("CURRENT_TRIP_VEHICLE",obj);

    // this.tripServices.getTripDetails.emit({vehicleID:"v"+ind});
    this._angularitics.eventTrack('TripView_Item_Clicked', vehicle); // records mixpanel event for click on trip status list
    this._store.dispatch({type: "VEHICLE_TRIP_DETAILS",payload:null});
    this._store.dispatch(go(['vtsdashboard/trip/status/details']));
  }

  expandAddress(){
    event.stopPropagation();
    // event.target.style.whiteSpace ='';
    // console.log(event.target.style.whiteSpace);
   }

  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration='';

    if(days>0){
      totalDuration=days+"d ";
    }
    if(hours>0){
      totalDuration=totalDuration+hours+"hr ";
    }

    if(days >= 1){
      return totalDuration;
    }

    if(minutes>0){
      return totalDuration=totalDuration+minutes+"min";
    }
    if(seconds>0 && totalDuration == ''){
      totalDuration=totalDuration+seconds+" sec";
    }

    if(totalDuration==''){
      return '0 sec';
    }else {
      return totalDuration;
    }

  }//close methods msToTime

  filterClickMixpanel()
  {
    this._angularitics.eventTrack('Trip_Filter_Mobile_Clicked', this.viewTrip);

  }



} // class close




