
import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";


import { freight_Location_path } from "./path.service";

// import { ETA_PATH } from "./path-services";
// import {KEY} from "./config-services"

@Injectable()
export class FreightLocationService {
  public defaultHeaders: Headers = new Headers();
  public freightLocation = new EventEmitter();
  public res : any = null;
  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>, ){
    // this.defaultHeaders =  new Headers();
    // this.defaultHeaders.append('Access-Control-Allow-Origin', '*');
    // this.defaultHeaders.append('Access-Control-Allow-Methods', 'GET,PUT,POST,DELETE,PATCH,OPTIONS');

    this.freightLocation.subscribe((obj)=>{
      let path = freight_Location_path;

      this.addRecentFreightLocation(path,obj).subscribe(res => console.log(res));
    })

  }// constructor close here

  //********************* add recent freight location *****/
  public addRecentFreightLocation(path,obj) {
    let headers = new Headers(this.defaultHeaders);
    headers.set('Content-Type','application/json');

    console.log(headers);
    let requestOptions : RequestOptionsArgs = new RequestOptions({
      method : RequestMethod.Post,
      headers : headers,
      body : obj
    });

    console.log(this.http);
    return this.http.request(path,requestOptions).map((response : Response)=> {
      console.log(response);
       return response.json();
    })
  }

}// class close



