import {Component, OnInit, isDevMode} from '@angular/core';
import {Router} from "@angular/router";
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";
import {TripServices} from "../../../../services/tripServices";
import {go} from "@ngrx/router-store";
import {ETA_DistanceService} from "../../../../services/eta-distance.service";
import {TripDetailSocketService} from "../../../../services/trip.socket.service";
import { Angulartics2Mixpanel } from 'angulartics2/dist';
import {TripTemplateServices} from "../../../../services/tripTemplate-services";

@Component({
  selector: 'app-trip-status-desktop',
  templateUrl: './trip-status-desktop.component.html',
  styleUrls: ['./trip-status-desktop.component.css']
})
export class TripStatusDesktop {
  // public doughnutChartLabels:string[] = ['Connected', 'Disconnected'];
  public doughnutChartData: number[] = [0, 0];
  public doughnutChartType: string = 'doughnut';
  public doughnutColor: any[] = [{backgroundColor: ["#add160", "#a7a9b2"]}];
  public doughnutChartOptions: any = {cutoutPercentage: 65};

  public isShowDetails: boolean = false;
  // public allVehicles :any = [];
  // public selectedTripStatusList :any = [];
  public previousSelectedVehicle: any = {_isShowDetails: false};
  public distanceToGo: any = [];
  public oLat: any;
  public oLng: any;
  public dLat: any;
  public dLng: any;

  // public driverDetail :any = {_driverName:'amit',_mobileNo:'96966969696'};


  //start for pagination
  // public totalLength = 0;
  // public pageSize = 10;
  // public pageSizeOptions = [5, 10, 25,50,100];
  // pageEvent: PageEvent;
  //close for pagination
  public isTripTemplateDialog=false;
  public selectedTemplate:any;

  public index_latLng: any[] = [];
  public isSearch = false;
  public selectedTripTemplate:any= {_selected:false};

  public vehicleObjList:any[]=[];
  public orgId;
  public token;

  public search: string = '';
  public isSpinner: boolean = false;
  public isDesable: boolean = true;
  public viewTrip = '';
  public tripStatusList: any = [];
  public unSub_TripStatus = null;
  public unSub_ETA_DistanceStore = null;
  public unSub_customerDetails = null;
  public unSub_updateTripData = null;
  public connection: any = null;
  public tripTemplateList:any[]=[];
  public unSub_templateList:any=null;
  public vehicleTripCount = {all: 0, unassigned: 0, underLoading: 0, underUnloading: 0, inTransite: 0, atPickupPt: 0};
  public gpsState = {connected: 0, disconncted: 0};


  ngOnDestroy() {
    this.tripSocketObj.disconnectTripListSocket();
    if (typeof this.unSub_TripStatus != 'undefined' && this.unSub_TripStatus != null && this.unSub_TripStatus != undefined)
      this.unSub_TripStatus.unsubscribe();

    if (typeof this.unSub_ETA_DistanceStore != 'undefined' && this.unSub_ETA_DistanceStore != null && this.unSub_ETA_DistanceStore != undefined)
      this.unSub_ETA_DistanceStore.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();

    if (typeof this.unSub_updateTripData != 'undefined' && this.unSub_updateTripData != null && this.unSub_updateTripData != undefined)
      this.unSub_updateTripData.unsubscribe();
  }

  constructor(private tripSocketObj: TripDetailSocketService,public templateServices:TripTemplateServices, public localStorageService: LocalStorageService, private _store: Store<any>, private tripServices: TripServices, private tripEtaDistance: ETA_DistanceService,public _angularitics:Angulartics2Mixpanel) {
    // this.tripServices.getTripStatus.emit();

    this.isSpinner = true;
    // this.orgId= this.localStorageService.get("ORGID");
    this.token = this.localStorageService.get("AUTH_TOKEN");
    this.customerDetail(this.token);
    this.templateServices.tripTemplateStatus.emit();
    this.unSub_templateList = this._store.select('tripTemplateList').subscribe(value => {
      console.log(value);

      if (value == null) {
        return;
      }

      let obj :any=[];
      obj = value['data'];
      // console.log(obj);
      if (value['error'] == "") {

        if(obj['status']== 200)
        {
          this.tripTemplateList=obj['list'];
          console.log(this.tripTemplateList);
        }
        // console.log(obj);
      }
    });

    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      // console.log(value);
      if (value == null) {
        return;
      }
      console.log(value);
      this.tripSocketObj.getTripList();
    });

    //dispatch data from trip-socket.service
    this.unSub_updateTripData = _store.select('updateTripData').subscribe(res => {

      let value =res;
      // console.log(value);
      if (value == null || this.tripStatusList == null) {
        return;
      }

      for (let i = 0; i < this.tripStatusList.length; i++) {
        if(this.tripStatusList[i]['vts'] != value['vts'] && this.tripStatusList[i]['uuid'] == value['vehicleId']){
                this.merge(this.tripStatusList[i], value);
                console.log(this.tripStatusList[i]);
                this.updateList();
        }
      }
    });

    this.unSub_TripStatus = _store.select('tripStatusList').subscribe(value => {
      console.log(value);
      if (value == null)
        return;

      this.isSpinner = false;

        this.tripStatusList = JSON.parse(JSON.stringify(value));
         // create new array
        console.log(this.tripStatusList);
      this.updateList();
      // console.log(this.vehicleTripCount);
      // console.log(this.gpsState);
      // console.log(this.doughnutChartData);
    });


    this.unSub_ETA_DistanceStore = _store.select("etaListDistanceStore").subscribe(data=> {
      if (data == null)
        return;

      // console.log(data);
      let distance = (data['value'][0]['distance']['value'] / 1000).toFixed(2) + " km"
      // console.log(data);
      // console.log(this.tripStatusList);
      let ind = data['index'];
      if (this.tripStatusList.length > ind) {
        // console.log(this.tripStatusList[ind]['distanceToGo'])

        this.tripStatusList[ind].distanceToGo = distance;
        this.tripStatusList[ind]['eta'] = this.msToTime(data['value'][0]['duration']['value'] * 1000);
        this.tripStatusList[ind]['timeValue'] = data['value'][0]['duration']['value'];

        // console.log(this.tripStatusList);
      }
    });

  } //constructor close


  updateList() {
    // if(value['status'] != 200){
    //   console.log(value['statusText']);
    //   this.errorMsg = value['statusText'];
    //   return;
    // }
    console.log("update value");
    this.gpsState = {connected: 0, disconncted: 0};
    this.vehicleTripCount = {all: 0, unassigned: 0, underLoading: 0, underUnloading: 0, inTransite: 0, atPickupPt: 0};

    // if (updated == null) {// to keep the bindings
    //   this.tripStatusList = JSON.parse(JSON.stringify(value)); // create new array
    // }

    // console.log(this.tripStatusList);
    this.vehicleTripCount['all'] = this.tripStatusList.length;

    for (let i = 0; i < this.tripStatusList.length; i++) {
      this.tripStatusList[i]['_isChecked']=false;
      this.tripStatusList[i]._vtsDuration = this.msToTime(this.tripStatusList[i].vtsDuration);
      // this.tripStatusList[i]._isExpandText = false;
      // this.tripStatusList[i]._isExpandFromAddress = false;
      // this.tripStatusList[i]._isExpandDestAddress = false;

      if (this.tripStatusList[i]['vts'] == 'WaitingForLoad') {
        this.vehicleTripCount['unassigned'] = this.vehicleTripCount['unassigned'] + 1;
      }

      if (this.tripStatusList[i]['vts'] == 'AtDestination') {
        this.vehicleTripCount['underUnloading'] = this.vehicleTripCount['underUnloading'] + 1;
      }

      if (this.tripStatusList[i]['vts'] == 'EnrouteForPickUp') {
        this.vehicleTripCount['underLoading'] = this.vehicleTripCount['underLoading'] + 1;
        this.oLat = this.tripStatusList[i]['latitude'];
        this.oLng = this.tripStatusList[i]['longitude'];

        this.dLat = this.tripStatusList[i]['tripStartLocation']['geofence']['center']['latitude'];
        this.dLng = this.tripStatusList[i]['tripStartLocation']['geofence']['center']['longitude'];

        // this.etaDistanceService.path = "https://maps.googleapis.com/maps/api/distancematrix/json?units=imperial&origins=" + this.oLat + "," + this.oLng + "&destinations=" + this.dLat + "," + this.dLng + "&key=AIzaSyCLN-Y6Ugg4D-kzS1l_keqhyJ4tam-TKiI";

        this.index_latLng.push({index: i, oLat: this.oLat, oLng: this.oLng, dLat: this.dLat, dLng: this.dLng});
        // this.tripStatusList[i]["distanceToGo"];
        // this.tripStatusList[i]["eta"];
        // this.tripStatusList[i]['timeValue'];
      }

      if (this.tripStatusList[i]['vts'] == 'EnrouteForDestination') {
        this.vehicleTripCount['inTransite'] = this.vehicleTripCount['inTransite'] + 1;

        this.oLat = this.tripStatusList[i]['latitude'];
        this.oLng = this.tripStatusList[i]['longitude'];

        this.dLat = this.tripStatusList[i]['tripEndLocation']['geofence']['center']['latitude'];
        this.dLng = this.tripStatusList[i]['tripEndLocation']['geofence']['center']['longitude'];

        this.index_latLng.push({index: i, oLat: this.oLat, oLng: this.oLng, dLat: this.dLat, dLng: this.dLng});
        // this.tripStatusList[i]["distanceToGo"];
        // this.tripStatusList[i]["eta"];
        // this.tripStatusList[i]['timeValue'];
      }

      if (this.tripStatusList[i]['vts'] == 'AtPickUpPoint') {
        this.vehicleTripCount['atPickupPt'] = this.vehicleTripCount['atPickupPt'] + 1;
      }

      if (this.tripStatusList[i]['gpsState'] == undefined || this.tripStatusList[i]['gpsState'] == 'Disconnected' || this.tripStatusList[i]['gpsState'] == null) {
        this.gpsState['disconncted'] = this.gpsState['disconncted'] + 1;
      }

    }
    // console.log(this.index_latLng)
    this.tripEtaDistance.getEtaDistanceList.emit(this.index_latLng); // for getting eta distance

    this.gpsState['connected'] = (this.vehicleTripCount['all'] - this.gpsState['disconncted']);

    this.doughnutChartData = [];
    this.doughnutChartData.push(this.gpsState['connected']);
    this.doughnutChartData.push(this.gpsState['disconncted']);

    // console.log(this.tripStatusList);
  }
  getVehicliObj(value)
  {
    // if(value._isChecked==undefined)
    // value._isChecked=false;
     console.log(value._isChecked);
  }

  customerDetail(token){
    let userDetails ={};
    let base64Url = token.split('.')[0];
    let base64 = base64Url.replace('-', '+').replace('_', '/');
    userDetails['userType']=JSON.parse(window.atob(base64));

    base64Url = token.split('.')[1];
    base64 = base64Url.replace('-', '+').replace('_', '/');
    userDetails['details']=JSON.parse(window.atob(base64));
    userDetails['details']['id'] =userDetails['details']['userId'];
    this.orgId=userDetails['details']['orgId'];
    console.log(userDetails);
    return userDetails;
  }


  merge(original, delta) {
    // return Object.assign(original, delta);
    for (var attrname in delta) {
      original[attrname] = delta[attrname];
    }
  }

  // events
  // public chartClicked(e:any):void {
  //   console.log(e);
  // }
  //
  // public chartHovered(e:any):void {
  //   console.log(e);
  // }

  changeBgColor(selectedVehicle) {
    // console.log(selectedVehicle);
    if (selectedVehicle == this.previousSelectedVehicle) {
      return;
    }

    selectedVehicle._isShowDetails = true;
    this.previousSelectedVehicle['_isShowDetails'] = false;
    this.previousSelectedVehicle = selectedVehicle;
  }

  closeDetails() {
    this.previousSelectedVehicle['_isShowDetails'] = false;
  }

  // selectTableHeight(){
  //   console.log(this.pageEvent['pageSize']);
  //   console.log(this.pageEvent['pageIndex']);
  //   this.selectedTripStatusList = this.tripStatusList.slice((this.pageEvent['pageSize']*this.pageEvent['pageIndex']),(this.pageEvent['pageSize']*(this.pageEvent['pageIndex']+1)));
  //   this.previousSelectedVehicle['_isShowDetails'] = false; // for change color of row when selected
  //   this.isShowDetails = false; //  for show details of vehicle in side of table
  // }


  showDetails(vehicle) {
    console.log(vehicle)
    this.isShowDetails = false;
    this._angularitics.eventTrack('TripView_Item_Clicked', vehicle);
    console.log(this.previousSelectedVehicle['_isShowDetails']);

    setTimeout(()=> {
      console.log(vehicle);
      let obj = JSON.parse(JSON.stringify(vehicle));
      this.localStorageService.set("CURRENT_TRIP_VEHICLE", obj);
      this._store.dispatch({type: "VEHICLE_TRIP_DETAILS", payload: null});
      this.isShowDetails = true;
    }, 10);
  } //close methods
  goToTripTemplateDialog()
  {
    this.isTripTemplateDialog=true;
  }

  goToAddTripView(type) {
    this.localStorageService.set("TEMPLATE_TYPE",type);
    for(let i=0;i<this.tripStatusList.length;i++)
    {
      if(this.tripStatusList[i]._isChecked===true)
      this.vehicleObjList.push(this.tripStatusList[i].vehicleRegistrationNumber);
    }
    console.log(this.vehicleObjList);
    this.localStorageService.set("SELECTED_VEHICLE_LIST", this.vehicleObjList  );
    this._store.dispatch(go(['/vtsdashboard/addTrip']));
  }
  goselectedTemplate(template)
  {
  this.isDesable=false;
  this.selectedTemplate=template;
  this.localStorageService.set("TRIP_TEMPLATE",this.selectedTemplate);
  console.log(this.selectedTemplate);
  }
  changeBgColor2(template){
    if(template == this.selectedTripTemplate){
      return;
    }

    template._selected = true;
    this.selectedTripTemplate['_selected'] = false;
    this.selectedTripTemplate = template;
    console.log(this.selectedTripTemplate['_selected']);
  }
  tripTemplateStatus() {
    this._store.dispatch(go(['/vtsdashboard/tripTemplateStatus']));
  }

  filterTripList(viewTripStatus)
  {
    this.viewTrip=viewTripStatus;
    this._angularitics.eventTrack('Trip_Filter_Web_Clicked', viewTripStatus);
  }

  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration = '';

    if (days > 0) {
      totalDuration = days + "d ";
    }
    if (hours > 0) {
      totalDuration = totalDuration + hours + "hr ";
    }

    if (days >= 1) {
      return totalDuration;
    }

    if (minutes > 0) {
      return totalDuration = totalDuration + minutes + "min";
    }
    if (seconds > 0 && totalDuration == '') {
      totalDuration = totalDuration + seconds + " sec";
    }

    if (totalDuration == '') {
      return '0 sec';
    } else {
      return totalDuration;
    }

  }//close methods msToTime

}



