import { ActionReducer, Action } from '@ngrx/store';
import {ActionRecorderServices } from '../services/actionRecorder.service';

var obj=new ActionRecorderServices();

export function tripStatusList(state: any = null, action:Action) {

  switch (action.type) {
    case "TRIP_STATUS_LIST" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}

export function vehicleTripDetails(state: any = null, action:Action) {

  switch (action.type) {
    case "VEHICLE_TRIP_DETAILS" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}

export function addFretTripDetails(state: any = null, action:Action) {

  switch (action.type) {
    case "ADD_FREIGHT_TRIP_DETAILS" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}

export function createTripDetails(state: any = null, action:Action) {

  switch (action.type) {
    case "CREATE_TRIP_DETAILS" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}
export function createQuickTripDetails(state: any = null, action:Action) {
    switch (action.type) {
      case "CREATE_QUICK_TRIP_DETAILS" : {
        obj.addAction(action);
        return action.payload;
      }

      default:
        return state;
    }

  }


export function searchLocationName(state: any = null, action:Action) {

  switch (action.type) {
    case "SEARCH_LOCATION_NAME" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }

}


export function updateTripData(state: any = null, action:Action) {
  switch (action.type) {
    case "UPDATE_TRIP_DATA" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }
}


export function refreshTripTemplate(state: any = null, action:Action) {
  switch (action.type) {
    case "REFERSH_TRIP_TEMPALTE" : {
      obj.addAction(action);
      return action.payload;
    }

    default:
      return state;
  }
}



